import { AggregateArchive, AggregateId, hermesFilter, HermesObservable, HermesSingle, singleFromObservable } from '@generic-ui/hermes';


export class StructureInitialValuesReadyArchive extends AggregateArchive<boolean> {

	static readonly default = false;

	constructor() {
		super(StructureInitialValuesReadyArchive.default);
	}

	on(aggregateId: AggregateId): HermesObservable<boolean> {
		return super.on(aggregateId)
					.pipe(
						hermesFilter(v => v)
					);
	}

	once(aggregateId: AggregateId): HermesSingle<boolean> {
		return singleFromObservable(this.on(aggregateId));
	}

}
