import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { SetScrollPositionCommand } from './set-scroll-position.command';
import { ScrollPositionSetEvent } from './scroll-position-set.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SetScrollPositionCommandHandler implements CommandHandler<StructureAggregate, SetScrollPositionCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetScrollPositionCommand> {
		return SetScrollPositionCommand;
	}

	handle(structure: StructureAggregate, command: SetScrollPositionCommand): void {

		const position = command.getPosition();

		structure.setScrollPosition(position);
	}

	publish(aggregate: StructureAggregate, command: SetScrollPositionCommand): void {
		this.domainEventPublisher.publish(new ScrollPositionSetEvent(command.getAggregateId()));
	}

}
