import { RandomStringGenerator } from '@generic-ui/hermes';

import { Sorter } from './sorter';
import { SortingConfig } from '../api/config/sorting-config';
import { SorterId } from '../api/id/sorter.id';
import { FieldId } from '../../../field/src/domain/field/field.id';
import { ItemEntity } from '../../../source/src/domain/item/item.entity';
import { SortOrder } from '../../../../composition/src/domain/column/sort/sort-order';
import { FieldEntity } from '../../../field/src/domain/field/field.entity';


export class SorterManager {

	private enabled: boolean;

	private multi: boolean;

	private readonly sorters = new Map<string, Sorter>();

	constructor(enabled: boolean = false,
				multi: boolean = false) {
		this.enabled = enabled;
		this.multi = multi;
	}

	setConfig(config: SortingConfig): void {

		if (config && config.enabled !== undefined && config.enabled !== null) {
			this.enabled = config.enabled;
		}

		if (config && config.multiSorting !== undefined && config.multiSorting !== null) {
			this.multi = config.multiSorting;
			this.sorters.clear();
		}

	}

	toggle(field: FieldEntity): void {

		const fieldId = field.getId(),
			sorter = this.sorters.get(fieldId.getId());

		if (sorter) {
			if (sorter.hasDirection()) {
				sorter.changeDirection();
			} else {
				this.delete(fieldId);
			}
		} else {
			this.add(field);
		}

	}

	setSortOrder(field: FieldEntity, sortOrder: SortOrder): void {

		const fieldId = field.getId(),
			sorter = this.sorters.get(fieldId.getId());

		if (sortOrder === SortOrder.NONE) {
			this.delete(fieldId);
		} else if (sortOrder === SortOrder.ASC || sortOrder === SortOrder.DESC) {

			this.delete(fieldId);

			const direction = sortOrder === SortOrder.ASC;

			this.add(field, direction);
		}
	}

	add(field: FieldEntity, direction = true): void {
		this.addSorter(field.getId(), new Sorter(new SorterId(RandomStringGenerator.generate()), field, direction));
	}

	addSorter(fieldId: FieldId, sorter: Sorter): void {

		if (!this.multi) {
			this.sorters.clear();
		}

		this.sorters.set(fieldId.getId(), sorter);
	}

	delete(fieldId: FieldId): void {
		this.sorters.delete(fieldId.getId());
	}

	update(): void {
	}

	// TODO Remove the loop from sorter.sort
	sort(entities: Array<ItemEntity>): Array<ItemEntity> {
		const sorters = this.getAll();

		let sortedEntities = Array.from(entities);

		for (const sorter of sorters) {
			sortedEntities = sorter.sort(sortedEntities);
		}

		return sortedEntities;
	}

	getAll(): Array<Sorter> {

		if (!this.enabled) {
			return [];
		}

		return Array.from(this.sorters)
					.map((arr) => arr[1])
					.sort((sorterOne: Sorter, sorterTwo: Sorter) => {
						return sorterOne.getRank() - sorterTwo.getRank();
					})
					.reverse();
	}
}
