import { StructureId } from '../../../../../structure-core/src/api/global/structure.id';
import { FieldId } from '../../../../../field/src/domain/field/field.id';
import { UniqueValueId } from '../../../domain/unique/unique-value-id';
import { StructureCommand } from '../../../../../structure-core/src/core/structure.command';


export class SelectUniqueFilterCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly uniqueValueId: UniqueValueId) {
		super(structureId, 'SelectUniqueFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getUniqueValueId(): UniqueValueId {
		return this.uniqueValueId;
	}

}
