import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { RowSelectToggleType } from '../../domain/row-select-toggle-type';
import { StructureCommand } from '../../../../structure-core/src/core/structure.command';

export class ToggleSelectedRowCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly selectedRow: string,
				private readonly type: RowSelectToggleType) {
		super(structureId, 'ToggleSelectedRowCommand');
	}

	getSelectedRow(): string {
		return this.selectedRow;
	}

	getType(): RowSelectToggleType {
		return this.type;
	}

}
