import { ElementRef, Injectable, Injector } from '@angular/core';

import { FabricInlineDialogService, FabricPlacement } from '@generic-ui/fabric';

import { Reactive } from '@generic-ui/hermes';
import { SelectCustomModalComponent } from './select-custom-modal.component';
import { SchemaTheme } from '../../../../../core/schema/src/api/theme/schema-theme';
import { SchemaReadModelRootId } from '../../../../../core/schema/src/api/global/schema.read-model-root-id';
import { SchemaWarehouse } from '../../../../../core/schema/src/api/schema.warehouse';
import { StructureThemeConverter } from '../../../../../feature-view/grid-view/src/schema/structure.theme.converter';

@Injectable()
export class StructureSelectCustomService extends Reactive {

	constructor(private readonly injector: Injector,
				private readonly schemaReadModelRootId: SchemaReadModelRootId,
				private readonly schemaWarehouse: SchemaWarehouse,
				private readonly structureThemeConverter: StructureThemeConverter,
				private readonly inlineDialogService: FabricInlineDialogService) {
		super();
	}

	open(elementRef: ElementRef): void {
		this.close();

		this.schemaWarehouse
			.onceTheme(this.schemaReadModelRootId)
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((theme: SchemaTheme) => {
				this.inlineDialogService.open(elementRef, SelectCustomModalComponent, {
					injector: this.injector,
					placement: FabricPlacement.BOTTOM,
					offset: 0,
					theme: this.structureThemeConverter.convertTheme(theme),
					customClass: 'gui-inline-dialog-header-menu'
				});
			});
	}

	close(): void {
		this.inlineDialogService.close();
	}
}
