import { AggregateDefinition, AggregateId, AggregateRoot, Command, Container, DomainEvent, DomainProviders } from '@generic-ui/hermes';

import { PagingManagerFactory } from '../domain/paging.manager-factory';
import { PagingDispatcher } from './paging.dispatcher';
import { SetPagingCommandHandler } from './set/set-paging.command-handler';
import { NextPageCommandHandler } from './next-page/next-page.command-handler';
import { PrevPageCommandHandler } from './prev-page/prev-page.command-handler';
import { ChangePagesizeCommandHandler } from './change-pagesize/change-pagesize.command-handler';
import { structureKey } from '../../../structure-core/src/api/global/structre.key';


export class PagingDomainModule implements DomainProviders {

	defineAggregate(): AggregateDefinition<any, any, any> | null {
		return null;
	}

	registerKey(): string {
		return structureKey;
	}

	registerProviders(container: Container): void {
		container.provide(PagingDispatcher);
		container.provide(PagingManagerFactory);
	}

	registerCommandHandlers<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(): Array<any> {
		return [
			SetPagingCommandHandler,
			NextPageCommandHandler,
			PrevPageCommandHandler,
			ChangePagesizeCommandHandler
		];
	}

	registerDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [];
	}

	registerMultiDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [];
	}
}
