import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../common/src/cdk/component/lib/src/gate';
import { NgChanges } from '../../../../common/src/cdk/component/lib/src/ng-changes';
import { StructurePublisher } from '../../../../../core/structure/structure-core/src/api/structure.publisher';
import { QuickFiltersConfig } from '../../../../../core/structure/filter/src/api/quick-filter/quick-filters.config';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { ifChanged } from '../../../../common/src/cdk/component/lib/src/check.input';

@Directive({
	selector: 'gui-structure[quickFilters]'
})
export class StructureQuickFiltersGate extends Gate implements OnChanges {

	@Input()
	quickFilters: boolean | QuickFiltersConfig;

	constructor(private readonly structureId: StructureId,
				private readonly structureCommandInvoker: StructurePublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureQuickFiltersGate>) {

		ifChanged(changes.quickFilters, () => {

			let quickFilters: QuickFiltersConfig;

			if (typeof this.quickFilters === 'boolean') {
				quickFilters = {
					enabled: this.quickFilters
				};
			} else {
				quickFilters = this.quickFilters;
			}

			this.structureCommandInvoker.setQuickFiltersConfig(quickFilters, this.structureId);
		});

	}

}
