import { ItemEntityId } from '../../../../source/src/domain/item/item.entity-id';
import { ItemEntity } from '../../../../source/src/domain/item/item.entity';

export class SelectedRow {

	private readonly index: number;

	private readonly itemId: ItemEntityId;

	private readonly item: ItemEntity;

	constructor(item: ItemEntity,
				index: number,
				itemId: ItemEntityId) {
		this.item = item;
		this.index = index;
		this.itemId = itemId;
	}

	getItem(): ItemEntity {
		return this.item;
	}

	getIndex(): number {
		return this.index;
	}

	getItemId(): ItemEntityId {
		return this.itemId;
	}

}
