import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../../feature/common/src/cdk/component/lib/src/smart-component';

import { CellTemplateWithContext } from '../../../../../core/composition/src/core-read/definition/cell-template-with-context';
import { ChangedValueEmitter } from '../../../../../feature/composition/src/column/edit/changed-value.emitter';
import { FilterPublisher } from '../../../../../core/structure/filter/src/api/filter.publisher';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';


@Component({
	selector: 'gui-structure-header-filter',
	template: `

		<!--		<ng-container-->
		<!--				*ngTemplateOutlet="column.editTemplate; context: editContext">-->
		<!--		</ng-container>-->

		<button gui-button [primary]="true">Add Filter</button>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureHeaderFilterComponent extends SmartComponent implements OnInit {

	@Input()
	column: CellTemplateWithContext;

	sub = new ChangedValueEmitter();

	editContext: any;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureFilterCommandService: FilterPublisher,
				private readonly structureId: StructureId) {
		super(changeDetectorRef, elementRef);

		this.editContext = {
			changes: this.sub
		};
	}

	ngOnInit() {
		this.sub
			.on()
			.subscribe((phrase: string) => this.filter(phrase));
	}

	filter(phrase: string): void {
		// this.structureFilterCommandService.addFilter(this.column.getFieldId(), '1', phrase, this.structureId);
	}

	protected getSelectorName(): string {
		return 'gui-structure-header-filter';
	}

}
