import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { StructureSourceOriginArchive } from './structure.source-origin.archive';
import { OriginSetEvent } from './set/origin-set.event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class StructureOriginChangedEventHandler implements DomainEventHandler<StructureId, OriginSetEvent> {

	constructor(private readonly structureSourceOriginRepository: StructureSourceOriginArchive) {
	}

	static readonly services = [StructureSourceOriginArchive];

	forEvent(): DomainEventType<OriginSetEvent> {
		return OriginSetEvent;
	}

	handle(event: OriginSetEvent): void {

		if (event.ofMessageType('OriginSetEvent')) {
			const origin = event.getOrigin();

			this.structureSourceOriginRepository.next(event.getAggregateId(), origin);
		}
	}
}
