import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../common/src/cdk/component/lib/src/gate';
import { NgChanges } from '../../../../common/src/cdk/component/lib/src/ng-changes';
import { FilterConfig } from '../../../../../core/structure/filter/src/api/config/filter.config';
import { StructurePublisher } from '../../../../../core/structure/structure-core/src/api/structure.publisher';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { ifChanged } from '../../../../common/src/cdk/component/lib/src/check.input';

@Directive({
	selector: 'gui-structure[filtering]'
})
export class StructureFilterGate extends Gate implements OnChanges {

	@Input()
	filtering: boolean | FilterConfig;

	constructor(private readonly structureId: StructureId,
				private readonly structureCommandInvoker: StructurePublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureFilterGate>) {

		ifChanged(changes.filtering, () => {

			let filtering: FilterConfig;

			if (typeof this.filtering === 'boolean') {
				filtering = {
					enabled: this.filtering
				};
			} else {
				filtering = this.filtering;
			}

			this.structureCommandInvoker.setFilterConfig(filtering, this.structureId);
		});
	}

}
