import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FormationPublisher } from '../../../../../core/structure/formation/src/api/formation.publisher';
import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { FormationWarehouse } from '../../../../../core/structure/formation/src/api/formation.warehouse';
import { useReactiveContext } from '../../../../common/src/cdk/component/lib';
import { StructureSelectCustomService } from './structure.select-custom.service';

@Component({
	selector: 'gui-select-custom',
	templateUrl: './select-custom.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	providers: [
		StructureSelectCustomService
	]
})
export class SelectCustomComponent extends SmartComponent implements OnInit {

	// @ViewChild('selectCustomContainer', { static: true })
	headerDialogContainer: ElementRef;

	options = ['a', 'b'];

	context = useReactiveContext();

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureSelectCustomService: StructureSelectCustomService,
				private readonly structureId: StructureId,
				private readonly formationCommandDispatcher: FormationPublisher,
				private readonly formationWarehouse: FormationWarehouse) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

	}

	openConfigDialog(): void {
		this.structureSelectCustomService.open(this.headerDialogContainer);
	}

	protected getSelectorName(): string {
		return 'gui-select-custom';
	}

}
