import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { FieldArchive } from '../core-read/field.archive';
import { FieldConverter } from '../core-read/init/field.converter';
import { FieldDomainModule } from '../core/field.domain-module';
import { FieldPublisher } from './field.publisher';
import { FieldWarehouse } from './field.warehouse';
import { FieldDomainWarehouse } from '../core-read/field.domain-warehouse';
import { FieldDomainPublisher } from '../core/field.domain-publisher';


export class FieldApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(FieldPublisher, FieldDomainPublisher);
		container.provide(FieldArchive);
		container.provide(FieldConverter);
		container.provide(FieldWarehouse, FieldDomainWarehouse);
	}
}

export function initializeField(): void {
	new DomainInitializer(new FieldApiModule(), new FieldDomainModule()).init();
}
