import { AggregateFactory } from '@generic-ui/hermes';

import { ListViewId } from './list-view.id';
import { ListViewAggregate } from '../domain/list-view.aggregate';


export class ListViewAggregateFactory extends AggregateFactory<ListViewId, ListViewAggregate> {

	constructor() {
		super();
	}

	create(id: ListViewId): ListViewAggregate {
		return new ListViewAggregate(id);
	}
}
