import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { ActiveFiltersSetEvent } from '../active-filters-set.event';
import { ActiveFilterModel } from '../../api/active/active-filter.model';
import { StructureAggregateEvent } from '../../../../structure-core/src/core/structure.aggregate-event';


export class FilterRemovedAggregateEvent extends StructureAggregateEvent {

	constructor(aggregateId: StructureId,
				private readonly activeFilters: ReadonlyArray<ActiveFilterModel>) {
		super(aggregateId, 'FilterRemovedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new ActiveFiltersSetEvent(
			this.getAggregateId(),
			this.activeFilters
		);
	}

}
