import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { SetSortingCommand } from './set-sorting.command';
import { SortingSetEvent } from './sorting-set.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SetSortingCommandHandler implements CommandHandler<StructureAggregate, SetSortingCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetSortingCommand> {
		return SetSortingCommand;
	}

	handle(aggregate: StructureAggregate, command: SetSortingCommand): void {

		const sortingConfig = command.getSortingConfig();

		aggregate.setSortingConfig(sortingConfig);
	}

	publish(aggregate: StructureAggregate, command: SetSortingCommand): void {
		this.domainEventPublisher.publish(new SortingSetEvent(command.getAggregateId()));
	}

}
