import { CompositionId } from '../../../../core/composition/src/api/global/composition.id';
import { hermesMap, HermesObservable } from '@generic-ui/hermes';
import { CompositionTemplateModel } from './composition.template.model';
import { CompositionWarehouse } from '../../../../core/composition';
import { CellTemplateWithContext } from '../../../../core/composition/src/core-read/definition/cell-template-with-context';
import { ViewTemplateRepository } from './view/view-template.repository';
import { EditTemplateRepository } from './edit/edit-template.repository';
import { CellView } from '../../../../core/composition/src/domain/column/cell-view';
import { DataType } from '../../../../core/structure/field/src/domain/field/data/data-type';
import { Injectable, TemplateRef } from '@angular/core';
import { CellTemplateWithAccessor } from '../../../../core/composition/src/core-read/definition/cell-template-with-accessor';
import { CompositionAccessorModel } from './composition.accessor.model';

@Injectable()
export class CompositionTemplateWarehouse {

	constructor(private readonly compositionWarehouse: CompositionWarehouse,
				private readonly viewTemplateRepository: ViewTemplateRepository,
				private readonly editTemplateRepository: EditTemplateRepository) {
	}

	onHeaderCols(compositionId: CompositionId): HermesObservable<ReadonlyArray<CompositionTemplateModel>> {
		return this.compositionWarehouse
				   .onHeaderColumns(compositionId)
				   .pipe(
					   hermesMap((arr: Array<CellTemplateWithContext>) => {
						   return arr.map((context) => {

							   let headerTemplate;

							   if (typeof context.getHeader() === 'function') {
								   headerTemplate = this.findViewTemplate(CellView.FUNCTION);
							   } else {
								   headerTemplate = this.findViewTemplate(CellView.HTML);
							   }

							   const editTemplate = this.findEditTemplate(context.getDataType());

							   return new CompositionTemplateModel(headerTemplate, editTemplate, context);
						   });
					   })
				   );
	}

	onAll(compositionId: CompositionId): HermesObservable<ReadonlyArray<CompositionTemplateModel>> {
		return this.compositionWarehouse
				   .onAllColumns(compositionId)
				   .pipe(
					   hermesMap((arr: Array<CellTemplateWithContext>) => {
						   return arr.map((context) => {

							   let headerTemplate;

							   if (typeof context.getHeader() === 'function') {
								   headerTemplate = this.findViewTemplate(CellView.FUNCTION);
							   } else {
								   headerTemplate = this.findViewTemplate(CellView.HTML);
							   }

							   const editTemplate = this.findEditTemplate(context.getDataType());

							   return new CompositionTemplateModel(headerTemplate, editTemplate, context);
						   });
					   })
				   );
	}

	// CellTemplateWithAccessor
	onTemplateCols(compositionId: CompositionId): HermesObservable<ReadonlyArray<CompositionAccessorModel>> {
		return this.compositionWarehouse
				   .onTemplateColumns(compositionId)
				   .pipe(
					   hermesMap((arr: Array<CellTemplateWithAccessor>) => {
						   return arr.map((context) => {

							   let cellTemplate;

							   if (context.getCellView() === CellView.NG_TEMPLATE) {
								   cellTemplate = context.getColumnConfig().templateRef;
							   } else {
								   cellTemplate = this.findViewTemplate(context.getCellView());
							   }

							   const editTemplate = this.findEditTemplate(context.getDataType());

							   return new CompositionAccessorModel(cellTemplate, editTemplate, context);
						   });
					   })
				   );
	}

	private findViewTemplate(view: CellView): TemplateRef<any> {

		if (view === CellView.FUNCTION) {
			return this.viewTemplateRepository.findTemplate(CellView.FUNCTION);
		} else {
			return this.viewTemplateRepository.findTemplate(view);
		}
	}

	private findEditTemplate(dataType: DataType): TemplateRef<any> {
		return this.editTemplateRepository.findTemplate(dataType);
	}

}
