import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { SetCompositionWidthCommand } from './set-composition-width.command';
import { CompositionWidthSetEvent } from './composition-width-set.event';
import { CompositionAggregate } from '../../../domain/composition.aggregate';


export class SetCompositionWidthCommandHandler implements CommandHandler<CompositionAggregate, SetCompositionWidthCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetCompositionWidthCommand> {
		return SetCompositionWidthCommand;
	}

	handle(composition: CompositionAggregate, command: SetCompositionWidthCommand): void {

		const width = command.getWidth();

		composition.setWidth(width);
	}

	publish(aggregate: CompositionAggregate, command: SetCompositionWidthCommand): void {
		this.domainEventPublisher.publish(new CompositionWidthSetEvent(command.getAggregateId()));
	}

}
