import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime } from 'rxjs/operators';

import { SmartComponent } from '../../../common/src/cdk/component/lib/src/smart-component';
import { StructureId } from '../../../../core/structure/structure-core/src/api/global/structure.id';
import { SearchPublisher } from '../../../../core/structure/search/src/api/search.publisher';
import { SearchWarehouse } from '../../../../core/structure/search/src/api/search.warehouse';
import { fromRxJsObservable, HermesSubscription } from '@generic-ui/hermes';
import { Observable } from 'rxjs';


@Component({
	selector: 'div[gui-search-bar]',
	templateUrl: './search.component.html',
	styleUrls: [
		'./style/searching.component.scss'
	],
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class SearchComponent extends SmartComponent implements OnInit {

	private static readonly FORM_SEARCH_NAME = 'searchPhrase';

	@ViewChild('formRef', { read: ElementRef, static: false })
	readonly formRef?: ElementRef;

	searchForm: FormGroup;

	placeholder: string;

	searchingEnabled: boolean;

	searchInputSubscription: HermesSubscription;

	constructor(private readonly formBuilder: FormBuilder,
				private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly searchCommandDispatcher: SearchPublisher,
				private readonly searchWarehouse: SearchWarehouse) {
		super(changeDetectorRef, elementRef);

		const controlsConfig = {};
		controlsConfig[SearchComponent.FORM_SEARCH_NAME] = '';

		this.searchForm = this.formBuilder.group(controlsConfig);
	}

	ngOnInit() {
		this.observeChanges();

		this.subscribe(
			this.searchWarehouse.onPlaceholder(this.structureId),
			(placeholder: string) => {
				this.placeholder = placeholder;
			}
		);

		this.subscribe(
			this.searchWarehouse.onSearchEnabled(this.structureId),
			(searchingEnabled: boolean) => {
				this.searchingEnabled = searchingEnabled;
			}
		);

		this.subscribeWithoutRender(
			this.searchWarehouse.onPhrase(this.structureId),
			(phrase: string) => {

				if (phrase === undefined) {
					phrase = null;
				}

				const controlsConfig = {};
				controlsConfig[SearchComponent.FORM_SEARCH_NAME] = phrase;

				if (phrase !== this.searchForm.get([SearchComponent.FORM_SEARCH_NAME]).value) {

					this.stopObserveChanges();
					this.searchForm.setValue(controlsConfig);
					this.observeChanges();
				}
			}
		);
	}

	clear(): void {
		this.searchForm.reset();
	}

	protected getSelectorName(): string {
		return 'gui-search-bar';
	}

	private observeChanges(): void {

		// @ts-ignore
		const searchPhrase$ = this.searchForm
			.controls[SearchComponent.FORM_SEARCH_NAME]
			.valueChanges as Observable<string>;

		this.searchInputSubscription =
			fromRxJsObservable(
				searchPhrase$
					.pipe(
						debounceTime(200)
					)
			)
				.pipe(
					this.takeUntil()
				)
				.subscribe((phrase: string) => {
					this.searchCommandDispatcher.search(phrase, this.structureId);
				});
	}

	private stopObserveChanges(): void {
		this.searchInputSubscription.unsubscribe();
	}
}
