import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';
import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { FieldWarehouse } from '../../../../../core/structure/field/src/api/field.warehouse';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FieldModel } from '../../../../../core/structure/field/src/api/model/field.model';
import { FilterWarehouse } from '../../../../../core/structure/filter/src/api/filter.warehouse';
import { FilterTypeCollectionModel } from '../../../../../core/structure/filter/src/api/type/filter-type-collection.model';
import { FilterTypeModel } from '../../../../../core/structure/filter/src/api/type/filter-type.model';
import { FilterTypeId } from '../../../../../core/structure/filter/src/domain/type/filter-type.id';
import { FilterPublisher } from '../../../../../core/structure/filter/src/api/filter.publisher';
import { FieldId } from '../../../../../core/structure/field/src/domain/field/field.id';
import { ActiveFilterModel } from '../../../../../core/structure/filter/src/api/active/active-filter.model';
import { CompositionId } from '../../../../../core/composition/src/api/global/composition.id';
import { FilterId } from '../../../../../core/structure/filter/src/api/id/filter.id';
import { CompositionTemplateModel } from '../../../../composition/src/column/composition.template.model';
import { CompositionTemplateWarehouse } from '../../../../composition/src/column/composition.template-warehouse';


@Component({
	selector: 'div[gui-filter-menu]',
	templateUrl: './filter-menu.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FilterMenuComponent extends SmartComponent implements OnInit {

	selectedColumn: CompositionTemplateModel;

	columns: Array<CompositionTemplateModel> = [];

	selectedField: FieldModel;

	fields: Array<FieldModel> = [];

	filterTypeMap: FilterTypeCollectionModel;

	filterTypes: Array<FilterTypeModel>;

	selectedFilterTypeId: FilterTypeId;

	selectedValue: string;

	activeFilters: Array<ActiveFilterModel> = [];

	constructor(changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly fieldWarehouse: FieldWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly filterCommandInvoker: FilterPublisher,
				private readonly compositionTemplateWarehouse: CompositionTemplateWarehouse) {
		super(changeDetectorRef, elementRef);
		this.addClassToHost('gui-block');
	}

	ngOnInit() {

		this.subscribe(
			this.filterWarehouse.onActiveFilters(this.structureId),
			(activeFilters: Array<ActiveFilterModel>) => {
				this.activeFilters = activeFilters;
			}
		);

		this.subscribe(
			this.filterWarehouse.onFilterTypes(this.structureId),
			(filterTypeMap: FilterTypeCollectionModel) => {
				this.filterTypeMap = filterTypeMap;
			}
		);

		this.subscribe(
			this.fieldWarehouse.onFields(this.structureId),
			(fieldReadModels: Array<FieldModel>) => {
				this.fields = fieldReadModels;
			}
		);

		this.subscribe(
			this.compositionTemplateWarehouse.onHeaderCols(this.compositionId),
			(columns: Array<CompositionTemplateModel>) => {
				this.columns = columns;
			}
		);
	}

	onFieldSelect(field: FieldModel): void {
		this.selectedField = field;

		this.filterTypes = this.filterTypeMap.getFilterTypes(this.selectedField.getFieldId());

		this.reRender();
	}

	onFilterTypeSelect(filterTypeId: FilterTypeId): void {

		this.selectedFilterTypeId = filterTypeId;

		this.reRender();
	}

	removeAllFilters(): void {
		this.filterCommandInvoker.removeAll(this.structureId);
	}

	onValueChanged(value: string): void {
		this.selectedValue = value;
	}

	addFilter(): void {

		const fieldId: FieldId = this.selectedField.getFieldId(),
			filterTypeId: FilterTypeId = this.selectedFilterTypeId,
			value: any = this.selectedValue;

		this.filterCommandInvoker.add(fieldId, filterTypeId, value, this.structureId);

		this.clearAddFilterForm();
	}

	onRemovedFilter(filterId: FilterId): void {
		event.preventDefault();
		this.filterCommandInvoker.remove(filterId, this.structureId);
	}

	removeFilter(): void {
	}

	clearAddFilterForm(): void {
		this.selectedColumn = null;
		this.selectedField = null;
		this.selectedFilterTypeId = null;
		this.selectedValue = null;
		this.reRender();
	}

	protected getSelectorName(): string {
		return 'gui-filter-menu';
	}
}
