import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { FieldArchive } from '../field.archive';
import { FieldConverter } from './field.converter';
import { FieldsInitedEvent } from '../../core/init/fields-inited.event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class FieldsInitedEventHandler implements DomainEventHandler<StructureId, FieldsInitedEvent> {

	constructor(private readonly fieldArchive: FieldArchive,
				private readonly fieldConverter: FieldConverter) {
	}

	static readonly services = [
		FieldArchive,
		FieldConverter
	] as const;

	forEvent(): DomainEventType<FieldsInitedEvent> {
		return FieldsInitedEvent;
	}

	handle(event: FieldsInitedEvent): void {

		if (event.ofMessageType('FieldsInitedEvent')) {

			const fields = this.fieldConverter.convert(event.getFields());

			this.fieldArchive.next(event.getAggregateId(), fields);
		}

	}

}
