import { Directive, Input, OnChanges } from '@angular/core';

import { Gate } from '../../../../common/src/cdk/component/lib/src/gate';
import { SummariesConfig } from '../../../../../core/structure/summaries/src/api/config/summaries-config';
import { NgChanges } from '../../../../common/src/cdk/component/lib/src/ng-changes';
import { SummariesPublisher } from '../../../../../core/structure/summaries/src/api/summaries.publisher';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { ifChanged } from '../../../../common/src/cdk/component/lib/src/check.input';


@Directive({
	selector: 'gui-structure[summaries]'
})
export class StructureSummariesGate extends Gate implements OnChanges {

	@Input()
	summaries: SummariesConfig;

	constructor(private readonly structureId: StructureId,
				private readonly summariesCommandInvoker: SummariesPublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureSummariesGate>) {

		ifChanged(changes.summaries, () => {
			this.summariesCommandInvoker.setConfig(this.summaries, this.structureId);
		});
	}

}
