import { Logger } from '../../../../../feature/common/src/cdk/logger/logger';

import { VerticalFormation } from './vertical-formation';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';


export class VerticalFormationFactory {

	constructor(private readonly logger: Logger) {
	}

	static readonly services = [Logger];

	create(structureId: StructureId,
		   enabled: boolean = false,
		   viewportHeight: number = -1,
		   sourceSize: number = 0,
		   scrollPosition: number = 0,
		   rowHeight?: number,
		   hiddenItemsTop?: number,
		   hiddenItemsBottom?: number): VerticalFormation {

		const vf = new VerticalFormation(
			structureId,
			enabled,
			viewportHeight,
			sourceSize,
			scrollPosition,
			rowHeight,
			hiddenItemsTop,
			hiddenItemsBottom
		);

		vf.setLogger(this.logger);

		return vf;
	}

}
