import { SearchDispatcher } from './search.dispatcher';
import { SearchConfig } from '../api/config/search-config';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SearchPublisher } from '../api/search.publisher';


export class SearchDomainPublisher extends SearchPublisher {

	constructor(private readonly searchDispatcher: SearchDispatcher) {
		super();
	}

	static readonly services = [SearchDispatcher];

	setSearchingConfig(config: SearchConfig, structureId: StructureId): void {
		this.searchDispatcher.setSearchingConfig(config, structureId);
	}

	search(phrase: string, structureId: StructureId): void {
		this.searchDispatcher.search(phrase, structureId);
	}
}
