import { FormationCustomSelectId } from './formation.custom-select.id';
import { ItemEntity } from '../../../../source/src/domain/item/item.entity';

export class FormationCustomSelectionFunctionModel {

	constructor(private readonly key: string,
				private readonly text: string,
				private readonly customSelectId: FormationCustomSelectId,
				private readonly builtIn: boolean,
				private readonly method?: (items: Array<ItemEntity>) => Array<ItemEntity>
	) {
	}

	getKey(): string {
		return this.key;
	}

	getText(): string {
		return this.text;
	}

	getCustomSelectId(): FormationCustomSelectId {
		return this.customSelectId;
	}

	isBuiltIn(): boolean {
		return this.builtIn;
	}

	customSelect(items: Array<ItemEntity>): Array<ItemEntity> {
		return this.method(items);
	}

}

export class FormationCustomSelection {

	constructor(private readonly enabled: boolean,
				private readonly selections: Array<FormationCustomSelectionFunctionModel>) {
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	getSelections(): Array<any> {
		return this.selections;
	}

}
