import { AggregateDefinition, AggregateId, AggregateRoot, Command, Container, DomainEvent, DomainProviders } from '@generic-ui/hermes';
import { structureKey } from '../api/global/structre.key';
import { CreateStructureCommandHandler } from './create/create-structure.command-handler';
import { StructureAggregateFactory } from './structure.aggregate-factory';
import { InMemoryStructureAggregateRepository } from '../persist/domain/in-memory.structure-aggregate.repository';
import { StructureCreatedEventHandler } from './create/structure-created.event-handler';


export class StructureDomainModule implements DomainProviders {

	defineAggregate(): AggregateDefinition<any, any, any> | null {
		return {
			aggregateKey: structureKey,
			createCommandHandler: CreateStructureCommandHandler,
			factory: StructureAggregateFactory,
			repository: InMemoryStructureAggregateRepository
		};
	}

	registerKey(): string {
		return structureKey;
	}

	registerProviders(container: Container): void {
		// container.provide(ListViewDispatcher);
	}

	registerCommandHandlers<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(): Array<any> {
		return [
			// SetListViewModeCommandHandler,
			// ToggleListViewSelectorCommandHandler
		];
	}

	registerDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [
			StructureCreatedEventHandler
		];
	}

	registerMultiDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [];
	}

}

