import { CompositionWarehouse } from '../../../../composition/src/api/composition.warehouse';
import { CompositionId } from '../../../../composition/src/api/global/composition.id';
import { CellTemplateWithAccessor } from '../../../../composition/src/core-read/definition/cell-template-with-accessor';
import { hermesFilter, hermesMap, hermesSwitchMap, hermesTake, Optional } from '@generic-ui/hermes';
import { FilterWarehouse } from '../api/filter.warehouse';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { FieldId } from '../../../field/src/domain/field/field.id';
import { FilterTypeModel } from '../api/type/filter-type.model';
import { FilterPublisher } from '../api/filter.publisher';
import { FilterTypeId } from '../domain/type/filter-type.id';
import { ActiveFilterModel } from '../api/active/active-filter.model';
import { FilterForColumn } from './filter-integration.api';


export class FilterIntegration {

	constructor(private readonly compositionWarehouse: CompositionWarehouse,
				private readonly filterCommandInvoker: FilterPublisher,
				private readonly filterWarehouse: FilterWarehouse) {
	}

	static readonly services = [CompositionWarehouse, FilterPublisher, FilterWarehouse];

	findFilterTypes(columnName: string, compositionId: CompositionId, structureId: StructureId): Array<string> {

		let filterTypes: Array<string> = [];

		this.compositionWarehouse
			.onTemplateColumns(compositionId)
			.pipe(
				hermesMap((cols: ReadonlyArray<CellTemplateWithAccessor>) => {
					return cols.find((col: CellTemplateWithAccessor) => {
						return col.getName() === columnName;
					});
				}),
				hermesFilter((col: CellTemplateWithAccessor) => {
					return col !== undefined;
				}),
				hermesTake(1),
				hermesSwitchMap((col: CellTemplateWithAccessor) => {
					return this.filterWarehouse
							   .onFilterTypesForFieldId(new FieldId(col.columnFieldId.getId()), structureId);
				})
			)
			.subscribe((types: Array<FilterTypeModel>) => {
				filterTypes = types.map((type) => type.getName());
			});

		return filterTypes;
	}

	findFilters(compositionId: CompositionId, structureId: StructureId): { [key: string]: ReadonlyArray<FilterForColumn> } {

		const filters = this.filterWarehouse.findFilters(structureId).getValueOrNullOrThrowError();

		const columnNames = this.compositionWarehouse.findColumnNames(compositionId);

		const obj = {};

		for (let i = 0; i < columnNames.length; i += 1) {
			obj[columnNames[i]] = filters.filter((filter: ActiveFilterModel) => {
											 return filter.getFieldName() === columnNames[i];
										 })
										 .map((filter: ActiveFilterModel) => {
											 return {
												 columnName: filter.getFieldName(),
												 filterId: filter.getFilterId().toString(),
												 type: filter.getFilterTypeName(),
												 value: filter.getValue()
											 };
										 });
		}

		return obj;
	}

	filter(
		columnName: string,
		filterType: string,
		value: any,
		compositionId: CompositionId,
		structureId: StructureId
	): void {

		this.compositionWarehouse
			.onTemplateColumns(compositionId)
			.pipe(
				hermesMap((cols: ReadonlyArray<CellTemplateWithAccessor>) => {
					return cols.find((col: CellTemplateWithAccessor) => {
						return col.getName() === columnName;
					});
				}),
				hermesFilter((col: CellTemplateWithAccessor) => {
					return col !== undefined;
				}),
				hermesTake(1),
				hermesSwitchMap((col: CellTemplateWithAccessor) => {

					return this.filterWarehouse
							   .onceFilterTypeId(new FieldId(col.columnFieldId.getId()), filterType, structureId)
							   .pipe(
								   hermesMap((filterTypeId: Optional<FilterTypeId>) => {
									   return {
										   fieldId: new FieldId(col.columnFieldId.getId()),
										   filterTypeId: filterTypeId
									   };
								   })
							   );
				})
			)
			.subscribe((params) => {

				const { fieldId, filterTypeId } = params;

				filterTypeId.ifPresent((ftId: FilterTypeId) => {
					this.filterCommandInvoker.add(
						fieldId,
						ftId,
						value,
						structureId
					);
				});
			});
	}

}
