import { AggregateDefinition, AggregateId, AggregateRoot, Command, Container, DomainEvent, DomainProviders } from '@generic-ui/hermes';

import { SetSchemaThemeCommandHandler } from './theme/set-schema-theme.command-handler';
import { SetRowColoringCommandHandler } from './row-coloring/set-row-coloring.command-handler';
import { SetSchemaHorizontalGridCommandHandler } from './grid/horizontal/set-schema-horizontal-grid.command-handler';
import { SetSchemaVerticalGridCommandHandler } from './grid/vertical/set-schema-vertical-grid.command-handler';
import { SchemaCssClassesEventHandler } from '../core-read/css-classes/schema-css-classes.event-handler';
import { schemaKey } from './schema.key';
import { SchemaVerticalGridRepository } from '../core-read/vertical-grid/schema.vertical-grid.repository';
import { SchemaThemeRepository } from '../core-read/theme/schema.theme.repository';
import { SchemaRowColoringRepository } from '../core-read/row-coloring/schema.row-coloring.repository';
import { SchemaHorizontalGridRepository } from '../core-read/horizontal-grid/schema.horizontal-grid.repository';
import { CreateSchemaCommandHandler } from './create/create-schema.command-handler';
import { SchemaAggregateFactory } from './create/schema.aggregate-factory';
import { InMemorySchemaAggregateRepository } from '../persist/in-memory.schema.aggregate-repository';
import { SchemaRowClassArchive } from './styling/schema.row-class.archive';
import { SchemaRowStyleArchive } from './styling/schema.row-style.archive';


export class SchemaDomainModule implements DomainProviders {

	defineAggregate(): AggregateDefinition<any, any, any> | null {
		return {
			aggregateKey: schemaKey,
			createCommandHandler: CreateSchemaCommandHandler,
			factory: SchemaAggregateFactory,
			repository: InMemorySchemaAggregateRepository
		};
	}

	registerKey(): string {
		return schemaKey;
	}

	registerProviders(container: Container): void {
		container.provide(SchemaRowClassArchive);
		container.provide(SchemaRowStyleArchive);
	}

	registerCommandHandlers<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(): Array<any> {
		return [
			SetSchemaThemeCommandHandler,
			SetRowColoringCommandHandler,
			SetSchemaHorizontalGridCommandHandler,
			SetSchemaVerticalGridCommandHandler
		];
	}

	registerDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [
			SchemaThemeRepository,
			SchemaHorizontalGridRepository,
			SchemaRowColoringRepository,
			SchemaVerticalGridRepository
		];
	}

	registerMultiDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [
			SchemaCssClassesEventHandler
		];
	}

}

