import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { FieldId } from '../../../../field/src/domain/field/field.id';
import { FilterTypeId } from '../../domain/type/filter-type.id';
import { StructureCommand } from '../../../../structure-core/src/core/structure.command';


export class AddFilterCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly filterTypeId: FilterTypeId,
				private readonly value: any) {
		super(structureId, 'AddFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getFilterTypeId(): FilterTypeId {
		return this.filterTypeId;
	}

	getValue(): any {
		return this.value;
	}

}
