import { Directive, Input, OnChanges, OnInit } from '@angular/core';
import { Gate } from '../../../../../feature/common/src/cdk/component/lib/src/gate';
import { SourcePublisher } from '../../../../../core/structure/source/src/api/source.publisher';
import { NgChanges } from '../../../../../feature/common/src/cdk/component/lib/src/ng-changes';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';


@Directive({
	selector: 'gui-structure[loading]'
})
export class SourceLoadingGate extends Gate implements OnChanges, OnInit {

	@Input()
	loading: boolean;

	constructor(private readonly structureId: StructureId,
				private readonly sourceCommandInvoker: SourcePublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<SourceLoadingGate>): void {


		if (this.isDefined('loading', changes)) {
			this.sourceCommandInvoker.setLoading(this.loading, this.structureId);
		}
	}

	ngOnInit() {
	}
}
