import { SearchingEnabledArchive } from '../core/enabled/searching-enabled.archive';
import { SearchPhraseArchive } from '../core/phrase/search-phrase.archive';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SearchWarehouse } from '../api/search.warehouse';
import { HermesObservable } from '@generic-ui/hermes';
import { SearchHighlightArchive } from '../core/highlight/search-highlight.archive';
import { SearchPlaceholderArchive } from '../core/placeholder/search-placeholder.archive';


export class SearchDomainWarehouse extends SearchWarehouse {

	constructor(private readonly searchingEnabledArchive: SearchingEnabledArchive,
				private readonly searchPhraseArchive: SearchPhraseArchive,
				private readonly searchHighlightArchive: SearchHighlightArchive,
				private readonly searchPlaceholderArchive: SearchPlaceholderArchive) {
		super();
	}

	static readonly services = [
		SearchingEnabledArchive,
		SearchPhraseArchive,
		SearchHighlightArchive,
		SearchPlaceholderArchive
	];

	onSearchEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.searchingEnabledArchive.on(structureId);
	}

	onPhrase(structureId: StructureId): HermesObservable<string> {
		return this.searchPhraseArchive.on(structureId);
	}

	onHighlight(structureId: StructureId): HermesObservable<boolean> {
		return this.searchHighlightArchive.on(structureId);
	}

	onPlaceholder(structureId: StructureId): HermesObservable<string> {
		return this.searchPlaceholderArchive.on(structureId);
	}

}
