import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { FilterToggledEvent } from '../../../../../filter/src/core/toggle/filter-toggled.event';
import { RemoveSearchPhraseCommand } from './remove-search-phrase.command';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';


export class RemoveSearchPhraseCommandHandler implements CommandHandler<StructureAggregate, RemoveSearchPhraseCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<RemoveSearchPhraseCommand> {
		return RemoveSearchPhraseCommand;
	}

	handle(aggregate: StructureAggregate, command: RemoveSearchPhraseCommand): void {

		aggregate.removeSearchPhrase();
	}

	publish(aggregate: StructureAggregate, command: RemoveSearchPhraseCommand): void {
		this.domainEventPublisher.publish(new FilterToggledEvent(command.getAggregateId()));
	}

}
