import { Injectable, Injector } from '@angular/core';

import { FabricDialogService } from '@generic-ui/fabric';

import { SchemaWarehouse } from '../../../../../../core/schema/src/api/schema.warehouse';
import { StructureThemeConverter } from '../../../../../../feature-view/grid-view/src/schema/structure.theme.converter';
import { SchemaReadModelRootId } from '../../../../../../core/schema/src/api/global/schema.read-model-root-id';
import { SchemaTheme } from '../../../../../../core/schema/src/api/theme/schema-theme';
import { ActiveFilterMenuComponent } from './active-filter-menu.component';
import { StructureId } from '../../../../../../core/structure/structure-core/src/api/global/structure.id';
import { Reactive } from '@generic-ui/hermes';


@Injectable()
export class ActiveFilterService extends Reactive {

	constructor(private readonly injector: Injector,
				private readonly schemaWarehouse: SchemaWarehouse,
				private readonly structureThemeConverter: StructureThemeConverter,
				private readonly fabricDialogService: FabricDialogService) {
		super();
	}

	open(readModelId: SchemaReadModelRootId, structureId: StructureId): void {

		const injector = Injector.create({
			parent: this.injector,
			providers: [
				{ provide: SchemaReadModelRootId, useValue: readModelId },
				{ provide: StructureId, useValue: structureId }
			]
		});

		this.schemaWarehouse
			.onceTheme(readModelId)
			.pipe(
				this.hermesTakeUntil()
			)
			.subscribe((theme: SchemaTheme) => {
				this.fabricDialogService.open({
					injector: injector,
					component: ActiveFilterMenuComponent,
					theme: this.structureThemeConverter.convertTheme(theme)
				});
			});
	}

}
