import { FieldId } from './field/field.id';
import { FieldEntity } from './field/field.entity';
import { FieldConfig } from '../api/config/field.config';
import { FieldEntityFactory } from './field/field.entity-factory';


export class FieldCollection {

	private readonly fields = new Map<string, FieldEntity>();

	constructor(private readonly fieldFactory: FieldEntityFactory) {
	}

	getField(fieldId: FieldId): FieldEntity {

		if (fieldId === null) {
			return this.getAllFields()[0];
		}

		return this.fields.get(fieldId.getId());
	}

	getAllFieldIds(): Array<string> {
		return Array.from(this.fields.keys());
	}

	getAllFields(): Array<FieldEntity> {
		return Array.from(this.fields.values());
	}

	getFieldsAsMap(): Map<string, FieldEntity> {
		return this.fields;
	}

	initFields(fieldConfigs: Array<FieldConfig>): void {

		const fields: Array<FieldEntity> = this.fieldFactory.create(fieldConfigs);

		this.clear();
		this.addFields(fields);
	}

	addFields(fields: Array<FieldEntity>): void {
		for (const field of fields) {
			this.addField(field);
		}
	}

	private addField(field: FieldEntity): void {
		this.fields.set(field.getId().toString(), field);
	}

	private clear(): void {
		this.fields.clear();
	}
}
