import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnChanges, OnInit, ViewEncapsulation } from '@angular/core';
import { Item } from '../../../../../core/structure/source/src/api/item/item';
import { FormationPublisher } from '../../../../../core/structure/formation/src/api/formation.publisher';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { RowSelectToggleType } from '../../../../../core/structure/formation/src/domain/row-select-toggle-type';
import { FormationWarehouse } from '../../../../../core/structure/formation/src/api/formation.warehouse';
import { SmartComponent } from '../../../../../feature/common/src/cdk/component/lib/src/smart-component';
import { RowSelectedReadModel } from '../../../../../core/structure/formation/src/api/row-selected/row-selected.read-model';
import { GuiRowClass, GuiRowStyle } from '../../../../../gui/grid/src/core/api/gui.grid.public-api';
import { StyleModifier } from '../../../../../feature/common/src/cdk/component/lib/src/dom/style/style-modifier';
import { ClassModifier } from '../../../../../feature/common/src/cdk/component/lib/src/dom/class/class-modifier';
import { CssClassModifier } from '../../../../../feature/structure/common/src/css-class/css-class.modifier';
import { NgChanges } from '../../../../../feature/common/src/cdk/component/lib/src/ng-changes';
import { CellEditorManager } from '../../../../../core/structure/structure-core/src/domain/edit/cell-editor.manager';
import { ifChanged } from '../../../../../feature/common/src/cdk/component/lib/src/check.input';
import { CompositionAccessorModel } from '../../../../../feature/composition/src/column/composition.accessor.model';


@Component({
	selector: 'div[gui-structure-row]',
	templateUrl: './structure-row.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureRowComponent extends SmartComponent implements OnChanges, OnInit {

	@Input()
	entity: Item;

	@Input()
	columns: Array<CompositionAccessorModel>;

	@Input()
	editMode: boolean;

	@Input()
	cellEditing: CellEditorManager;

	@Input()
	searchPhrase: string;

	@Input()
	index: number;

	@Input()
	rowStyle: GuiRowStyle;

	@Input()
	rowClass: GuiRowClass;

	@Input()
	detailsEnabled: boolean = false;

	@Input()
	checkboxSelection: boolean = false;

	@Input()
	radioSelection: boolean = false;

	selectedItem: boolean = false;

	private row: RowSelectedReadModel;

	private readonly styleModifier: StyleModifier;

	private readonly classModifier: ClassModifier;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly elRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly formationCommandDispatcher: FormationPublisher,
				private readonly cssClassModifier: CssClassModifier) {
		super(changeDetectorRef, elRef);
		this.styleModifier = new StyleModifier(this.elRef.nativeElement);
		this.classModifier = new ClassModifier(this.elRef.nativeElement);
	}

	ngOnChanges(changes: NgChanges<StructureRowComponent>) {

		ifChanged(changes.entity, () => {
			this.checkSelectedItem();
			this.updateRowClass(changes.entity.previousValue);
			this.updateRowStyle(changes.entity.previousValue);
		});

		ifChanged(changes.rowClass, () => {
			this.updateRowClass();
		});

		ifChanged(changes.rowStyle, () => {
			this.updateRowStyle();
		});
	}

	ngOnInit() {

		this.subscribeWithoutRender(
			this.formationWarehouse.onRowSelectedReadModel(this.structureId),
			(row: RowSelectedReadModel) => {
				this.row = row;
				const prevValue = this.selectedItem;

				this.checkSelectedItem();

				if (prevValue !== this.selectedItem) {

					if (prevValue) {
						this.cssClassModifier.unselect(this.elRef.nativeElement);
					} else {
						this.cssClassModifier.select(this.elRef.nativeElement);
					}

					this.changeDetectorRef.detectChanges();
				}
			}
		);
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();

		this.updateRowClass();
		this.updateRowStyle();
	}

	trackByFn(): number {
		return 0;
	}

	selectCheckbox(): void {
		event.stopPropagation();
		this.formationCommandDispatcher.toggleSelectedRow(this.entity.getId(), RowSelectToggleType.ADD, this.structureId);
	}

	selectRadio(): void {
		event.stopPropagation();
		this.formationCommandDispatcher.toggleSelectedRow(this.entity.getId(), RowSelectToggleType.NONE, this.structureId);
	}

	checkSelectedItem(): void {
		if (this.row) {

			const prevValue = this.selectedItem;

			this.selectedItem = this.row.isSelected(this.entity.getId());

			if (prevValue !== this.selectedItem) {

				if (prevValue) {
					this.cssClassModifier.unselect(this.elRef.nativeElement);
				} else {
					this.cssClassModifier.select(this.elRef.nativeElement);
				}
			}
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-row';
	}

	private calculateRowStyle(entity: Item): string {

		if (!this.rowStyle) {
			return '';
		}

		if (typeof this.rowStyle.style === 'string') {
			return this.rowStyle.style || '';
		}

		if (typeof this.rowStyle.styleFunction === 'function') {
			return this.rowStyle.styleFunction(entity.getSourceItem(), entity.getPosition());
		}

		return '';
	}

	private updateRowStyle(prevEntity?: Item): void {

		if (prevEntity) {
			const rowStylesToRemove = this.calculateRowStyle(prevEntity);

			this.removeRowStyles(rowStylesToRemove);
		}

		const rowStylesToAdd = this.calculateRowStyle(this.entity);

		this.renderRowStyles(rowStylesToAdd);
	}

	private removeRowStyles(rowStyle: string): void {

		if (!rowStyle) {
			return;
		}

		const styles = rowStyle.split(';');

		for (let i = 0; i < styles.length; i += 1) {

			const separatedStyles = styles[i].split(':');

			if (separatedStyles[0] && separatedStyles[1]) {
				this.styleModifier.getHost().removeStyleByName(separatedStyles[0].trim());
			}
		}
	}

	private renderRowStyles(rowStyle: string): void {

		if (!rowStyle) {
			return;
		}

		const styles = rowStyle.split(';');

		for (let i = 0; i < styles.length; i += 1) {

			const separatedStyles = styles[i].split(':');

			if (separatedStyles[0] && separatedStyles[1]) {
				this.styleModifier.getHost().setStyleByName(separatedStyles[0].trim(), separatedStyles[1].trim());
			}
		}
	}

	private updateRowClass(prevEntity?: Item): void {

		if (prevEntity) {
			const toRemoveClass = this.calculateRowClass(prevEntity);

			if (toRemoveClass) {
				this.classModifier.getHost().remove(toRemoveClass);
			}
		}

		const toAddClass = this.calculateRowClass(this.entity);

		if (toAddClass) {
			this.classModifier.getHost().add(toAddClass);
		}
	}

	private calculateRowClass(entity: Item): string {

		if (!this.rowClass) {
			return '';
		}

		let clazz: string;

		if (typeof this.rowClass.class === 'string') {
			clazz = this.rowClass.class || '';
		}

		if (typeof this.rowClass.classFunction === 'function') {
			if (entity) {
				clazz = this.rowClass.classFunction(entity.getSourceItem(), entity.getPosition()) || '';
			}
		}

		return clazz;
	}

}
