import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, OnInit, ViewEncapsulation } from '@angular/core';

import { FabricDialogService } from '@generic-ui/fabric';

import { FilterMenuComponent } from './filter-menu.component';
import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { FilterWarehouse } from '../../../../../core/structure/filter/src/api/filter.warehouse';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { ActiveFilterModel } from '../../../../../core/structure/filter/src/api/active/active-filter.model';

@Component({
	selector: 'div[gui-filter-menu-trigger]',
	templateUrl: './filter-menu-trigger.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	styleUrls: [
		'../style/filter.scss'
	]
})
export class FilterMenuTriggerComponent extends SmartComponent implements OnInit {

	activeFiltersExist: boolean = false;

	constructor(detector: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly injector: Injector,
				private readonly fabricDialogService: FabricDialogService,
				private readonly structureId: StructureId,
				private readonly filterWarehouse: FilterWarehouse) {
		super(detector, elementRef);
	}

	ngOnInit() {

		this.subscribe(
			this.filterWarehouse.onActiveFilters(this.structureId),
			(activeFilters: ReadonlyArray<ActiveFilterModel>) => {
				this.activeFiltersExist = activeFilters.length > 0;
			}
		);
	}

	openDrawer(): void {
		this.fabricDialogService.open({ component: FilterMenuComponent, injector: this.injector });
	}

	protected getSelectorName(): string {
		return 'gui-filter-menu-trigger';
	}
}
