import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SearchPhraseArchive } from './search-phrase.archive';
import { SearchPhraseSetDomainEvent } from './set-phrase/search-phrase-set.domain-event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class SearchPhraseSetEventHandler implements DomainEventHandler<StructureId, SearchPhraseSetDomainEvent> {

	constructor(private readonly searchPhraseRepository: SearchPhraseArchive) {
	}

	static readonly services = [SearchPhraseArchive];

	forEvent(): DomainEventType<SearchPhraseSetDomainEvent> {
		return SearchPhraseSetDomainEvent;
	}

	handle(event: SearchPhraseSetDomainEvent): void {

		if (event.ofMessageType('SearchPhraseSetDomainEvent')) {
			this.searchPhraseRepository.next(event.getAggregateId(), event.getPhrase());
		}
	}

}
