import { Changes } from './ng-changes';

interface InputChanges {

	isChanged(): boolean;

	ifChanged(callback: () => void): void;

}

export function checkInput<T>(prop: Changes<T>): InputChanges {

	return {
		isChanged: () => {
			return hasChanged(prop);
		},
		ifChanged: (callback: () => void) => {
			if (hasChanged(prop)) {
				callback();
			}
		}
	} as InputChanges;

}

export function ifChanged<T>(prop: Changes<T>, callback: (value: any) => void): void {
	if (hasChanged(prop)) {
		callback(prop.currentValue);
	}
}

export function hasChanged<T>(prop: Changes<T>) {
	return prop !== undefined && prop.currentValue !== undefined;
}
