import { hermesMap, HermesObservable, Optional } from '@generic-ui/hermes';

import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SourceWarehouse } from '../../../source/src/api/source.warehouse';
import { FormationWarehouse } from '../api/formation.warehouse';
import { RowSelectedArchive } from './row-selected/row-selected.archive';
import { SelectedRow } from '../api/row-selected/selected-row';
import { RowSelectedReadModel } from '../api/row-selected/row-selected.read-model';
import { RowSelectionMode, RowSelectionType } from '../api/row-selected/row-selection';
import { FormationTypeRepository } from './type/formation.type.repository';
import { FormationModeRepository } from './mode/formation.mode.repository';
import { FormationEnabledRepository } from './enabled/formation.enabled.repository';
import { FormationCustomSelection } from '../api/custom/formation.custom-selection';
import { FormationCustomRepository } from './custom/formation.custom.repository';


export class FormationDomainWarehouse extends FormationWarehouse {

	constructor(private readonly rowSelectedRepository: RowSelectedArchive,
				private readonly formationModeRepository: FormationModeRepository,
				private readonly formationTypeRepository: FormationTypeRepository,
				private readonly formationEnabledRepository: FormationEnabledRepository,
				private readonly formationCustomRepository: FormationCustomRepository,
				private readonly sourceWarehouse: SourceWarehouse) {
		super();
	}

	static readonly services = [
		RowSelectedArchive,
		FormationModeRepository,
		FormationTypeRepository,
		FormationEnabledRepository,
		FormationCustomRepository,
		SourceWarehouse
	] as const;

	findSelectedRows(structureId: StructureId): Optional<ReadonlyArray<SelectedRow>> {

		const items = this.sourceWarehouse.findPreparedItems(structureId),
			selectedItemIds = this.findSelectedItemIds(structureId).getValueOrNullOrThrowError();

		const selectedItems = [],
			length = items.length;

		for (let i = 0; i < length; i += 1) {

			const item = items[i];

			if (selectedItemIds.some((itemId: string) => itemId === item.getId().toString())) {
				selectedItems.push(new SelectedRow(item.getSourceItem(), i, item.getId()));
			}
		}
		return Optional.of(selectedItems);
	}

	onRowSelectedReadModel(structureId: StructureId): HermesObservable<RowSelectedReadModel> {
		return this.rowSelectedRepository
				   .on(structureId);
	}

	findSelectedItemIds(structureId: StructureId): Optional<ReadonlyArray<string>> {
		return this.rowSelectedRepository
				   .find(structureId)
				   .map((r: RowSelectedReadModel) => {
					   return r.getAll();
				   });
	}

	onSelectedRows(structureId: StructureId): HermesObservable<ReadonlyArray<string>> {
		return this.rowSelectedRepository
				   .on(structureId)
				   .pipe(
					   hermesMap((rm: RowSelectedReadModel) => {
						   return rm.getAll();
					   })
				   );
	}

	onMode(structureId: StructureId): HermesObservable<RowSelectionMode> {
		return this.formationModeRepository.on(structureId);
	}

	onType(structureId: StructureId): HermesObservable<RowSelectionType> {
		return this.formationTypeRepository.on(structureId);
	}

	onSelectionEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.formationEnabledRepository.on(structureId);
	}

	onCustomSelections(structureId: StructureId): HermesObservable<FormationCustomSelection> {
		return this.formationCustomRepository.on(structureId);
	}

}
