import { AggregateId, CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { SetSortOrderCommand } from './set-sort-order.command';
import { SortOrderSetEvent } from './sort-order-set.event';
import { CompositionId } from '../../../../../composition/src/api/global/composition.id';
import { FieldId } from '../../../../field/src/domain/field/field.id';
import { Sorter } from '../../domain/sorter';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SetSortOrderCommandHandler implements CommandHandler<StructureAggregate, SetSortOrderCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetSortOrderCommand> {
		return SetSortOrderCommand;
	}

	handle(structure: StructureAggregate, command: SetSortOrderCommand): void {

		const structureId = command.getAggregateId(),
			compositionId = command.getCompositionId(),
			fieldId = command.getFieldId(),
			sortOrder = command.getSortOrder();

		const sorters = structure.setSortOrder(fieldId, sortOrder);

		this.publishSortEvent(structureId, compositionId, fieldId, sorters);
	}

	publish(aggregate: StructureAggregate, command: SetSortOrderCommand): void {
	}

	private publishSortEvent(aggregateId: AggregateId, compositionId: CompositionId, fieldId: FieldId, sorters: Array<Sorter>): void {

		const directions = sorters.map((sorter: Sorter) => {
			return {
				direction: sorter.hasDirection(),
				fieldId: sorter.getField().getId()
			};
		});

		this.domainEventPublisher.publish(new SortOrderSetEvent(aggregateId, compositionId, directions));
	}

}
