import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SummariesValues } from '../domain/calculation/summaries.values';
import { StructureDomainEvent } from '../../../structure-core/src/core/structure.domain-event';


export class StructureSummariesChangedEvent extends StructureDomainEvent {

	constructor(structureId: StructureId,
				private readonly values: Map<string, SummariesValues>) {
		super(structureId, values, 'StructureSummariesChangedEvent');
	}

	getSummaries() {
		return this.values;
	}

}
