import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { ActiveFiltersSetEvent } from '../../core/active-filters-set.event';
import { ActiveFilterArchive } from './active-filter.archive';
import { ActiveFilterModel } from '../../api/active/active-filter.model';
import { FilterDiContainer } from '../../api/di/filter.di-container';


export class ActiveFiltersSetEventHandler implements DomainEventHandler<StructureId, ActiveFiltersSetEvent> {

	private readonly activeFilterRepository: ActiveFilterArchive = FilterDiContainer.resolve(ActiveFilterArchive);

	forEvent(): DomainEventType<ActiveFiltersSetEvent> {
		return ActiveFiltersSetEvent;
	}

	handle(activeFiltersSetEvent: ActiveFiltersSetEvent): void {

		if (activeFiltersSetEvent.ofMessageType('ActiveFiltersSetEvent')) {

			const activeFilters: ReadonlyArray<ActiveFilterModel> = activeFiltersSetEvent.getFilters();

			this.activeFilterRepository.next(activeFiltersSetEvent.getAggregateId(), activeFilters);
		}
	}

}
