import { TemplateRef } from '@angular/core';
import { CellTemplateWithAccessor } from '../../../../core/composition/src/core-read/definition/cell-template-with-accessor';

export class CompositionAccessorModel extends CellTemplateWithAccessor {

	constructor(public template: TemplateRef<any> | null = null,
				public editTemplate: TemplateRef<any> | null = null,
				context: CellTemplateWithAccessor) {
		super(
			context.getColumnConfig(),
			context.columnDefinitionId,
			context.getName(),
			context.editable,
			context.templateFun,
			context.formatterFun,
			context.accessor,
			context.searchAccessor,
			context.width,
			context.columnFieldId,
			context.getAlign(),
			context.isCellEditingEnabled(),
			context.getType(),
			context.getView()
		);
	}
}
