import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SelectedRowChangedEvent } from '../../core/selected-row-changed.event';
import { RowSelectedArchive } from './row-selected.archive';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { RowSelectedReadModel } from '../../api/row-selected/row-selected.read-model';


export class SelectedRowChangedEventHandler implements DomainEventHandler<StructureId, SelectedRowChangedEvent> {

	constructor(private readonly rowSelectedArchive: RowSelectedArchive) {
	}

	static readonly services = [RowSelectedArchive] as const;

	forEvent(): DomainEventType<SelectedRowChangedEvent> {
		return SelectedRowChangedEvent;
	}

	handle(rowChangedEvent: SelectedRowChangedEvent): void {

		if (rowChangedEvent.ofMessageType('SelectedRowChangedEvent')) {

			const rowSelectedRead = new RowSelectedReadModel(
				rowChangedEvent.getSelectedRows(),
				rowChangedEvent.isAllSelected(),
				rowChangedEvent.isAllUnselected()
			);

			this.rowSelectedArchive.next(rowChangedEvent.getAggregateId(), rowSelectedRead);
		}
	}

}
