import { GenericBuilder } from '../../../../../../common/src/cdk/generic-builder';
import { FormationCustomSelectionConfig } from '../custom/formation.custom-selection.config';

export enum RowSelectionType {
	ROW,
	CHECKBOX,
	RADIO
}

export enum RowSelectionMode {
	SINGLE,
	MULTIPLE
}

export class RowSelection {

	private readonly enabled: boolean;

	private readonly selectedRowIndexes: Array<number>;

	private readonly selectedRowIds: Array<string>;

	private readonly type: RowSelectionType;

	private readonly mode: RowSelectionMode;

	private readonly matcher: (item: any) => any;

	private readonly customConfig?: FormationCustomSelectionConfig;

	private constructor(config: {
		enabled?: boolean,
		selectedRowIndexes?: Array<number>
		selectedRowIds?: Array<string>
		mode?: RowSelectionMode,
		type?: RowSelectionType,
		matcher?: (item: any) => any,
		config?: FormationCustomSelectionConfig
	}) {
		if (config?.enabled) {
			this.enabled = config.enabled;
		}

		if (config?.selectedRowIndexes) {
			this.selectedRowIndexes = config.selectedRowIndexes;
		}

		if (config?.selectedRowIds) {
			this.selectedRowIds = config.selectedRowIds;
		}

		if (config?.mode) {
			this.mode = config.mode;
		}

		if (config?.type) {
			this.type = config.type;
		}

		if (config?.matcher) {
			this.matcher = config.matcher;
		}

		if (config?.config) {
			this.customConfig = config.config;
		}
	}

	isModeDefined(): boolean {
		return this.mode !== undefined;
	}

	isTypeDefined(): boolean {
		return this.type !== undefined;
	}

	isSelectedRowIndexesDefined(): boolean {
		return this.selectedRowIndexes !== undefined;
	}

	isSelectedRowIdsDefined(): boolean {
		return this.selectedRowIds !== undefined;
	}

	isEnabledDefined(): boolean {
		return this.enabled !== undefined;
	}

	isMatcherDefined(): boolean {
		return this.matcher !== undefined;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	isCustomSelectConfig(): boolean {
		return this.customConfig !== undefined;
	}

	getSelectedRowIndexes(): Array<number> {
		return this.selectedRowIndexes;
	}

	getSelectedRowIds(): Array<string> {
		return this.selectedRowIds;
	}

	getMode(): RowSelectionMode {
		return this.mode;
	}

	getType(): RowSelectionType {
		return this.type;
	}

	getMatcher(): (item: any) => any {
		return this.matcher;
	}

	getCustomSelectConfig(): FormationCustomSelectionConfig {
		return this.customConfig;
	}

	// eslint-disable-next-line @typescript-eslint/member-ordering
	static Builder = class RowSelectionBuilder extends GenericBuilder<RowSelection> {

		enabled: boolean;

		selectedRowIndexes: Array<number>;

		selectedRowIds: Array<string>;

		type: RowSelectionType;

		mode: RowSelectionMode;

		config: FormationCustomSelectionConfig;

		matcher: (item: any) => any;

		constructor() {
			super();
		}

		buildObject(): RowSelection {
			return new RowSelection({
				enabled: this.enabled,
				selectedRowIndexes: this.selectedRowIndexes,
				selectedRowIds: this.selectedRowIds,
				type: this.type,
				mode: this.mode,
				matcher: this.matcher,
				config: this.config
			});
		}

		withEnabled(enabled: boolean): RowSelectionBuilder {
			this.enabled = enabled;
			return this;
		}

		withSelectedRowIndexes(indexes: Array<number>): RowSelectionBuilder {
			this.selectedRowIndexes = indexes;
			return this;
		}

		withSelectedRowIds(ids: Array<string>): RowSelectionBuilder {
			this.selectedRowIds = ids;
			return this;
		}

		withType(type: RowSelectionType): RowSelectionBuilder {
			this.type = type;
			return this;
		}

		withMode(mode: RowSelectionMode): RowSelectionBuilder {
			this.mode = mode;
			return this;
		}

		withMatcher(matcher: (item: any) => any): RowSelectionBuilder {
			this.matcher = matcher;
			return this;
		}

		witCustomSelection(config: FormationCustomSelectionConfig): RowSelectionBuilder {
			this.config = config;
			return this;
		}

	};

}
