import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { SelectionTypeSetEvent } from './selection-type-set.event';
import { RowSelectionType } from '../../api/row-selected/row-selection';
import { StructureAggregateEvent } from '../../../../structure-core/src/core/structure.aggregate-event';

export class SelectionTypeSetAggregateEvent extends StructureAggregateEvent {

	constructor(structureId: StructureId,
				private readonly selectionType: RowSelectionType) {
		super(structureId, 'SelectionTypeSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new SelectionTypeSetEvent(this.getAggregateId(), this.selectionType);
	}
}
