import { CompositionId } from '../../../../../composition/src/api/global/composition.id';
import { FieldId } from '../../../../field/src/domain/field/field.id';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { StructureDomainEvent } from '../../../../structure-core/src/core/structure.domain-event';


export class SortToggledEvent extends StructureDomainEvent {

	constructor(aggregateId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly directions: Array<{ fieldId: FieldId, direction: boolean }>) {
		super(aggregateId, { compositionId, directions }, 'SortToggledEvent');
	}

	getCompositionId(): CompositionId {
		return this.compositionId;
	}

	getDirections(): any {
		return this.directions;
	}
}
