import { ItemEntity } from '../../../source/src/domain/item/item.entity';
import { DataType } from '../../../field/src/domain/field/data/data-type';
import { FieldEntity } from '../../../field/src/domain/field/field.entity';


export class SearchManager {

	private searchFields: Array<FieldEntity> = [];

	private searchPhrase: string;

	private enabledDataTypes: Array<DataType> = [
		DataType.STRING
	];

	addSearchPhrase(fields: Array<FieldEntity>, searchPhrase: string): void {

		if (!searchPhrase) {
			this.searchFields = [];
			return;
		}

		const searchableFields =
			fields.filter((field: FieldEntity) => {
				return this.enabledDataTypes.some((dt: DataType) => dt === field.getDataType());
			});

		if (searchableFields.length === 0) {
			return;
		}

		this.searchFields = searchableFields;

		this.searchPhrase = searchPhrase;
	}

	removeSearchFilters(): void {
		this.searchFields = [];
	}

	search(entities: Array<ItemEntity>): Array<ItemEntity> {

		if (entities.length === 0 || this.searchFields.length === 0) {
			return entities;
		}

		const searchedItems = new Set<ItemEntity>();

		for (let i = 0; i < entities.length; i += 1) {

			this.searchFields
				.filter((field: FieldEntity) => field.search(entities[i], this.searchPhrase))
				.forEach(() => {
					searchedItems.add(entities[i]);
				});
		}

		return Array.from(searchedItems);
	}

}
