import { Directive, Input, OnChanges } from '@angular/core';

import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { Gate } from '../../../../common/src/cdk/component/lib/src/gate';
import { SortingConfig } from '../../../../../core/structure/sorting/src/api/config/sorting-config';
import { NgChanges } from '../../../../common/src/cdk/component/lib/src/ng-changes';
import { SortingPublisher } from '../../../../../core/structure/sorting/src/api/sorting.publisher';
import { ifChanged } from '../../../../common/src/cdk/component/lib/src/check.input';


@Directive({
	selector: 'gui-structure[sorting]'
})
export class StructureSortingGate extends Gate implements OnChanges {

	@Input()
	sorting: boolean | SortingConfig;

	constructor(private readonly structureId: StructureId,
				private readonly sortingCommandInvoker: SortingPublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureSortingGate>) {

		ifChanged(changes.sorting, () => {

			let sorting: SortingConfig;

			if (typeof this.sorting === 'boolean') {
				sorting = {
					enabled: this.sorting
				};
			} else {
				sorting = this.sorting;
			}

			this.sortingCommandInvoker.setSortingConfig(sorting, this.structureId);
		});
	}
}
