import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { NextPageCommand } from './next-page.command';
import { NextPageEvent } from './next-page.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class NextPageCommandHandler implements CommandHandler<StructureAggregate, NextPageCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<NextPageCommand> {
		return NextPageCommand;
	}

	handle(aggregate: StructureAggregate, command: NextPageCommand): void {

		// missing implementation
		aggregate.nextPage();
	}

	publish(aggregate: StructureAggregate, command: NextPageCommand): void {
		this.domainEventPublisher.publish(new NextPageEvent(command.getAggregateId()));
	}

}
