import { ReadModelRoot } from '@generic-ui/hermes';

import { PagingModel } from '../../../../paging/src/api/model/paging.model';
import { SourceIsLoadingModel } from '../../../../source/src/core-read/source-is-loading.model';
import { Item } from '../../../../source/src/api/item/item';
import { VerticalFormationModel } from '../../../../vertical-formation/src/core-read/vertical-formation.model';
import { StructureReadModelRootId } from './structure.read-model-root-id';


export class StructureReadModelRoot extends ReadModelRoot<StructureReadModelRootId> {

	private readonly paging: PagingModel;

	private readonly entities: Array<Item>;

	private readonly source: SourceIsLoadingModel;

	private readonly verticalFormation: VerticalFormationModel;

	constructor(uid: StructureReadModelRootId,
				paging: PagingModel,
				entities: Array<Item>,
				source: SourceIsLoadingModel,
				verticalFormation: VerticalFormationModel) {
		super(uid);
		this.paging = paging;
		this.entities = entities;
		this.source = source;
		this.verticalFormation = verticalFormation;
	}

	getPaging(): PagingModel {
		return this.paging;
	}

	getEntities(): Array<Item> {
		return this.entities;
	}

	getSource(): SourceIsLoadingModel {
		return this.source;
	}

	getVerticalFormation(): VerticalFormationModel {
		return this.verticalFormation;
	}

	// TODO Remove
	getTopMargin(): number {
		return this.verticalFormation.getTopMargin();
	}

	isLoaderVisible(): boolean {
		return this.getSource().isLoading();
	}

	getSourceHeight(): number {
		return this.verticalFormation.getSourceHeight();
	}

	isReadyToDisplay(): boolean {
		return this.entities.length > 0;
		// this.presentation.getColumns().length > 0; // TODO presentation getColumns
	}

	isVerticalScrollEnabled(): boolean {
		return this.verticalFormation.isEnabled();
	}

}

