import { FilterWarehouse } from '../api/filter.warehouse';
import { FilterEnabledArchive } from './config/filter-enabled.archive';
import { QuickFilterEnabledArchive } from './quick-filter/quick-filter-enabled.archive';
import { FilterTypeArchive } from './type/filter-type.archive';
import { UniqueValueCollectionArchive } from './unique/unique-value-collection.archive';
import { ActiveFilterArchive } from './active/active-filter.archive';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { hermesMap, HermesObservable, HermesSingle, Optional, singleFromObservable } from '@generic-ui/hermes';
import { FilterTypeCollectionModel } from '../api/type/filter-type-collection.model';
import { ActiveFilterModel } from '../api/active/active-filter.model';
import { UniqueValueCollectionModel } from '../api/unique/unique-value-collection.model';
import { FieldId } from '../../../field/src/domain/field/field.id';
import { FilterTypeModel } from '../api/type/filter-type.model';
import { FilterTypeId } from '../domain/type/filter-type.id';
import { FilterDiContainer } from '../api/di/filter.di-container';


export class FilterDomainWarehouse extends FilterWarehouse {

	private readonly activeFilterArchive: ActiveFilterArchive = FilterDiContainer.resolve(ActiveFilterArchive);

	private readonly filterTypeArchive: FilterTypeArchive = FilterDiContainer.resolve(FilterTypeArchive);

	constructor(private readonly filterEnabledArchive: FilterEnabledArchive,
				private readonly structureQuickFilterRepository: QuickFilterEnabledArchive,
				private readonly uniqueValuesArchive: UniqueValueCollectionArchive) {
		super();
	}

	static readonly services = [FilterEnabledArchive, QuickFilterEnabledArchive, UniqueValueCollectionArchive];

	onFilteringEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.filterEnabledArchive.on(structureId);
	}

	onQuickFiltersEnabled(structureId: StructureId): HermesObservable<boolean> {
		return this.structureQuickFilterRepository.on(structureId);
	}

	onFilterTypes(structureId: StructureId): HermesObservable<FilterTypeCollectionModel> {
		return this.filterTypeArchive.on(structureId);
	}

	onFilterTypesForFieldId(fieldId: FieldId, structureId: StructureId): HermesObservable<ReadonlyArray<FilterTypeModel>> {
		return this.onFilterTypes(structureId)
				   .pipe(
					   hermesMap((map: FilterTypeCollectionModel) => {
						   return map.getFilterTypes(fieldId);
					   })
				   );
	}

	onActiveFilters(structureId: StructureId): HermesObservable<ReadonlyArray<ActiveFilterModel>> {
		return this.activeFilterArchive.on(structureId);
	}

	findFilters(structureId: StructureId): Optional<ReadonlyArray<ActiveFilterModel>> {
		return this.activeFilterArchive.find(structureId);
	}

	onUniqueValues(structureId: StructureId): HermesObservable<UniqueValueCollectionModel> {
		return this.uniqueValuesArchive.on(structureId);
	}

	onceFilterTypeId(fieldId: FieldId, filterTypeName: string, structureId: StructureId): HermesSingle<Optional<FilterTypeId>> {
		return singleFromObservable(
			this.onFilterTypes(structureId)
				.pipe(
					hermesMap((map: FilterTypeCollectionModel) => {
						const filterTypes = map.getFilterTypes(fieldId);

						const filterType = filterTypes.find((fieldType) => fieldType.getName() === filterTypeName);

						if (filterType === undefined) {
							return Optional.empty();
						} else {
							return Optional.of(filterType.getId());
						}
					})
				)
		);

	}

}
