import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../feature/common/src/cdk/component/lib/src/smart-component';
import { CellTemplateWithContext } from '../../../../core/composition/src/core-read/definition/cell-template-with-context';
import { CompositionPublisher } from '../../../../core/composition/src/api/composition.publisher';
import { CompositionId } from '../../../../core/composition/src/api/global/composition.id';
import { CompositionTemplateWarehouse } from '../../../../feature/composition/src/column/composition.template-warehouse';
import { CompositionTemplateModel } from '../../../../feature/composition/src/column/composition.template.model';


@Component({
	selector: 'div[gui-structure-column-manager]',
	template: `

		<ol class="gui-structure-ordered-list gui-p-0 gui-my-4 gui-list-none gui-overflow-auto">
			<li *ngFor="let column of columns"
				(click)="toggleColumn(column)"
				class="gui-px-13 gui-py-6 gui-cursor-pointer">

				<gui-checkbox [checked]="column.isEnabled()"
							  [disabled]="enabledColumnsCount === 1 && column.isEnabled()">

					<ng-container
						*ngTemplateOutlet="column.viewTemplate;
											context: column.context">
					</ng-container>

				</gui-checkbox>

			</li>
		</ol>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnManagerComponent extends SmartComponent implements OnInit {

	columns: Array<CompositionTemplateModel>;

	enabledColumnsCount: number;

	constructor(private changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private compositionId: CompositionId,
				private compositionCommandInvoker: CompositionPublisher,
				private compositionTemplateWarehouse: CompositionTemplateWarehouse) {
		super(changeDetectorRef, elementRef);

		this.addClassToHost('gui-block');
	}

	ngOnInit() {

		this.subscribe(
			this.compositionTemplateWarehouse.onAll(this.compositionId),
			(columns: Array<CompositionTemplateModel>) => {
				this.columns = columns;
				this.enabledColumnsCount = this.columns
											   .map((c) => +c.isEnabled())
											   .reduce((accumulator, currentValue) => accumulator + currentValue);
			}
		);
	}

	toggleColumn(column: CellTemplateWithContext): void {
		event.stopPropagation();
		if (column.isEnabled()) {
			this.compositionCommandInvoker.disableColumn(column.getColumnDefinitionId(), this.compositionId);
		} else {
			this.compositionCommandInvoker.enableColumn(column.getColumnDefinitionId(), this.compositionId);
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-column-manager';
	}
}
