import { Item } from '../../api/item/item';
import { ItemEntity } from '../../domain/item/item.entity';


export class ItemFactory {

	create(source: any): Item;
	create(source: Array<any>): Array<Item>;
	create(source: any): any {

		if (Array.isArray(source)) {
			return this.createItems(source);
		} else {
			return this.createItem(source);
		}
	}

	createItems(source: Array<any>): Array<Item> {
		return source.map((source: any) => this.createItem(source));
	}

	private createItem(originItem: ItemEntity | any): Item {

		if (originItem instanceof ItemEntity) {
			return new Item(originItem.sourceItem, originItem.getPosition(), originItem.getId().toString(), originItem.getVersion());
		} else {
			return new Item(originItem, 0);
		}
	}
}
