import { CommandDispatcher, CoreContainer } from '@generic-ui/hermes';

import { SortingConfig } from '../api/config/sorting-config';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SetSortingCommand } from './set-config/set-sorting.command';
import { FieldId } from '../../../field/src/domain/field/field.id';
import { CompositionId } from '../../../../composition/src/api/global/composition.id';
import { ToggleSortCommand } from './toggle/toggle-sort.command';
import { SortOrder } from '../../../../composition/src/domain/column/sort/sort-order';
import { SetSortOrderCommand } from './order/set-sort-order.command';


export class SortingDispatcher {

	private readonly commandDispatcher: CommandDispatcher = CoreContainer.resolve(CommandDispatcher);


	setSortingConfig(config: SortingConfig, structureId: StructureId): void {
		this.commandDispatcher.dispatch(new SetSortingCommand(structureId, config));
	}

	toggleSort(fieldId: FieldId, compositionId: CompositionId, structureId: StructureId): void {
		this.commandDispatcher.dispatch(new ToggleSortCommand(structureId, compositionId, fieldId));
	}

	setSortOrder(fieldId: FieldId, sortOrder: SortOrder, compositionId: CompositionId, structureId: StructureId): void {
		this.commandDispatcher.dispatch(new SetSortOrderCommand(structureId, compositionId, fieldId, sortOrder));
	}
}
