import { AggregateEvent, AggregateEventType, AggregateRoot } from '@generic-ui/hermes';

import { PagingManager } from '../../../paging/src/domain/paging.manager';
import { SourceManager } from '../../../source/src/domain/source.manager';
import { VerticalFormation } from '../../../vertical-formation/src/domain/vertical-formation';
import { SummariesManager } from '../../../summaries/src/domain/summaries.manager';
import { SorterManager } from '../../../sorting/src/domain/sorter.manager';
import { FilterManager } from '../../../filter/src/domain/filter.manager';
import { FieldCollection } from '../../../field/src/domain/field-collection';
import { FormationManager } from '../../../formation/src/domain/formation.manager';
import { StructureId } from '../api/global/structure.id';
import { StructureEditSourceItemParams } from '../../../source/src/core/origin/edit/structure.edit-source-item.params';
import { PagingConfig } from '../../../paging/src/api/config/paging-config';
import { ItemEntity } from '../../../source/src/domain/item/item.entity';
import { SortingConfig } from '../../../sorting/src/api/config/sorting-config';
import { FieldId } from '../../../field/src/domain/field/field.id';
import { Sorter } from '../../../sorting/src/domain/sorter';
import { FilterConfig } from '../../../filter/src/api/config/filter.config';
import { QuickFiltersConfig } from '../../../filter/src/api/quick-filter/quick-filters.config';
import { SearchConfig } from '../../../search/src/api/config/search-config';
import { SearchManager } from '../../../search/src/domain/search.manager';
import { StructurePreparedEntitiesSetAggregateEvent } from '../../../source/src/core/prepared/structure.prepared-entities-set.aggregate-event';
import { SortOrder } from '../../../../composition/src/domain/column/sort/sort-order';
import { FieldsInitedAggregateEvent } from '../../../field/src/core/init/fields-inited.aggregate-event';
import { SchemaTheme } from '../../../../schema/src/api/theme/schema-theme';
import { SchemaAggregate } from '../../../../schema/src/domain/schema.aggregate';
import { StructureCreatedAggregateEvent } from '../core/create/structure-created.aggregate-event';
import { FieldConfig } from '../../../field/src/api/config/field.config';
import { FilterTypesInitedAggregateEvent } from '../../../filter/src/core/type/init/filter-types-inited.aggregate-event';
import { AllFiltersRemovedAggregateEvent } from '../../../filter/src/core/remove-all/all-filters-removed.aggregate-event';
import { FilterTypeId } from '../../../filter/src/domain/type/filter-type.id';
import { FilterAddedAggregateEvent } from '../../../filter/src/core/add/filter-added.aggregate-event';
import { FilterId } from '../../../filter/src/api/id/filter.id';
import { FilterRemovedAggregateEvent } from '../../../filter/src/core/remove/filter-removed.aggregate-event';
import { UniqueFilterManager } from '../../../filter/src/domain/unique/unique-filter-manager';
import { UniqueFilterCalculatedAggregateEvent } from '../../../filter/src/core/unique/calculate/unique-filter-calculated.aggregate-event';
import { UniqueValueId } from '../../../filter/src/domain/unique/unique-value-id';
import { UniqueFilterSelectedAggregateEvent } from '../../../filter/src/core/unique/select/unique-filter-selected.aggregate-event';
import { UniqueFilterUnselectedAggregateEvent } from '../../../filter/src/core/unique/unselect/unique-filter-unselected.aggregate-event';
import { AllUniqueFilterUnselectedAggregateEvent } from '../../../filter/src/core/unique/unselect-all/all-unique-filter-unselected.aggregate-event';
import { AllUniqueFilterSelectedAggregateEvent } from '../../../filter/src/core/unique/select-all/all-unique-filter-selected.aggregate-event';
import { SelectedRowChangedAggregateEvent } from '../../../formation/src/core/selected-row-changed.aggregate-event';
import { RowSelectToggleType } from '../../../formation/src/domain/row-select-toggle-type';
import { SelectionEnabledSetAggregateEvent } from '../../../formation/src/core/set-enabled/selection-enabled-set.aggregate-event';
import { RowSelectionMode, RowSelectionType } from '../../../formation/src/api/row-selected/row-selection';
import { ItemEntityId } from '../../../source/src/domain/item/item.entity-id';
import { structureKey } from '../api/global/structre.key';
import { FormationCustomSelectId } from '../../../formation/src/api/custom/formation.custom-select.id';
import { FormationCustomSelectionConfig } from '../../../formation/src/api/custom/formation.custom-selection.config';


export class StructureAggregate extends AggregateRoot<StructureId> {

	private pagingManager: PagingManager;

	private readonly sourceManager: SourceManager;

	private sorterManager: SorterManager;

	private filterManager: FilterManager;

	private searchManager: SearchManager;

	private readonly verticalFormation: VerticalFormation;

	private fieldCollection: FieldCollection;

	private summariesManager: SummariesManager;

	private readonly uniqueFilterManager: UniqueFilterManager;

	constructor(structureId: StructureId,
				pagingAggregate: PagingManager,
				sourceManager: SourceManager,
				verticalFormation: VerticalFormation,
				summariesManager: SummariesManager,
				sorterCollection: SorterManager,
				filterManager: FilterManager,
				uniqueFilterManager: UniqueFilterManager,
				searchManager: SearchManager,
				fieldCollection: FieldCollection) {
		super(structureId, structureKey);
		this.pagingManager = pagingAggregate;
		this.sourceManager = sourceManager;
		this.verticalFormation = verticalFormation;
		this.summariesManager = summariesManager;
		this.sorterManager = sorterCollection;
		this.filterManager = filterManager;
		this.uniqueFilterManager = uniqueFilterManager;
		this.searchManager = searchManager;
		this.fieldCollection = fieldCollection;

		this.addEvent(
			this.sourceManager.events
		);
		this.sourceManager.events = [];
	}

	createEvent(): AggregateEventType<AggregateEvent<StructureId>> {
		return StructureCreatedAggregateEvent;
	}

	override clearEvents(): void {
		super.clearEvents();
		this.pagingManager.clearEvents();
	}

	init(): void {
		this.initTheme();
	}

	setVerticalFormationEnabled(enabled: boolean): void {
		this.verticalFormation.setEnabled(enabled);
		this.calculateSource();
	}

	getVerticalFormation(): VerticalFormation {
		return this.verticalFormation;
	}

	setSummariesEnabled(enabled: boolean): ReadonlyArray<AggregateEvent<StructureId>> {

		this.summariesManager.setEnabled(enabled);

		this.calculateSource();

		return this.getEvents();
	}

	setOrigin(items: Array<any>): ReadonlyArray<AggregateEvent<StructureId>> {

		this.addEvent(
			this.sourceManager.setOrigin(items, this.getId())
		);

		// if (false) {
		// 	this.calculateUniqueValues();
		// }

		this.calculateSource();

		return this.getEvents();
	}

	deleteAllSelected(): void {

	}

	deleteItem(payload: number | ItemEntityId | Array<number> | Array<ItemEntityId>): void {
		this.addEvent(
			this.sourceManager.deleteOriginItem(payload, this.getId())
		);
		this.generateSelectedRowChangedEvent();
		this.calculateSource();
	}

	editItem(editParams: StructureEditSourceItemParams): ReadonlyArray<AggregateEvent<StructureId>> {

		const itemId = editParams.getItemId(),
			fieldId = editParams.getColumnFieldId(),
			field = this.fieldCollection.getField(fieldId as any), // as FieldId
			value = editParams.getValue();

		const editItemEvents = this.sourceManager.editOriginItem(itemId, value, field, this.getId());

		editItemEvents.forEach((e) => this.addEvent(e));

		// TODO
		// this.calculateUniqueValues();

		this.calculateSource();

		return this.getEvents();
	}

	setHeight(height: number): void {

		this.verticalFormation.setViewportHeight(height);

		this.calculateSourceBasedOnVirtualScroll();
	}

	setTheme(theme: SchemaTheme): void {

		// #1707 Move to SchemeAggregate
		if (theme === SchemaTheme.MATERIAL) {
			this.getVerticalFormation().setRowHeight(52);
		}

		if (theme === SchemaTheme.GENERIC) {
			this.getVerticalFormation().setRowHeight(42);
		}

		if (theme === SchemaTheme.FABRIC) {
			this.getVerticalFormation().setRowHeight(36);
		}

		if (theme === SchemaTheme.LIGHT) {
			this.getVerticalFormation().setRowHeight(56);
		}

		if (theme === SchemaTheme.DARK) {
			this.getVerticalFormation().setRowHeight(38);
		}
	}

	setScrollPosition(position: number): void {
		this.verticalFormation.setScrollPosition(position);
		this.calculateSourceBasedOnVirtualScroll();
	}

	scrollToIndex(index: number): void {
		this.addEvent(
			this.verticalFormation.scrollToIndex(index)
		);
	}

	setSelection(enabled: boolean): void {

		this.sourceManager.getFormation().setSelection(enabled);

		this.addEvent(
			new SelectionEnabledSetAggregateEvent(this.getId(), enabled)
		);
	}

	setSelectionMode(mode: RowSelectionMode): void {
		this.addEvent(
			this.sourceManager.getFormation().setMode(mode)
		);
	}

	setSelectionType(type: RowSelectionType): void {
		this.addEvent(
			this.sourceManager.getFormation().setType(type)
		);
	}

	setFormationMatcher(matcher: (item: any) => any): void {
		this.getFormation().setMatcher(matcher);
		this.sourceManager.reSelect();
		this.generateSelectedRowChangedEvent();
	}

	selectByIndex(indexes: Array<number>): void {
		this.sourceManager.selectByIndex(indexes);
		this.generateSelectedRowChangedEvent();
	}

	selectByIds(ids: Array<string>): void {
		this.sourceManager.selectByIds(ids);
		this.generateSelectedRowChangedEvent();
	}

	/**
	 *
	 * Doesnt' work
	 */
	setSelectedRows(selectedRows: Array<string>): void {
		this.sourceManager.setSelectedRows(selectedRows);
		this.generateSelectedRowChangedEvent();
	}

	setSelectionCustomConfig(config: FormationCustomSelectionConfig): void {
		this.addEvent(
			this.sourceManager.setCustomConfig(config)
		);
	}

	toggleRow(selectedRow: string, type: RowSelectToggleType): void {

		this.sourceManager.toggleRow(selectedRow, type);

		this.generateSelectedRowChangedEvent();
	}

	selectCustom(id: FormationCustomSelectId): void {
		this.sourceManager.selectCustom(id);
		this.generateSelectedRowChangedEvent();
	}

	selectAll(): void {
		this.sourceManager.selectAll();
		this.generateSelectedRowChangedEvent();
	}

	unselectAll(): void {
		this.sourceManager.unselectAll();
		this.generateSelectedRowChangedEvent();
	}

	getFormation(): FormationManager {
		return this.sourceManager.getFormation();
	}

	getPaging(): PagingManager {
		return this.pagingManager;
	}

	changePaging(config: PagingConfig) {
		this.pagingManager.change(config);
		this.calculateSource();
	}

	setPaging(paging: PagingManager): void {
		this.pagingManager = paging;
		this.calculateSource();
	}

	nextPage(): void {

		this.pagingManager.nextPage();

		this.calculateSource();
	}

	prevPage(): void {
		this.pagingManager.prevPage();
		this.calculateSource();
	}

	changePageSize(pageSize: number): ReadonlyArray<AggregateEvent<StructureId>> {

		const pagingEvents = this.pagingManager.changePageSize(pageSize);

		// TODO
		pagingEvents.forEach((event) => {
			this.addEvent(event);
		});

		this.calculateSource();

		return this.getEvents();
	}

	getEntities(): Array<ItemEntity> {
		return this.sourceManager.getSlicedEntities();
	}

	getSource(): SourceManager {
		return this.sourceManager;
	}

	createFields(fieldConfigs: Array<FieldConfig>): ReadonlyArray<AggregateEvent<StructureId>> {

		this.fieldCollection.initFields(fieldConfigs);

		const fields = this.fieldCollection.getAllFields();

		this.addEvent(
			new FieldsInitedAggregateEvent(this.getId(), fieldConfigs, fields)
		);

		this.filterManager.assignFilterTypes(fields);

		this.addEvent(
			new FilterTypesInitedAggregateEvent(this.getId(), this.filterManager.getFilterTypes())
		);

		return this.getEvents();
	}

	setSortingConfig(config: SortingConfig): void {
		this.sorterManager.setConfig(config);
	}

	toggleSort(fieldId: FieldId): Array<Sorter> {

		const field = this.fieldCollection.getField(fieldId);

		this.sorterManager.toggle(field);

		this.calculateSource();

		return this.sorterManager.getAll();
	}

	setSortOrder(fieldId: FieldId, sortOrder: SortOrder): Array<Sorter> {

		const field = this.fieldCollection.getField(fieldId);

		this.sorterManager.setSortOrder(field, sortOrder);

		this.calculateSource();

		return this.sorterManager.getAll();
	}

	setFilterConfig(config: FilterConfig): void {
		this.filterManager.getSettings().setFilterConfig(config);
	}

	setQuickFiltersConfig(config: QuickFiltersConfig): void {
		this.filterManager.getSettings().setQuickFiltersConfig(config);
	}

	toggleFilter(fieldId: FieldId, externalFilterId: string, filterValue: string): Array<AggregateEvent<StructureId>> {

		const field = this.fieldCollection.getField(fieldId);

		// Field not found
		// TODO throw aggregate event
		if (field === undefined) {
			return [];
		}

		// this.filterManager.addFilter(field, externalFilterId, filterValue);

		// this.filterManager.a

		this.calculateSource();

		return [];
	}

	addFilter(fieldId: FieldId, filterTypeId: FilterTypeId, value: any): void {

		this.filterManager.add(fieldId, filterTypeId, value);

		const fields = this.fieldCollection.getFieldsAsMap();

		const activeFilters = this.filterManager.getAllActiveFilters(fields);

		this.addEvent(
			new FilterAddedAggregateEvent(
				this.getId(),
				activeFilters
			)
		);

		this.calculateSource();
	}

	removeAllFilters(): void {
		this.filterManager.removeAll();

		this.addEvent(
			new AllFiltersRemovedAggregateEvent(this.getId())
		);

		this.calculateSource();
	}

	removeFilter(filterId: FilterId): void {
		this.filterManager.remove(filterId);

		const fields = this.fieldCollection.getFieldsAsMap();
		const activeFilters = this.filterManager.getAllActiveFilters(fields);

		this.addEvent(
			new FilterRemovedAggregateEvent(this.getId(), activeFilters)
		);

		this.calculateSource();
	}

	setSearchingConfig(config: SearchConfig): void {
		this.filterManager.getSettings().setSearchingConfig(config);
	}

	addSearchPhrase(phrase: string): Array<AggregateEvent<StructureId>> {

		const fields = this.fieldCollection.getAllFields();

		this.searchManager.addSearchPhrase(fields, phrase);

		this.calculateSource();

		return [];
	}

	removeSearchPhrase(): Array<AggregateEvent<StructureId>> {

		this.searchManager.removeSearchFilters();

		this.calculateSource();

		return [];
	}

	selectAllUniqueFilter(fieldId: FieldId): void {

		this.uniqueFilterManager.selectAll(fieldId);

		this.addEvent(
			new AllUniqueFilterSelectedAggregateEvent(
				this.getId()
			)
		);

		this.generateCalculateUniqueValuesAggregateEvent();

		this.calculateSource();
	}

	selectUniqueFilter(fieldId: FieldId, uniqueValueId: UniqueValueId): void {

		this.uniqueFilterManager.select(fieldId, uniqueValueId);

		this.addEvent(
			new UniqueFilterSelectedAggregateEvent(
				this.getId()
			)
		);

		this.generateCalculateUniqueValuesAggregateEvent();

		this.calculateSource();
	}

	unselectAllUniqueFilter(fieldId: FieldId): void {

		this.uniqueFilterManager.unselectAll(fieldId);

		this.addEvent(
			new AllUniqueFilterUnselectedAggregateEvent(
				this.getId()
			)
		);

		this.generateCalculateUniqueValuesAggregateEvent();

		this.calculateSource();
	}

	unselectUniqueFilter(fieldId: FieldId, uniqueValueId: UniqueValueId): void {

		this.uniqueFilterManager.unselect(fieldId, uniqueValueId);

		this.addEvent(
			new UniqueFilterUnselectedAggregateEvent(
				this.getId()
			)
		);

		this.generateCalculateUniqueValuesAggregateEvent();

		this.calculateSource();
	}

	private calculateSource(): void {

		if (!this.sourceManager.getOrigin() || !this.pagingManager) {
			return;
		}

		// TODO Refactor
		this.sourceManager.setEntities(this.sourceManager.getOrigin());

		// filtering
		this.sourceManager.setEntities(
			this.filterManager.filter(this.sourceManager.getEntities(), this.fieldCollection.getFieldsAsMap())
		);

		// search
		const searchedEntities = this.searchManager.search(this.sourceManager.getEntities());
		this.sourceManager.setEntities(searchedEntities);

		// unique filtering
		// if (false) {
		// 	this.sourceManager.setEntities(
		// 		this.uniqueFilterManager.filterAll(this.sourceManager.getEntities(), this.fieldCollection.getAllFields())
		// 	);
		// }

		// sorting
		const ents = this.sorterManager.sort(this.sourceManager.getEntities());
		this.sourceManager.setEntities(ents);

		// calculate filteredEntities
		this.sourceManager.setPreparedEntities();

		this.pagingManager.setSourceSize(this.sourceManager.getPreparedEntities().length);

		this.addEvent(new StructurePreparedEntitiesSetAggregateEvent(this.getId(), ents));

		// summaries
		const summariesEvents = this.summariesManager.calculate(
			this.fieldCollection.getAllFields(),
			this.sourceManager.getEntities()
		);

		summariesEvents.forEach((ae: AggregateEvent<StructureId>) => {
			this.addEvent(ae);
		});

		// paging
		this.sourceManager.setEntities(this.pagingManager.sample(this.sourceManager.getEntities()));

		this.verticalFormation.setSourceSize(this.sourceManager.getEntities().length);

		this.calculateSourceBasedOnVirtualScroll();
	}

	private calculateSourceBasedOnVirtualScroll(): void {

		if (this.verticalFormation.isEnabled()) {

			const range = this.verticalFormation.getRange();

			this.sourceManager.setSlicedEntities(
				this.sourceManager.getEntities().slice(range.getStart(), range.getEnd())
			);
		} else {
			this.sourceManager.setSlicedEntities(
				this.sourceManager.getEntities()
			);
		}
	}

	private initTheme() {

		// TODO
		const theme = SchemaAggregate.DEFAULT_THEME;

		this.setTheme(theme);
	}

	private calculateUniqueValues(): void {

		const fields = this.fieldCollection.getAllFields(),
			items = this.sourceManager.getOrigin();

		this.uniqueFilterManager.calculateAll(items, fields);

		this.generateCalculateUniqueValuesAggregateEvent();
	}

	private generateCalculateUniqueValuesAggregateEvent(): void {
		this.addEvent(
			new UniqueFilterCalculatedAggregateEvent(
				this.getId(),
				this.uniqueFilterManager.getAll(
					this.fieldCollection.getAllFields()
				)
			)
		);
	}

	private generateSelectedRowChangedEvent() {
		this.addEvent(
			new SelectedRowChangedAggregateEvent(
				this.getId(),
				this.sourceManager.getFormation().getSelectedItemIds().map(i => i.toString()),
				this.sourceManager.getFormation().isAllSelected(),
				this.sourceManager.getFormation().isAllUnselected()
			)
		);
	}

}
