import { SourceManager } from './source.manager';
import { FormationManagerFactory } from '../../../formation/src/domain/formation.manager-factory';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';


export class SourceManagerFactory {

	constructor(private readonly formationManagerFactory: FormationManagerFactory) {
	}

	static readonly services = [FormationManagerFactory];

	createDefault(id: StructureId): SourceManager {

		const fm = this.formationManagerFactory.create(id);

		return new SourceManager(fm);
	}

	create(id: StructureId): SourceManager {
		const fm = this.formationManagerFactory.create(id);

		return new SourceManager(fm);
	}

}
