import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { FieldEntity } from '../../domain/field/field.entity';
import { FieldConfig } from '../../api/config/field.config';
import { StructureDomainEvent } from '../../../../structure-core/src/core/structure.domain-event';


export class FieldsInitedEvent extends StructureDomainEvent {

	constructor(aggregateId: StructureId,
				public readonly fieldConfigs: Array<FieldConfig>, // TODO merge
				private readonly fields: Array<FieldEntity>) {
		super(aggregateId, { fieldConfigs, fields }, 'FieldsInitedEvent');
	}

	getFields(): Array<FieldEntity> {
		return this.fields;
	}
}
