import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SummariesEnabledArchive } from './summaries-enabled-archive.service';
import { StructureSummariesEnabledSetEvent } from '../../core/set/structure.summaries-enabled-set.event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class StructureSummariesEnabledSetEventHandler implements DomainEventHandler<StructureId, StructureSummariesEnabledSetEvent> {

	constructor(private readonly summariesEnabledArchive: SummariesEnabledArchive) {
	}

	static readonly services = [SummariesEnabledArchive];

	forEvent(): DomainEventType<StructureSummariesEnabledSetEvent> {
		return StructureSummariesEnabledSetEvent;
	}

	handle(event: StructureSummariesEnabledSetEvent): void {

		if (event.ofMessageType('StructureSummariesEnabledSetEvent')) {
			this.summariesEnabledArchive.next(event.getAggregateId(), event.isEnabled());
		}
	}

}
