import { SummariesValues } from './summaries.values';
import { DataType } from '../../../../field/src/domain/field/data/data-type';
import { ItemEntity } from '../../../../source/src/domain/item/item.entity';
import { SummariesType } from '../summaries.type';
import { FieldEntity } from '../../../../field/src/domain/field/field.entity';


export abstract class SummariesCalculator<T, A extends SummariesValues> {

	abstract forDataType(dataType: DataType): boolean;

	abstract prepare(field: FieldEntity): void;

	abstract postCalculate(field: FieldEntity, items: Array<ItemEntity>): void;

	abstract aggregate(field: FieldEntity, value: T): void;

	abstract generateAggregatedValues(field: FieldEntity): A;

	calculate(fields: Array<FieldEntity>, items: Array<ItemEntity>): Map<string, A> {

		const filteredFields = fields.filter((field: FieldEntity) => this.forDataType(field.getDataType()));

		if (!filteredFields || filteredFields.length === 0 || items.length === 0) {
			return null;
		}

		const count = new Map<string, number>(),
			distinct = new Map<string, Set<string>>();

		// init
		filteredFields.forEach((field: FieldEntity) => {

			const key = field.getKey();

			count.set(key, 0);
			distinct.set(key, new Set());

			this.prepare(field);
		});

		// calculate
		items.forEach((item: ItemEntity) => {

			filteredFields.forEach((field: FieldEntity) => {

				const key = field.getKey(),
					value = field.getValue(item);

				if (value !== null || value !== undefined || value !== '') {

					if (field.isSummaries(SummariesType.COUNT)) {
						const countForField = count.get(key);
						count.set(key, countForField + 1);
					}

					if (field.isSummaries(SummariesType.DISTINCT)) {
						const distinctSet = distinct.get(key);
						distinctSet.add(value);
					}
				}

				this.aggregate(field, value);
			});
		});

		filteredFields.forEach((field: FieldEntity) => {

			this.postCalculate(field, items);

		});

		const summaries = new Map();

		filteredFields.forEach((field: FieldEntity) => {

			const key = field.getKey();

			const aggregatedValues = this.generateAggregatedValues(field);

			if (field.isSummaries(SummariesType.COUNT)) {
				aggregatedValues.setCount(count.get(key));
			}

			if (field.isSummaries(SummariesType.DISTINCT)) {
				aggregatedValues.setDistinct((distinct.get(key)).size);
			}

			summaries.set(key, aggregatedValues);
		});

		return summaries;
	}

}
