import { ChangeDetectionStrategy, Component, ElementRef, Inject, OnInit, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

import { structureRowDetailViewItem, structureRowDetailViewTemplate } from './structure.row-detail-view.tokens';
import { SelectedRow } from '../../../../../core/structure/formation/src/api/row-selected/selected-row';
import { DynamicallyCreatedComponent } from '../../../../../feature/common/src/cdk/component/lib/src/dynamically-created-component';
import { ItemEntity } from '../../../../../core/structure/source/src/domain/item/item.entity';


@Component({
	selector: 'div[gui-row-detail]',
	templateUrl: './structure.row-detail-view.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureRowDetailViewComponent extends DynamicallyCreatedComponent implements OnInit {

	selectedRowValue: ItemEntity;

	safeHTML: SafeHtml;

	constructor(elRef: ElementRef,
				@Inject(structureRowDetailViewItem) private readonly item: SelectedRow,
				@Inject(structureRowDetailViewTemplate) private readonly template: any,
				private readonly sanitizer: DomSanitizer) {
		super(elRef);

		this.safeHTML = this.sanitizer.bypassSecurityTrustHtml(this.template(this.item.getItem(), this.item.getIndex()));
	}

	ngOnInit(): void {
		this.selectedRowValue = this.item.getItem();
	}

	protected getSelectorName(): string {
		return 'gui-row-detail';
	}

}
