import { EventRepository } from '@generic-ui/hermes';

import { ListViewId } from '../core/list-view.id';
import { ListViewReadModelRootId } from './global/list-view.read-model-root-id';
import { ListViewModeSetEvent } from '../core/mode/list-view-mode-set.event';


export class ListViewEventRepository extends EventRepository<ListViewReadModelRootId> {

	constructor() {
		super();
	}

	onModeChange(listViewReadModelRootId: ListViewReadModelRootId) {

		return this.onEvent(listViewReadModelRootId, ListViewModeSetEvent);
	}


}
