import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetPagingCommand } from './set-paging.command';
import { PagingSetEvent } from './paging-set.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SetPagingCommandHandler implements CommandHandler<StructureAggregate, SetPagingCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);


	forCommand(): CommandType<SetPagingCommand> {
		return SetPagingCommand;
	}

	handle(structureAggregate: StructureAggregate, command: SetPagingCommand): void {

		const pagingConfig = command.getPagingConfig();

		structureAggregate.changePaging(pagingConfig);
	}

	publish(aggregate: StructureAggregate, command: SetPagingCommand): void {
		this.domainEventPublisher.publish(new PagingSetEvent(command.getAggregateId()));
	}

}
