import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewEncapsulation } from '@angular/core';
import { FieldModel } from '../../../../../../core/structure/field/src/api/model/field.model';
import { PureComponent } from '../../../../../common/src/cdk/component/lib/src/pure-component';
import { NgChanges } from '../../../../../common/src/cdk/component/lib/src/ng-changes';

import { GuiSelectOption } from '@generic-ui/fabric';
import { ifChanged } from '../../../../../common/src/cdk/component/lib/src/check.input';

@Component({
	selector: 'div[gui-field-selector][fields]',
	template: `
		<gui-select (optionChanged)="onFieldSelected($event)"
					[options]="fieldsAsOptions"
					[placeholder]="'Select column'">
		</gui-select>
	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FieldSelectorComponent extends PureComponent implements OnChanges {

	@Input()
	fields: ReadonlyArray<FieldModel>;

	@Output()
	fieldSelected = new EventEmitter<FieldModel>();

	fieldsAsOptions: Array<GuiSelectOption>;

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	ngOnChanges(changes: NgChanges<FieldSelectorComponent>) {

		ifChanged(changes.fields, () => {
			this.fieldsAsOptions = this.fields.map((field) => {
				return {
					name: field.getFieldId().toString(),
					value: field.getName()
				};
			});
		});
	}

	onFieldSelected(fieldId: GuiSelectOption): void {
		const field = this.fields.find((field: FieldModel) => field.getFieldId().toString() === fieldId.name);

		this.fieldSelected.emit(field);
	}

	protected getSelectorName(): string {
		return 'gui-field-selector';
	}
}
