import { ClassModifier } from '../../../../common/src/cdk/component/lib/src/dom/class/class-modifier';
import { CssClass } from './css-class';


export class CssClassModifier {

	private readonly classModifier: ClassModifier = new ClassModifier();

	select(htmlElement: HTMLElement): void {
		this.classModifier.getElement(htmlElement).add(CssClass.SELECTED);
	}

	unselect(htmlElement: HTMLElement): void {
		this.classModifier.getElement(htmlElement).remove(CssClass.SELECTED);
	}

	add(htmlElement: HTMLElement, clazzName: string): void {
		this.classModifier.getElement(htmlElement).add(clazzName);
	}

	remove(htmlElement: HTMLElement, clazzName: string): void {
		this.classModifier.getElement(htmlElement).remove(clazzName);
	}

	toggle(htmlElement: HTMLElement): void {
	}

}
