import { FilterTypeModel } from './filter-type.model';
import { FieldId } from '../../../../field/src/domain/field/field.id';

export class FilterTypeCollectionModel {

	private readonly map: Map<string, Array<FilterTypeModel>>; // = new WeakMap<FieldId, Array<FilterTypeReadModel>>();

	constructor(map: Map<string, Array<FilterTypeModel>>) {
		this.map = map;
	}

	getFilterTypes(fieldId: FieldId): Array<FilterTypeModel> {
		const result = this.map.get(fieldId.toString());

		if (result === undefined) {
			return [];
		} else {
			return result;
		}
	}

}
