import { AggregateId, DomainEvent } from '@generic-ui/hermes';

import { ItemEntity } from '../../domain/item/item.entity';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { OriginSetEvent } from './set/origin-set.event';
import { StructureAggregateEvent } from '../../../../structure-core/src/core/structure.aggregate-event';


export class StructureOriginChangedAggregateEvent extends StructureAggregateEvent {

	constructor(aggregateId: AggregateId,
				private readonly origin: Array<ItemEntity>) {
		super(aggregateId, 'StructureOriginChangedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new OriginSetEvent(this.getAggregateId(), this.origin);
	}

	getOrigin(): Array<ItemEntity> {
		return this.origin;
	}
}
