import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Inject, Injector, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';

import { StructurePublisher } from '../../../../../../core/structure/structure-core/src/api/structure.publisher';
import { CellTemplateWithContext } from '../../../../../../core/composition/src/core-read/definition/cell-template-with-context';
import { SmartComponent } from '../../../../../../feature/common/src/cdk/component/lib/src/smart-component';
import { CompositionWarehouse } from '../../../../../../core/composition/src/api/composition.warehouse';

import { StructureColumnMenuConfigArchive } from './structure.column-menu-config.archive';
import { StructureColumnMenuConfig } from './structure.column-menu-config';
import { FilterWarehouse } from '../../../../../../core/structure/filter/src/api/filter.warehouse';
import { StructureId } from '../../../../../../core/structure/structure-core/src/api/global/structure.id';
import { UniqueValueCollectionModel } from '../../../../../../core/structure/filter/src/api/unique/unique-value-collection.model';
import { TranslationFacade } from '../../../../../../core/l10n/src/api/translation.facade';
import { Translation } from '../../../../../../core/l10n/src/api/translation';
import { hermesSwitchMap } from '@generic-ui/hermes';
import { CompositionId } from '../../../../../../core/composition/src/api/global/composition.id';
import { CompositionPublisher } from '../../../../../../core/composition/src/api/composition.publisher';
import { StructureColumnConfigService } from '../structure.column-config.service';


@Component({
	selector: 'div[gui-column-config]',
	templateUrl: './structure.column-config.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureColumnConfigComponent extends SmartComponent implements OnInit {

	@ViewChild('headerSortMenu', { read: ElementRef, static: false })
	readonly headerSortMenu?: ElementRef;

	config: StructureColumnMenuConfig;

	uniqueValues: Array<any> = [];

	hideColumnTitle: string;

	dropdownTextTranslation: string;

	private readonly structureColumnConfigService: StructureColumnConfigService;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly structureCommandService: StructurePublisher,
				private readonly compositionReadModelService: CompositionWarehouse,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly translationFacade: TranslationFacade,
				private readonly structureColumnMenuConfigArchive: StructureColumnMenuConfigArchive,
				private readonly compositionId: CompositionId,
				private readonly compositionCommandInvoker: CompositionPublisher,
				private readonly injector: Injector,
				@Inject('column') public readonly column: CellTemplateWithContext) {
		super(changeDetectorRef, elementRef);

		this.structureColumnConfigService = this.injector.get(StructureColumnConfigService);
	}

	ngOnInit() {

		this.subscribe(
			this.filterWarehouse.onUniqueValues(this.structureId),
			(uniqueValuesReadModel: UniqueValueCollectionModel) => {
				this.uniqueValues = uniqueValuesReadModel.getValues(this.column.getFieldId());
			}
		);

		this.subscribe(
			this.structureColumnMenuConfigArchive
				.on()
				.pipe(
					hermesSwitchMap((config: StructureColumnMenuConfig) => {
						this.config = config;
						return this.translationFacade.onTranslation();
					})
				),
			(translation: Translation) => {
				this.setTabTitles(translation);
				this.dropdownTextTranslation = translation.headerMenuMainTabColumnSort;
			}
		);
	}

	isEnabled(): boolean {
		return this.config && this.config.isEnabled();
	}

	hideColumn(): void {
		this.compositionCommandInvoker.disableColumn(this.column.getColumnDefinitionId(), this.compositionId);

		this.structureColumnConfigService.close();
	}

	moveLeft(): void {
		this.compositionCommandInvoker.moveLeft(this.column.getColumnDefinitionId(), this.compositionId);
		this.structureColumnConfigService.close();
	}

	moveRight(): void {
		this.compositionCommandInvoker.moveRight(this.column.getColumnDefinitionId(), this.compositionId);
		this.structureColumnConfigService.close();
	}

	highlightColumn(): void {
		this.compositionCommandInvoker.highlightColumn(this.column.getColumnDefinitionId(), this.compositionId);
		this.structureColumnConfigService.close();
	}

	protected getSelectorName(): string {
		return 'gui-column-config';
	}

	private setTabTitles(translation: Translation): void {
		this.config.setMainMenu(translation.headerMenuMainTab);
		this.config.setFilterMenu(translation.headerMenuFilterTab);
		this.config.setColumnsMenu(translation.headerMenuColumnsTab);
	}
}
