import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { SortingPublisher } from './sorting.publisher';
import { SortingDomainModule } from '../core/sorting.domain-module';
import { SortingDomainPublisher } from '../core/sorting.domain-publisher';


export class SortingApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(SortingPublisher, SortingDomainPublisher);
	}
}

export function initializeSorting(): void {
	new DomainInitializer(new SortingApiModule(), new SortingDomainModule()).init();
}

