import { DomainEventBus, EventRepository, hermesFilter, hermesMap, HermesObservable, HermesSubject, hermesSwitchMap, hermesTake } from '@generic-ui/hermes';

import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { SourceWarehouse } from '../../../source/src/api/source.warehouse';
import { SelectedRow } from './row-selected/selected-row';
import { SelectedRowChangedEvent } from '../core/selected-row-changed.event';
import { ItemEntity } from '../../../source/src/domain/item/item.entity';
import { StructureReadModelRootId } from '../../../structure-core/src/api/read/structure.read-model-root-id';


export class FormationEventRepository extends EventRepository<StructureReadModelRootId> {

	private readonly map = new Map();

	private readonly subject$ = new HermesSubject();

	constructor(private readonly sourceWarehouse: SourceWarehouse,
				private readonly domainEventBusTOREMOVE: DomainEventBus) {
		super();

		this.domainEventBusTOREMOVE
			.ofEvents([
					SelectedRowChangedEvent as any
				]
			)
			.pipe(
				hermesSwitchMap((event: SelectedRowChangedEvent) => {

					const selectedRows = event.getSelectedRows();
					const structureId = event.getAggregateId();

					return this.sourceWarehouse
							   .onPreparedItems(structureId)
							   .pipe(
								   hermesTake(1),
								   hermesMap((items: Array<ItemEntity>) => {

									   const selectedItems = [],
										   length = items.length,
										   itemsMap = new Map<string, ItemEntity>();

									   for (let i = 0; i < length; i += 1) {
										   itemsMap.set(items[i].getId().toString(), items[i]);
									   }

									   for (let j = 0; j < selectedRows.length; j += 1) {
										   const item: ItemEntity = itemsMap.get(selectedRows[j]);
										   selectedItems.push(new SelectedRow(item.getSourceItem(), j, item.getId()));
									   }

									   return {
										   id: structureId,
										   items: selectedItems
									   };
								   })
							   );
				})
			)
			.subscribe((e) => {
				this.map.set(e.id.toString(), e.items);
				this.subject$.next(this.map);
			});
	}

	static readonly services = [SourceWarehouse, DomainEventBus] as const;

	onItemSelected(structureId: StructureId): HermesObservable<ReadonlyArray<SelectedRow>> {
		return this.subject$
				   .toObservable()
				   .pipe(
					   hermesFilter((map: any) => {
						   return map.has(structureId.toString());
					   }),
					   hermesMap((map: any) => {
						   return map.get(structureId.toString());
					   })
				   );
	}

}
