import { ChangeDetectionStrategy, Component, ElementRef, EventEmitter, Input, OnChanges, Output, ViewEncapsulation } from '@angular/core';
import { FilterTypeModel } from '../../../../../../core/structure/filter/src/api/type/filter-type.model';
import { FilterTypeId } from '../../../../../../core/structure/filter/src/domain/type/filter-type.id';
import { PureComponent } from '../../../../../common/src/cdk/component/lib/src/pure-component';

import { GuiSelectOption } from '@generic-ui/fabric';
import { NgChanges } from '../../../../../common/src/cdk/component/lib/src/ng-changes';


@Component({
	selector: 'div[gui-filter-type-selector][filterTypes]',
	templateUrl: './filter-type-selector.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class FilterTypeSelectorComponent extends PureComponent implements OnChanges {

	@Input()
	filterTypes: ReadonlyArray<FilterTypeModel>;

	@Output()
	filterTypeSelected = new EventEmitter<FilterTypeId>();

	filterTypesAsOptions: ReadonlyArray<GuiSelectOption> = [];

	disabled = true;

	constructor(elementRef: ElementRef) {
		super(elementRef);
	}

	ngOnChanges(changes: NgChanges<FilterTypeSelectorComponent>) {

		this.ifChanged(changes.filterTypes, () => {

			this.filterTypesAsOptions = this.filterTypes.map((filter) => {
				return {
					name: filter.getId().toString(),
					value: filter.getName()
				};
			});

			this.disabled = this.filterTypesAsOptions.length === 0;
		});
	}

	onSelectChange(option: GuiSelectOption): void {

		const filterType = this.filterTypes.find((filterType: FilterTypeModel) => {
			return filterType.getId().toString() === option.name;
		});

		this.filterTypeSelected.emit(filterType.getId());
	}

	protected getSelectorName(): string {
		return 'gui-filter-type-selector';
	}

}
