import { AggregateArchive } from '@generic-ui/hermes';
import { ColumnHighlightManager } from './column-highlight.manager';
import { CompositionId } from '../../api/global/composition.id';
import { ColumnId } from '../../api/column/column.id';


export class ColumnHighlightArchive extends AggregateArchive<ColumnHighlightManager> {

	constructor() {
		super(new ColumnHighlightManager());
	}

	toggle(key: CompositionId, value: ColumnId) {
		this.find(key)
			.ifPresent((manager) => {
				manager.toggle(value);
				this.next(key, manager);
			});
	}

	protected override equals(a: ColumnHighlightManager, b: ColumnHighlightManager): boolean {
		return false;
	}

	protected override createDefaultValue(defaultValue: ColumnHighlightManager): ColumnHighlightManager {
		return new ColumnHighlightManager();
	}

}
