import { AggregateEvent, CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { CompositionEventConverter } from '../../composition.event-converter';
import { CompositionAggregate } from '../../../domain/composition.aggregate';

import { SetColumnsCommand } from './set-columns.command';
import { ColumnParams } from './column.params';
import { CompositionId } from '../../../api/global/composition.id';


export class SetColumnsCommandHandler implements CommandHandler<CompositionAggregate, SetColumnsCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	constructor(private readonly compositionEventConverter: CompositionEventConverter) {
	}

	static readonly services = [CompositionEventConverter] as const;

	forCommand(): CommandType<SetColumnsCommand> {
		return SetColumnsCommand;
	}

	handle(aggregate: CompositionAggregate, command: SetColumnsCommand): void {

		const setColumnsParams: Array<ColumnParams> = command.getParams();

		aggregate.setColumns(setColumnsParams);
	}

	publish(aggregate: CompositionAggregate, command: SetColumnsCommand): void {
		this.publishEvents(aggregate, command);
	}

	private publishEvents(composition: CompositionAggregate, command: SetColumnsCommand): void {

		const events = composition.getEvents() as Array<AggregateEvent<CompositionId>>;

		const domainEvents = this.compositionEventConverter.convert(events);

		this.domainEventPublisher.publish(domainEvents);
	}
}
