import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../../structure-core/src/api/global/structure.id';
import { UniqueFilterCalculatedEvent } from './unique-filter-calculated.event';
import { UniqueValue } from '../../../domain/unique/unique-value';
import { StructureAggregateEvent } from '../../../../../structure-core/src/core/structure.aggregate-event';


export class UniqueFilterCalculatedAggregateEvent extends StructureAggregateEvent {

	constructor(aggregateId: StructureId,
				private readonly map: Map<string, Array<UniqueValue>>) {
		super(aggregateId, 'UniqueFilterCalculatedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new UniqueFilterCalculatedEvent(
			this.getAggregateId(),
			this.map
		);
	}

}
