import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnChanges, ViewChild, ViewEncapsulation } from '@angular/core';
import { ChangedValueEmitter } from '../../../../../../feature/composition/src/column/edit/changed-value.emitter';
import { EditEventType } from '../../../../../../feature/composition/src/column/edit/state/edit.event-type';
import { Item } from '../../../../../../core/structure/source/src/api/item/item';
import { SmartComponent } from '../../../../../../feature/common/src/cdk/component/lib/src/smart-component';
import { StructureEditSourceItemParams } from '../../../../../../core/structure/source/src/core/origin/edit/structure.edit-source-item.params';
import { SourcePublisher } from '../../../../../../core/structure/source/src/api/source.publisher';
import { StructureCellEditState } from '../../../edit/structure.cell-edit.state';
import { NgChanges } from '../../../../../../feature/common/src/cdk/component/lib/src/ng-changes';
import { StructureId } from '../../../../../../core/structure/structure-core/src/api/global/structure.id';
import { ifChanged } from '../../../../../../feature/common/src/cdk/component/lib/src/check.input';
import { CompositionAccessorModel } from '../../../../../../feature/composition/src/column/composition.accessor.model';

@Component({
	selector: 'div[gui-structure-cell-edit][entity][cell]',
	template: `

		<span #cellContainer>
			<ng-container
				*ngTemplateOutlet="cell.editTemplate;
									context: editContext">
			</ng-container>
		</span>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureCellEditComponent extends SmartComponent implements OnChanges {

	@ViewChild('cellContainer', { read: ElementRef, static: true })
	readonly cellContainerRef?: ElementRef;

	@Input()
	entity: Item;

	@Input()
	cell: CompositionAccessorModel;

	editContext: any;

	valueChanges$: ChangedValueEmitter<any>;

	status$: ChangedValueEmitter<any>;

	actualValue: any;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly sourceCommandService: SourcePublisher) {
		super(changeDetectorRef, elementRef);
	}

	ngOnChanges(changes: NgChanges<StructureCellEditComponent>) {

		ifChanged(changes.entity, () => {
			this.initEditContext();
		});

		ifChanged(changes.cell, () => {
			this.initEditContext();
		});

	}

	ngOnInit() {
		this.initEditContext();
	}

	submitChangesAndExit(): void {

		const itemId = this.entity.getId(),
			updatedValue = this.actualValue,
			fieldId = this.cell.columnFieldId;

		this.sourceCommandService.editItem(
			new StructureEditSourceItemParams(itemId, fieldId, updatedValue),
			this.structureId
		);
	}

	protected getSelectorName(): string {
		return 'gui-structure-cell-edit';
	}

	private initEditContext(): void {
		this.valueChanges$ = new ChangedValueEmitter();
		this.status$ = new ChangedValueEmitter<EditEventType>();

		this.editContext = {
			status: this.status$,
			valueChanges: this.valueChanges$,
			value: this.cell.getValue(this.entity),
			focus: false,
			parent: this.cellContainerRef
		};

		this.status$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((status: EditEventType) => {

				switch (status) {
					case EditEventType.SUBMIT:
						this.submitChangesAndExit();
						this.publishEditSubmit();
						break;

					case EditEventType.CANCEL:
						// this.exitEditMode();
						this.publishEditCancel();
						break;

					default:
						break;
				}
			});
	}

	private observeValueChanges(): void {

		this.valueChanges$
			.on()
			.pipe(
				this.takeUntil()
			)
			.subscribe((value: any) => {
				this.actualValue = value;
			});
	}

	private publishEditState(state: StructureCellEditState): void {
		// this.structureCellEditStore.next(state);
	}

	private publishEditEnter(): void {
		this.publishEditState(StructureCellEditState.ENTER);
	}

	private publishEditCancel(): void {
		this.publishEditState(StructureCellEditState.CANCEL);
	}

	private publishEditSubmit(): void {
		this.publishEditState(StructureCellEditState.SUBMIT);
	}

}
