import { AggregateId, DomainEvent } from '@generic-ui/hermes';

import { ItemEntity } from '../../domain/item/item.entity';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { StructurePreparedEntitiesSetEvent } from './structure.prepared-entities-set.event';
import { StructureAggregateEvent } from '../../../../structure-core/src/core/structure.aggregate-event';


export class StructurePreparedEntitiesSetAggregateEvent extends StructureAggregateEvent {

	constructor(aggregateId: AggregateId,
				private readonly preparedItems: Array<ItemEntity>) {
		super(aggregateId, 'StructurePreparedEntitiesSetAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {
		return new StructurePreparedEntitiesSetEvent(this.getAggregateId(), this.preparedItems);
	}

	getPreparedItems(): Array<ItemEntity> {
		return this.preparedItems;
	}

}
