import { AggregateEvent, RandomStringGenerator } from '@generic-ui/hermes';

import { ItemEntity } from './item/item.entity';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { StructureSourceItemEditedAggregateEvent } from '../core/origin/edit/structure.source-item-edited.aggregate-event';
import { StructureOriginChangedAggregateEvent } from '../core/origin/structure.origin-changed.aggregate-event';
import { FieldEntity } from '../../../field/src/domain/field/field.entity';
import { ItemEntityId } from './item/item.entity-id';
import { FormationManager } from '../../../formation/src/domain/formation.manager';
import { RowSelectToggleType } from '../../../formation/src/domain/row-select-toggle-type';
import { RowSelectionMode, RowSelectionType } from '../../../formation/src/api/row-selected/row-selection';
import { FormationCustomSelectId } from '../../../formation/src/api/custom/formation.custom-select.id';
import { FormationCustomSelectionConfig } from '../../../formation/src/api/custom/formation.custom-selection.config';
import { StructureAggregateEvent } from '../../../structure-core/src/core/structure.aggregate-event';


export class SourceManager {

	events: Array<any> = [];

	private loading: boolean = false;

	private fetched: boolean = false;

	private origin: Array<ItemEntity>;

	private entities: Array<ItemEntity> = [];

	// Entities after filtering, searching
	private preparedEntities: Array<ItemEntity> = [];

	private slicedEntities: Array<ItemEntity> = [];

	private readonly formationManager: FormationManager;

	constructor(formationManager: FormationManager) {
		this.formationManager = formationManager;
		this.events.push(
			...this.formationManager.init(true, RowSelectionMode.SINGLE, RowSelectionType.ROW)
		);
	}

	isLoading(): boolean {
		return this.loading;
	}

	setLoading(enabled: boolean): void {
		this.loading = enabled;
	}

	setEntities(ent: Array<ItemEntity>): void {
		this.entities = ent;
		this.recalculatePositions();
	}

	getEntities(): Array<ItemEntity> {
		return this.entities;
	}

	setPreparedEntities(): void {
		this.preparedEntities = Array.from(this.entities);
	}

	getPreparedEntities(): Array<ItemEntity> {
		return this.preparedEntities;
	}

	setSlicedEntities(ent: Array<ItemEntity>) {
		this.slicedEntities = ent;
	}

	getSlicedEntities(): Array<ItemEntity> {
		return this.slicedEntities;
	}

	setOrigin(items: Array<any> = [], structureId: StructureId): AggregateEvent<StructureId> {
		this.origin = this.convertItems(items);

		return this.createOriginChangedEvent(structureId);
	}

	setConvertedOrigin(items: Array<ItemEntity> = [], structureId: StructureId): AggregateEvent<StructureId> {
		this.origin = Array.from(items);

		return this.createOriginChangedEvent(structureId);
	}

	getOrigin(): Array<ItemEntity> {
		return this.origin;
	}

	editOriginItem(itemId: string, value: any, field: FieldEntity, structureId: StructureId): ReadonlyArray<AggregateEvent<StructureId>> {

		const item: ItemEntity = this.findOriginItem(itemId),
			itemBeforeChange = item.clone();

		if (item) {
			item.sourceItem[field.getAccessor()] = value;
			item.bumpVersion();
		}

		return [
			new StructureSourceItemEditedAggregateEvent(structureId, itemBeforeChange, item.clone()),
			this.createOriginChangedEvent(structureId)
		];
	}

	deleteAllSelected(structureId: StructureId): Array<AggregateEvent<StructureId>> {
		let events: Array<AggregateEvent<StructureId>> = [];

		const itemEntityIds = this.formationManager.getSelectedItemIds();

		itemEntityIds.forEach((itemEntityId: ItemEntityId) => {
			events = events.concat(this.deleteOriginItemByItemId(itemEntityId, structureId));
		});

		return events;
	}

	deleteOriginItem(payload: number | ItemEntityId | Array<number> | Array<ItemEntityId>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (payload instanceof ItemEntityId) {
			return this.deleteOriginItemByItemId(payload, structureId);
		} else if (Array.isArray(payload)) {

			let events: Array<AggregateEvent<StructureId>> = [];

			payload.forEach((item: number | ItemEntityId) => {
				events = events.concat(this.deleteOneOriginItem(item, structureId));
			});

			return events;

		} else if (Number.isInteger(payload)) {
			return this.deleteOriginItemByIndex(payload, structureId);
		} else {
			return [];
		}
	}

	addOriginItem(item: any, structureId: StructureId): ReadonlyArray<StructureAggregateEvent> {
		this.origin.push(item);
		return [this.createOriginChangedEvent(structureId)];
	}

	setCustomConfig(config: FormationCustomSelectionConfig): Array<StructureAggregateEvent> {
		return this.formationManager.setCustomConfig(config);
	}

	selectCustom(id: FormationCustomSelectId): void {
		this.formationManager.selectCustom(id, this.getEntities());
	}

	selectAll(): void {
		this.formationManager.selectAll(this.getEntities().map(entity => entity.getId()));
	}

	unselectAll(): void {
		this.formationManager.unselectAll();
	}

	selectByIndex(indexes: Array<number>): void {
		this.formationManager.selectByIndex(indexes, this.getEntities().map(e => e.getId()));
	}

	selectByIds(ids: Array<string>): void {
		this.formationManager.selectByIds(ids, this.getEntities());
	}

	reSelect(): void {
		this.formationManager.reSelectByIds(this.getEntities());
	}

	setSelectedRows(selectedRows: Array<string>): void {
		this.formationManager.selectRows(selectedRows, this.getEntities().map(e => e.getId()));
	}

	toggleRow(itemId: string, type: RowSelectToggleType): void {
		this.formationManager.toggleRow(itemId, type, this.getEntities().map(e => e.getId()));
	}

	getFormation(): FormationManager {
		return this.formationManager;
	}

	private convertItems(items: Array<any>): Array<ItemEntity> {
		return items.map((item: any, index: number) => {
			return new ItemEntity(new ItemEntityId(RandomStringGenerator.generate()), item, index);
		});
	}

	private createOriginChangedEvent(structureId: StructureId): AggregateEvent<StructureId> {
		return new StructureOriginChangedAggregateEvent(structureId, this.origin);
	}

	private findOriginItem(itemId: string): ItemEntity {
		return this.origin.find((item: ItemEntity) => item.getId().toString() === itemId);
	}

	private findOriginItemIndex(itemId: string): number {
		return this.origin.findIndex((item: ItemEntity) => item.getId().toString() === itemId);
	}

	private recalculatePositions(): void {
		this.entities.forEach((ent: ItemEntity, index: number) => {
			ent.setPosition(index);
		});
	}

	private deleteOneOriginItem(item: number | ItemEntityId, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (item instanceof ItemEntityId) {
			return this.deleteOriginItemByItemId(item, structureId);
		} else if (Number.isInteger(item)) {
			return this.deleteOriginItemByIndex(item, structureId);
		} else {
			return [];
		}
	}

	private deleteOriginItemByItemId(itemId: ItemEntityId, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		const index = this.findOriginItemIndex(itemId.toString());

		return this.deleteOriginItemByIndex(index, structureId);
	}

	private deleteOriginItemByIndex(index: number, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		if (index > -1) {
			const removedItems: Array<ItemEntity> = this.origin.splice(index, 1);

			if (removedItems.length > 0) {
				for (const element of removedItems) {
					this.formationManager.unselectRow(element.getId());
				}
				this.formationManager.calculateAllSelected(removedItems.map((f) => f.getId()));
				this.formationManager.calculateAllUnselected();
			}

			return [
				this.createOriginChangedEvent(structureId)
			];
		}

		return [];
	}

	private deleteManyOriginItemByIndex(indexes: Array<number>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		let events: Array<AggregateEvent<StructureId>> = [];

		indexes.forEach((index: number) => {
			events = events.concat(this.deleteOriginItemByIndex(index, structureId));
		});

		return events;
	}

	private deleteManyOriginItemByItemID(itemIds: Array<ItemEntityId>, structureId: StructureId): Array<AggregateEvent<StructureId>> {

		let events: Array<AggregateEvent<StructureId>> = [];

		itemIds.forEach((itemId: ItemEntityId) => {
			events = events.concat(this.deleteOriginItemByItemId(itemId, structureId));
		});

		return events;
	}

}
