import { CommandDispatcher, CoreContainer } from '@generic-ui/hermes';

import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { StructureSetSummariesEnabledCommand } from './set/structure.set-summaries-enabled.command';


export class SummariesDispatcher {

	private readonly commandDispatcher: CommandDispatcher = CoreContainer.resolve(CommandDispatcher);


	setSummariesEnabled(enabled: boolean, structureId: StructureId): void {
		this.commandDispatcher.dispatch(new StructureSetSummariesEnabledCommand(structureId, enabled));
	}

}
