import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { SummariesPublisher } from './summaries.publisher';
import { SummariesEventRepository } from './summaries.event-repository';
import { SummariesDomainModule } from '../core/summaries.domain-module';
import { SummariesEnabledArchive } from '../core-read/enabled/summaries-enabled-archive.service';
import { SummariesWarehouse } from './summaries.warehouse';
import { SummariesDomainWarehouse } from '../core-read/summaries.domain-warehouse';
import { SummariesDomainPublisher } from '../core/summaries.domain-publisher';
import { SummariesDomainEventRepository } from '../core-read/summaries.domain-event-repository';


export class SummariesApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(SummariesPublisher, SummariesDomainPublisher);
		container.provide(SummariesEventRepository, SummariesDomainEventRepository);
		container.provide(SummariesWarehouse, SummariesDomainWarehouse);
		container.provide(SummariesEnabledArchive);
	}
}

export function initializeSummaries(): void {
	new DomainInitializer(new SummariesApiModule(), new SummariesDomainModule()).init();
}
