import { FieldConfig } from '../../api/config/field.config';
import { FieldEntity } from './field.entity';
import { DataFieldFactory } from './data/data-field.factory';
import { FieldEntityIdGenerator } from './field-entity-id.generator';


export class FieldEntityFactory {

	constructor(private readonly fieldIdGenerator: FieldEntityIdGenerator,
				private readonly dataFieldFactory: DataFieldFactory) {
	}

	static readonly services = [
		FieldEntityIdGenerator,
		DataFieldFactory
	];

	create(configs: Array<FieldConfig>): Array<FieldEntity> {

		if (!configs) {
			return [];
		}

		return configs.map((fieldConfig: FieldConfig, index: number) => {

			const fieldId = this.fieldIdGenerator.generateId(),
				dataField = this.dataFieldFactory.create(fieldConfig);

			return new FieldEntity(fieldId, dataField, this.getFieldName(fieldConfig, index));
		});
	}

	private getFieldName(fieldConfig: FieldConfig, index: number): string {

		if (typeof fieldConfig.field === 'string') {
			return fieldConfig.field.toLowerCase();
		} else {
			return 'Field #' + index;
		}
	}

}
