import { DomainEvent } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { FieldEntity } from '../../domain/field/field.entity';
import { FieldsInitedEvent } from './fields-inited.event';
import { FieldConfig } from '../../api/config/field.config';
import { StructureAggregateEvent } from '../../../../structure-core/src/core/structure.aggregate-event';


export class FieldsInitedAggregateEvent extends StructureAggregateEvent {

	constructor(structureId: StructureId,
				private readonly fieldConfigs: Array<FieldConfig>, // TODO merge
				private readonly fields: Array<FieldEntity>) {
		super(structureId, 'FieldsInitedAggregateEvent');
	}

	toDomainEvent(): DomainEvent<StructureId> {

		return new FieldsInitedEvent(
			this.getAggregateId(),
			this.fieldConfigs,
			this.fields
		);
	}

	getFieldConfigs(): Array<FieldConfig> {
		return this.fieldConfigs;
	}

	getFields(): Array<FieldEntity> {
		return this.fields;
	}
}
