import { CommandHandler, CommandType } from '@generic-ui/hermes';

import { InitFieldsCommand } from './init-fields.command';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';
import { FieldConfig } from '../../api/config/field.config';


export class InitFieldsCommandHandler implements CommandHandler<StructureAggregate, InitFieldsCommand> {

	forCommand(): CommandType<InitFieldsCommand> {
		return InitFieldsCommand;
	}

	handle(structure: StructureAggregate, command: InitFieldsCommand): void {

		const fieldConfigs: Array<FieldConfig> = command.getFieldConfigs();

		structure.createFields(fieldConfigs);
	}

}
