import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { FieldId } from '../../../../../core/structure/field/src/domain/field/field.id';
import { UniqueValueCollectionModel } from '../../../../../core/structure/filter/src/api/unique/unique-value-collection.model';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FilterWarehouse } from '../../../../../core/structure/filter/src/api/filter.warehouse';
import { UniqueValueModel } from '../../../../../core/structure/filter/src/api/unique/unique-value.model';
import { FilterPublisher } from '../../../../../core/structure/filter/src/api/filter.publisher';

@Component({
	selector: 'div[gui-unique-value-list][fieldId]',
	templateUrl: './unique-value-list.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	styleUrls: [
		'../style/unique-value-list.component.scss'
	]
})
export class UniqueValueListComponent extends SmartComponent implements OnInit {

	@Input()
	fieldId: FieldId;

	selectAllChecked: boolean;

	selectAllIndeterminate: boolean;

	uniqueValues: Array<UniqueValueModel> = [];

	constructor(changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly filterCommandInvoker: FilterPublisher) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.subscribe(
			this.filterWarehouse.onUniqueValues(this.structureId),
			(uniqueValuesReadModel: UniqueValueCollectionModel) => {
				this.uniqueValues = uniqueValuesReadModel.getValues(this.fieldId);

				this.selectAllChecked = uniqueValuesReadModel.isSelectAllChecked(this.fieldId);
				this.selectAllIndeterminate = uniqueValuesReadModel.isIndeterminate(this.fieldId);
			}
		);
	}

	toggleAllSelect(): void {
		event.stopPropagation();

		if (this.selectAllChecked) {
			this.filterCommandInvoker.unselectAllUniqueFilter(this.fieldId, this.structureId);
		} else {
			this.filterCommandInvoker.selectAllUniqueFilter(this.fieldId, this.structureId);
		}
	}

	toggleSelect(uniqueValueReadModel: UniqueValueModel): void {
		event.stopPropagation();

		if (uniqueValueReadModel.isEnabled()) {
			this.filterCommandInvoker.unselectUniqueFilter(this.fieldId, uniqueValueReadModel.getId(), this.structureId);
		} else {
			this.filterCommandInvoker.selectUniqueFilter(this.fieldId, uniqueValueReadModel.getId(), this.structureId);
		}
	}

	clearFilters(): void {
		this.filterCommandInvoker.selectAllUniqueFilter(this.fieldId, this.structureId);
	}

	protected getSelectorName(): string {
		return 'gui-unique-value-list';
	}

}
