import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewEncapsulation } from '@angular/core';

import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FormationPublisher } from '../../../../../core/structure/formation/src/api/formation.publisher';
import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { FormationWarehouse } from '../../../../../core/structure/formation/src/api/formation.warehouse';
import { RowSelectedReadModel } from '../../../../../core/structure/formation/src/api/row-selected/row-selected.read-model';
import { RowSelectionMode } from '../../../../../core/structure/formation/src/api/row-selected/row-selection';
import { useReactiveContext } from '../../../../common/src/cdk/component/lib';

@Component({
	selector: 'div[gui-select-all]',
	templateUrl: './select-all.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class SelectAllComponent extends SmartComponent implements OnInit {

	selectAllChecked: boolean;

	selectAllIndeterminate: boolean;

	modeMulti = false;

	context = useReactiveContext();

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly formationCommandDispatcher: FormationPublisher,
				private readonly formationWarehouse: FormationWarehouse) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {
		this.context
			.connect(this.formationWarehouse.onMode(this.structureId))
			.subscribeAndRender((mode: RowSelectionMode) => {
				this.modeMulti = mode === RowSelectionMode.MULTIPLE;
			});

		this.context
			.connect(this.formationWarehouse.onRowSelectedReadModel(this.structureId))
			.subscribeAndRender(
				(rowSelectedReadModel: RowSelectedReadModel) => {
					this.selectAllChecked = rowSelectedReadModel.isAllSelected();
					this.selectAllIndeterminate = rowSelectedReadModel.isIndeterminate();
				});
	}

	toggleSelectAll(): void {

		if (this.selectAllIndeterminate) {
			this.formationCommandDispatcher.unselectAll(this.structureId);
		} else {

			if (this.selectAllChecked) {
				this.formationCommandDispatcher.unselectAll(this.structureId);
			} else {
				this.formationCommandDispatcher.selectAll(this.structureId);
			}
		}
	}

	protected getSelectorName(): string {
		return 'gui-select-all';
	}

}
