import { RowSelectionMode, RowSelectionType } from '../../api/row-selected/row-selection';

export class FormationSelection {

	constructor(private mode: RowSelectionMode,
				private type: RowSelectionType) {
	}

	setMode(mode: RowSelectionMode): void {
		this.mode = mode;
	}

	getMode(): RowSelectionMode {
		if (this.type === RowSelectionType.RADIO) {
			return RowSelectionMode.SINGLE;
		} else {
			return this.mode;
		}
	}

	isSingle(): boolean {
		return this.getMode() === RowSelectionMode.SINGLE;
	}

	setType(type: RowSelectionType): void {
		this.type = type;
	}

	getType(): RowSelectionType {
		return this.type;
	}
}
