export class RowSelectedReadModel {

	constructor(private readonly itemIds: Array<string>,
				private readonly allSelected: boolean,
				private readonly allUnselected: boolean) {
	}

	getAll(): Array<string> {
		return this.itemIds;
	}

	isSelected(id: string): boolean {
		return this.itemIds.some((itemId: string) => itemId === id);
	}

	isAllSelected(): boolean {
		return this.allSelected;
	}

	isAllUnselected(): boolean {
		return this.allUnselected;
	}

	isIndeterminate(): boolean {
		return !(this.isAllSelected() || this.isAllUnselected());
	}

}
