import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { combineLatest } from 'rxjs';

import { SmartComponent } from '../../../../feature/common/src/cdk/component/lib/src/smart-component';
import { FormationPublisher } from '../../../../core/structure/formation/src/api/formation.publisher';
import { StructureEditModeArchive } from '../edit/structure.edit-mode.archive';
import { FormationWarehouse } from '../../../../core/structure/formation/src/api/formation.warehouse';
import { Item } from '../../../../core/structure/source/src/api/item/item';
import { StructureWarehouse } from '../../../../core/structure/structure-core/src/api/structure.warehouse';
import { StructureId } from '../../../../core/structure/structure-core/src/api/global/structure.id';
import { VerticalFormationWarehouse } from '../../../../core/structure/vertical-formation/src/api/vertical-formation.warehouse';
import { RowSelectionType } from '../../../../core/structure/formation/src/api/row-selected/row-selection';
import { RowSelectToggleType } from '../../../../core/structure/formation/src/domain/row-select-toggle-type';
import { SchemaRowClass } from '../../../../core/schema/src/api/styling/schema.row-class';
import { SchemaRowStyle } from '../../../../core/schema/src/api/styling/schema.row-style';
import { fromRxJsObservable, toRxJsObservable } from '@generic-ui/hermes';
import { SearchWarehouse } from '../../../../core/structure/search/src/api/search.warehouse';
import { CellEditorManager } from '../../../../core/structure/structure-core/src/domain/edit/cell-editor.manager';
import { SchemaWarehouse } from '../../../../core/schema/src/api/schema.warehouse';
import { SchemaReadModelRootId } from '../../../../core/schema/src/api/global/schema.read-model-root-id';
import { CompositionAccessorModel } from '../../../../feature/composition/src/column/composition.accessor.model';


@Component({
	selector: 'div[gui-structure-content]',
	templateUrl: './structure-content.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureContentComponent extends SmartComponent implements OnInit {

	@Input()
	source: Array<Item>;

	@Input()
	columns: Array<CompositionAccessorModel>;

	editMode = false;

	cellEditing: CellEditorManager;

	rowHeight: number;

	searchPhrase: string = '';

	highlighting = true;

	phrase = true;

	rowDetailOpened = 4;

	checkboxSelection = false;

	radioSelection = false;

	schemaRowClass: SchemaRowClass;

	schemaRowStyle: SchemaRowStyle;

	selectionEnabled = true;

	constructor(private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly formationPublisher: FormationPublisher,
				private readonly structureEditModeArchive: StructureEditModeArchive,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly verticalFormationWarehouse: VerticalFormationWarehouse,
				private readonly structureId: StructureId, // REfactor
				private readonly schemaReadModelRootId: SchemaReadModelRootId, // REfactor
				private readonly searchWarehouse: SearchWarehouse,
				private readonly schemaWarehouse: SchemaWarehouse) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		this.subscribe(
			this.formationWarehouse.onType(this.structureId),
			(type: RowSelectionType) => {
				this.checkboxSelection = type === RowSelectionType.CHECKBOX;
				this.radioSelection = type === RowSelectionType.RADIO;
			}
		);

		this.subscribe(
			this.formationWarehouse.onSelectionEnabled(this.structureId),
			(enabled) => {
				this.selectionEnabled = enabled;
			}
		);

		this.subscribe(
			this.structureEditModeArchive.on(),
			(enabled: boolean) => {
				this.editMode = enabled;
			}
		);

		this.subscribe(
			this.structureWarehouse.onEditManager(this.structureId),
			(manager: CellEditorManager) => {
				this.cellEditing = manager;
			}
		);

		this.subscribe(
			this.schemaWarehouse.onRowClass(this.schemaReadModelRootId),
			(schemaRowClass: SchemaRowClass) => {
				this.schemaRowClass = schemaRowClass;
			}
		);

		this.subscribe(
			this.schemaWarehouse.onRowStyle(this.schemaReadModelRootId),
			(schemaRowStyle: SchemaRowStyle) => {
				this.schemaRowStyle = schemaRowStyle;
			}
		);

		this.subscribe(
			this.verticalFormationWarehouse.onRowHeight(this.structureId),
			(rowHeight: number) => {
				this.rowHeight = rowHeight;
			}
		);

		this.subscribe(
			fromRxJsObservable(
				combineLatest(
					toRxJsObservable(this.searchWarehouse.onPhrase(this.structureId)),
					toRxJsObservable(this.searchWarehouse.onHighlight(this.structureId))
				)
			),
			(args: Array<any>) => {

				const searchPhrase = args[0],
					highlighting = args[1];

				this.highlighting = highlighting;
				this.searchPhrase = searchPhrase;

				if (!this.highlighting) {
					this.searchPhrase = '';
				}
			}
		);
	}

	trackByFn() {
		return 0;
	}

	translateY(index: number): string {

		const height = index * this.rowHeight;

		return `translateY(${height}px)`;
	}

	toggleSelectedRow(entity: Item): void {

		if (!this.selectionEnabled) {
			return;
		}

		if (!this.checkboxSelection && !this.radioSelection) {
			this.formationPublisher.toggleSelectedRow(entity.getId(), RowSelectToggleType.NONE, this.structureId);
		}
	}

	protected getSelectorName(): string {
		return 'gui-structure-content';
	}

}
