import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';
import { CompositionChangeSortStatusCommand } from './composition-change-sort-status.command';
import { CompositionChangeSortStatusEvent } from './composition-change-sort-status.event';
import { CompositionAggregate } from '../../../domain/composition.aggregate';


export class CompositionChangeSortStatusCommandHandler implements CommandHandler<CompositionAggregate, CompositionChangeSortStatusCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<CompositionChangeSortStatusCommand> {
		return CompositionChangeSortStatusCommand;
	}

	handle(composition: CompositionAggregate, command: CompositionChangeSortStatusCommand): void {

		const sortParams = command.getSortParams();

		composition.changeSort(sortParams);
	}

	publish(composition: CompositionAggregate, command: CompositionChangeSortStatusCommand): void {

		const compositionId = command.getAggregateId(),
			columns = composition.getActiveColumns();

		this.domainEventPublisher.publish(new CompositionChangeSortStatusEvent(compositionId, columns));
	}

}
