import { CommandHandler, CommandType } from '@generic-ui/hermes';

import { AddFilterCommand } from './add-filter.command';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';
import { FieldId } from '../../../../field/src/domain/field/field.id';
import { FilterTypeId } from '../../domain/type/filter-type.id';


export class AddFilterCommandHandler implements CommandHandler<StructureAggregate, AddFilterCommand> {

	forCommand(): CommandType<AddFilterCommand> {
		return AddFilterCommand;
	}

	handle(structure: StructureAggregate, command: AddFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			filterTypeId: FilterTypeId = command.getFilterTypeId(),
			value: any = command.getValue();
		structure.addFilter(fieldId, filterTypeId, value);
	}

}
