import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { SummariesEnabledArchive } from '../../../../summaries/src/core-read/enabled/summaries-enabled-archive.service';
import { StructureCreatedEvent } from './structure-created.event';
import { StructureId } from '../../api/global/structure.id';


export class StructureCreatedEventHandler implements DomainEventHandler<StructureId, StructureCreatedEvent> {

	constructor(private readonly summariesEnabledArchive: SummariesEnabledArchive) {
	}

	static readonly services = [SummariesEnabledArchive];

	forEvent(): DomainEventType<StructureCreatedEvent> {
		return StructureCreatedEvent;
	}

	handle(event: StructureCreatedEvent): void {

		if (event.ofMessageType('StructureCreatedEvent')) {

			const structureId = event.getAggregateId();

			this.summariesEnabledArchive.init(structureId);
		}
	}

}
