import { AggregateFactory } from '@generic-ui/hermes';

import { PagingManagerFactory } from '../../../paging/src/domain/paging.manager-factory';
import { SourceManagerFactory } from '../../../source/src/domain/source.manager-factory';
import { VerticalFormationFactory } from '../../../vertical-formation/src/domain/vertical-formation.factory';
import { SummariesManagerFactory } from '../../../summaries/src/domain/summaries.manager.factory';
import { FilterManagerFactory } from '../../../filter/src/domain/filter.manager-factory';
import { FieldCollectionFactory } from '../../../field/src/core/field-collection.factory';
import { StructureId } from '../api/global/structure.id';
import { StructureAggregate } from '../domain/structure.aggregate';
import { SorterManager } from '../../../sorting/src/domain/sorter.manager';
import { SearchManagerFactory } from '../../../search/src/domain/search.manager-factory';
import { UniqueFilterManager } from '../../../filter/src/domain/unique/unique-filter-manager';


export class StructureAggregateFactory extends AggregateFactory<StructureId, StructureAggregate> {

	constructor(private readonly pagingAggregateFactory: PagingManagerFactory,
				private readonly sourceManagerFactory: SourceManagerFactory,
				private readonly verticalFormationFactory: VerticalFormationFactory,
				private readonly summariesManagerFactory: SummariesManagerFactory,
				private readonly filterManagerFactory: FilterManagerFactory,
				private readonly searchManagerFactory: SearchManagerFactory,
				private readonly fieldCollectionFactory: FieldCollectionFactory) {
		super();
	}

	static readonly services = [
		PagingManagerFactory, SourceManagerFactory, VerticalFormationFactory, SummariesManagerFactory,
		FilterManagerFactory, SearchManagerFactory, FieldCollectionFactory
	];

	create(structureId: StructureId): StructureAggregate {

		const paging = this.pagingAggregateFactory.createDefault(),
			source = this.sourceManagerFactory.createDefault(structureId),
			verticalFormation = this.verticalFormationFactory.create(structureId);

		const sorterContainer: SorterManager = new SorterManager(),
			filterContainer = this.filterManagerFactory.create(false),
			fieldContainer = this.fieldCollectionFactory.create(),
			summariesManager = this.summariesManagerFactory.create(structureId),
			searchManager = this.searchManagerFactory.create();

		const structureAggregate = new StructureAggregate(structureId,
			paging,
			source,
			verticalFormation,
			summariesManager,
			sorterContainer,
			filterContainer,
			new UniqueFilterManager(),
			searchManager,
			fieldContainer
		);

		this.init(structureAggregate);

		return structureAggregate;
	}

	init(structureAggregate: StructureAggregate): void {
		structureAggregate.init();
	}

}
