import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { QuickFilterEnabledArchive } from './quick-filter-enabled.archive';
import { ConfigQuickFilterSetEvent } from '../../core/quick/config-quick-filter-set.event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class ConfigQuickFilterSetEventHandler implements DomainEventHandler<StructureId, ConfigQuickFilterSetEvent> {

	constructor(private readonly quickFilterEnabledArchive: QuickFilterEnabledArchive) {
	}

	static readonly services = [QuickFilterEnabledArchive];

	forEvent(): DomainEventType<ConfigQuickFilterSetEvent> {
		return ConfigQuickFilterSetEvent;
	}

	handle(event: ConfigQuickFilterSetEvent): void {

		if (event.ofMessageType('ConfigQuickFilterSetEvent')) {
			this.quickFilterEnabledArchive.next(event.getAggregateId(), event.getEnabled());
		}

	}

}
