import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { StructurePreparedEntitiesSetEvent } from './structure.prepared-entities-set.event';
import { StructurePreparedItemsArchive } from './structure.prepared-items.archive';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class StructurePreparedItemsEventHandler implements DomainEventHandler<StructureId, StructurePreparedEntitiesSetEvent> {

	constructor(private readonly structurePreparedItemsRepository: StructurePreparedItemsArchive) {
	}

	static readonly services = [StructurePreparedItemsArchive];

	forEvent(): DomainEventType<StructurePreparedEntitiesSetEvent> {
		return StructurePreparedEntitiesSetEvent;
	}

	handle(event: StructurePreparedEntitiesSetEvent): void {

		if (event.ofMessageType('StructurePreparedEntitiesSetEvent')) {

			const preparedItems = event.getPreparedItems();

			this.structurePreparedItemsRepository.next(event.getAggregateId(), preparedItems);
		}

	}
}
