import { SummariesCalculator } from '../summaries.calculator';
import { BooleanSummarizedValues } from './boolean-summarized.values';

import { DataType } from '../../../../../field/src/domain/field/data/data-type';
import { SummariesType } from '../../summaries.type';
import { ItemEntity } from '../../../../../source/src/domain/item/item.entity';
import { FieldEntity } from '../../../../../field/src/domain/field/field.entity';


export class BooleanSummariesCalculator extends SummariesCalculator<boolean, BooleanSummarizedValues> {

	private readonly truthy = new Map<string, number>();
	private readonly falsy = new Map<string, number>();

	constructor() {
		super();
	}

	forDataType(dataType: DataType): boolean {
		return dataType === DataType.BOOLEAN;
	}

	prepare(field: FieldEntity): void {

		const key = field.getKey();

		if (field.isSummaries(SummariesType.TRUTHY)) {
			this.truthy.set(key, 0);
		}

		if (field.isSummaries(SummariesType.FALSY)) {
			this.falsy.set(key, 0);
		}
	}

	postCalculate(field: FieldEntity, items: Array<ItemEntity>): void {
	}

	aggregate(field: FieldEntity, value: boolean): void {

		const booleanValue = value, // (value as any === 'true'),
			key = field.getKey(),
			truthyForField = this.truthy.get(key),
			falsyForField = this.falsy.get(key);

		if (booleanValue) {
			if (field.isSummaries(SummariesType.TRUTHY)) {
				this.truthy.set(key, truthyForField + 1);
			}
		} else {
			if (field.isSummaries(SummariesType.FALSY)) {
				this.falsy.set(key, falsyForField + 1);
			}
		}

	}

	generateAggregatedValues(field: FieldEntity): BooleanSummarizedValues {

		const key = field.getKey();

		return new BooleanSummarizedValues(
			this.truthy.get(key),
			this.falsy.get(key)
		);
	}

}
