import { ChangeDetectorRef, inject, ViewRef } from '@angular/core';
import { HermesObservable, HermesSubject, HermesSubscription, hermesTakeUntil } from '@generic-ui/hermes';

export interface ReactiveContext<T> {

	connect<T>(stream$: HermesObservable<T>): ReactiveContext<T>;

	subscribe(next?: (value: T) => void, error?: (e: any) => void, complete?: () => void): HermesSubscription;

	subscribeAndRender(next?: (value: T) => void, error?: (e: any) => void, complete?: () => void): HermesSubscription;

	unsubscribe(): void;

}

export function useReactiveContext<T>(stream$?: HermesObservable<T>, cd?: ChangeDetectorRef): ReactiveContext<T> {

	const unsub$ = new HermesSubject<void>();

	let changeDetector: ChangeDetectorRef | undefined;

	if (cd) {
		changeDetector = cd;
	} else {
		changeDetector = inject(ChangeDetectorRef);
	}

	let innerStream$: HermesObservable<T> | undefined;

	if (stream$) {
		innerStream$ = stream$.pipe(hermesTakeUntil(unsub$));
	}

	(changeDetector as ViewRef).onDestroy(() => {
		unsub$.next();
		unsub$.complete();
	});

	const context = {
		connect: (stream$: HermesObservable<T>) => {
			innerStream$ = stream$.pipe(hermesTakeUntil(unsub$));
			return context;
		},
		subscribe(next?: (value: T) => void, error?: (e: any) => void, complete?: () => void): HermesSubscription {
			return innerStream$.subscribe(next, error, complete);
		},
		subscribeAndRender(next?: (value: T) => void, error?: (e: any) => void, complete?: () => void): HermesSubscription {
			return innerStream$.subscribe((v) => {
					next(v);
					changeDetector.detectChanges();
				},
				error,
				complete
			);
		},
		unsubscribe(): void {
			unsub$.next();
			unsub$.complete();
		}
	} as ReactiveContext<T>;
	return context;
}
