import { CellContext } from './cell-context';
import { FieldId } from '../../../../structure/field/src/domain/field/field.id';
import { SortOrder } from '../../domain/column/sort/sort-order';
import { ColumnAlign } from '../../domain/column/column-align';
import { ColumnDefinitionId } from '../../api/column/column-definition-id';

// Basically header template
export class CellTemplateWithContext {

	constructor(
		// public viewTemplate: TemplateRef<any> | null = null,
		// 		public editTemplate: TemplateRef<any> | null = null,
		// public editContext: any,
		private header: any,
		private dataType: any,
		public context: CellContext,
		public width: number,
		private readonly fieldId: FieldId,
		private readonly columnDefinitionId: ColumnDefinitionId,
		private readonly sortStatus: SortOrder,
		private readonly enabled: boolean,
		private readonly cssClasses: string,
		private readonly styles: string,
		private readonly sortable: boolean,
		private readonly align: ColumnAlign) {
	}

	getHeader() {
		return this.header;
	}

	getDataType() {
		return this.dataType;
	}

	getColumnDefinitionId(): ColumnDefinitionId {
		return this.columnDefinitionId;
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getSortStatus(): SortOrder {
		return this.sortStatus;
	}

	getCssClasses(): string {
		return this.cssClasses;
	}

	getStyles(): string {
		return this.styles;
	}

	isSortEnabled(): boolean {
		return this.sortable;
	}

	isAscSort(): boolean {
		return this.sortStatus === SortOrder.ASC;
	}

	isDescSort(): boolean {
		return this.sortStatus === SortOrder.DESC;
	}

	isNoSort(): boolean {
		return this.sortStatus === SortOrder.NONE;
	}

	isEnabled(): boolean {
		return this.enabled;
	}

	isAlignLeft(): boolean {
		return this.align === ColumnAlign.LEFT;
	}

	isAlignCenter(): boolean {
		return this.align === ColumnAlign.CENTER;
	}

	isAlignRight(): boolean {
		return this.align === ColumnAlign.RIGHT;
	}

	getAlign(): ColumnAlign {
		return this.align;
	}

}
