import { StructureReadModelRepository } from '../../../structure-core/src/api/read/structure.read-model-repository';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { StructureReadModelRoot } from '../../../structure-core/src/api/read/structure.read-model-root';
import { Item } from '../api/item/item';
import { ItemEntity } from '../domain/item/item.entity';
import { hermesDistinctUntilChanged, hermesMap, HermesObservable, HermesSingle, singleFromObservable } from '@generic-ui/hermes';
import { SourceWarehouse } from '../api/source.warehouse';
import { StructurePreparedItemsArchive } from '../core/prepared/structure.prepared-items.archive';
import { StructureSourceOriginArchive } from '../core/origin/structure.source-origin.archive';


export class SourceDomainWarehouse extends SourceWarehouse {

	constructor(private readonly structureRepository: StructureReadModelRepository,
				private readonly structurePreparedItemsRepository: StructurePreparedItemsArchive,
				private readonly structureSourceOriginRepository: StructureSourceOriginArchive) {
		super();
	}

	static readonly services = [StructureReadModelRepository, StructurePreparedItemsArchive, StructureSourceOriginArchive];

	findItems(structureId: StructureId): ReadonlyArray<Item> {
		return this.structureRepository.getStructure(structureId).getEntities();
	}

	onItems(structureId: StructureId): HermesObservable<ReadonlyArray<Item>> {

		return this.structureRepository
				   .on(structureId)
				   .pipe(
					   hermesMap((structure: StructureReadModelRoot) => structure.getEntities()),
					   hermesDistinctUntilChanged((ents1: ReadonlyArray<Item>, ents2: ReadonlyArray<Item>) => {
						   if (ents1.length !== ents2.length) {
							   return false;
						   }

						   let flag = true;

						   ents1.forEach((ent, index) => {
							   if (!ent.equals(ents2[index])) {
								   flag = false;
								   return;
							   }
						   });

						   return flag;
					   })
				   );
	}

	onItemsSize(structureId: StructureId): HermesObservable<number> {

		return this.onItems(structureId)
				   .pipe(
					   hermesMap((entities: ReadonlyArray<Item>) => {
						   return entities.length;
					   })
				   );
	}

	onceItems(structureId: StructureId): HermesSingle<ReadonlyArray<Item>> {
		return singleFromObservable(this.onItems(structureId));
	}

	onOriginSize(structureId: StructureId): HermesObservable<number> {
		return this.structureSourceOriginRepository
				   .on(structureId)
				   .pipe(
					   hermesMap(origin => origin.length)
				   );
	}

	onLoading(structureId: StructureId): HermesObservable<boolean> {
		return this.structureRepository
				   .on(structureId)
				   .pipe(
					   hermesMap((structure: StructureReadModelRoot) => structure.getSource().isLoading())
				   );
	}

	onPreparedItems(structureId: StructureId): HermesObservable<ReadonlyArray<ItemEntity>> {
		return this.structurePreparedItemsRepository.on(structureId);
	}

	findPreparedItems(structureId: StructureId): ReadonlyArray<ItemEntity> {
		return this.structurePreparedItemsRepository.getPreparedItems(structureId);
	}

}
