import { FilterId } from '../id/filter.id';

export class ActiveFilterModel {

	constructor(private readonly filterId: FilterId,
				private readonly fieldName: string,
				private readonly filterTypeName: string,
				private readonly value: any) {
	}

	getText(): string {
		return `${this.fieldName}: ${this.filterTypeName}: ${this.value}`;
	}

	getFilterId(): FilterId {
		return this.filterId;
	}

	getFieldName(): string {
		return this.fieldName;
	}

	getFilterTypeName(): string {
		return this.filterTypeName;
	}

	getValue(): any {
		return this.value;
	}

}
