import { SchemaCssClassesRepository } from './css-classes/schema.css-classes.repository';
import { SchemaThemeRepository } from './theme/schema.theme.repository';
import { SchemaHorizontalGridRepository } from './horizontal-grid/schema.horizontal-grid.repository';
import { SchemaRowColoringRepository } from './row-coloring/schema.row-coloring.repository';
import { SchemaVerticalGridRepository } from './vertical-grid/schema.vertical-grid.repository';
import { SchemaReadModelRootId } from '../api/global/schema.read-model-root-id';
import { SchemaTheme } from '../api/theme/schema-theme';
import { SchemaRowColoring } from '../api/row-coloring/schema-row-coloring';
import { SchemaCssClass } from '../api/css-classes/schema-css-class';
import { SchemaWarehouse } from '../api/schema.warehouse';
import { HermesObservable, HermesSingle, singleFromObservable } from '@generic-ui/hermes';
import { SchemaRowClassArchive } from '../core/styling/schema.row-class.archive';
import { SchemaRowStyleArchive } from '../core/styling/schema.row-style.archive';
import { SchemaRowClass } from '../api/styling/schema.row-class';
import { SchemaRowStyle } from '../api/styling/schema.row-style';


export class SchemaDomainWarehouse extends SchemaWarehouse {

	constructor(private readonly schemaCssClassesRepository: SchemaCssClassesRepository,
				private readonly schemaThemeRepository: SchemaThemeRepository,
				private readonly schemaHorizontalGridRepository: SchemaHorizontalGridRepository,
				private readonly schemaRowColoringRepository: SchemaRowColoringRepository,
				private readonly schemaVerticalGridRepository: SchemaVerticalGridRepository,
				private readonly schemaRowClassArchive: SchemaRowClassArchive,
				private readonly schemaRowStyleArchive: SchemaRowStyleArchive
	) {
		super();
	}

	static readonly services = [
		SchemaCssClassesRepository,
		SchemaThemeRepository,
		SchemaHorizontalGridRepository,
		SchemaRowColoringRepository,
		SchemaVerticalGridRepository,
		SchemaRowClassArchive,
		SchemaRowStyleArchive
	] as const;

	onTheme(schemaId: SchemaReadModelRootId): HermesObservable<SchemaTheme> {
		return this.schemaThemeRepository
				   .on(schemaId.toAggregateId());
	}

	onceTheme(schemaId: SchemaReadModelRootId): HermesSingle<SchemaTheme> {
		return singleFromObservable(this.onTheme(schemaId));
	}

	onHorizontalGrid(schemaId: SchemaReadModelRootId): HermesObservable<boolean> {
		return this.schemaHorizontalGridRepository
				   .on(schemaId.toAggregateId());
	}

	onVerticalGrid(schemaId: SchemaReadModelRootId): HermesObservable<boolean> {
		return this.schemaVerticalGridRepository
				   .on(schemaId.toAggregateId());
	}

	onRowColoring(schemaId: SchemaReadModelRootId): HermesObservable<SchemaRowColoring> {
		return this.schemaRowColoringRepository
				   .on(schemaId.toAggregateId());
	}

	onCssClasses(schemaId: SchemaReadModelRootId): HermesObservable<SchemaCssClass> {
		return this.schemaCssClassesRepository
				   .on(schemaId.toAggregateId());
	}

	onRowClass(schemaId: SchemaReadModelRootId): HermesObservable<SchemaRowClass> {
		return this.schemaRowClassArchive.on(schemaId.toAggregateId());
	}

	onRowStyle(schemaId: SchemaReadModelRootId): HermesObservable<SchemaRowStyle> {
		return this.schemaRowStyleArchive.on(schemaId.toAggregateId());
	}

}
