import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetRowHeightCommand } from './set-row-height.command';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';
import { RowHeightSetEvent } from './row-height-set.event';


export class SetRowHeightCommandHandler implements CommandHandler<StructureAggregate, SetRowHeightCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetRowHeightCommand> {
		return SetRowHeightCommand;
	}

	handle(structure: StructureAggregate, command: SetRowHeightCommand): void {

		const rowHeight = command.getRowHeight();

		const formation = structure.getVerticalFormation();

		formation.setRowHeight(rowHeight);
	}

	publish(aggregate: StructureAggregate, command: SetRowHeightCommand): void {
		this.domainEventPublisher.publish(new RowHeightSetEvent(command.getAggregateId()));
	}

}
