import { ChangeDetectorRef, Component, ElementRef, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, ViewChild, ViewEncapsulation } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

import { GuiGridGateway } from './gui.grid-gateway';

import { structureParentComponent } from '../../../../feature-view/grid-view/src/structure-parent-component';
import { GuiGridRegister } from './register/gui.grid.register';
import { GuiGridApi, GuiGridApiProvider } from './api/gui.grid.api-provider';
import { FormationPublisher } from '../../../../core/structure/formation/src/api/formation.publisher';
import { SourcePublisher } from '../../../../core/structure/source/src/api/source.publisher';
import { FormationWarehouse } from '../../../../core/structure/formation/src/api/formation.warehouse';
import { SearchPublisher } from '../../../../core/structure/search/src/api/search.publisher';
import { GuiGridThemeCommandInvoker } from './theme/gui.grid.theme.command-invoker';
import { SchemaPublisher } from '../../../../core/schema/src/api/schema.publisher';
import { StructurePublisher } from '../../../../core/structure/structure-core/src/api/structure.publisher';
import { ClassModifier } from '../../../../feature/common/src/cdk/component/lib/src/dom/class/class-modifier';
import { AttributeModifier } from '../../../../feature/common/src/cdk/component/lib/src/dom/attribute/attribute-modifier';
import { PagingPublisher } from '../../../../core/structure/paging/src/api/paging.publisher';
import { SummariesPublisher } from '../../../../core/structure/summaries/src/api/summaries.publisher';
import { SortingPublisher } from '../../../../core/structure/sorting/src/api/sorting.publisher';
import { StructureDefinition } from '../../../../feature-view/grid-view/src/structure-definition';
import { guiGridStructureDefinition } from './gui.grid-structure-definition';
import { CompositionPublisher } from '../../../../core/composition/src/api/composition.publisher';
import { CompositionWarehouse } from '../../../../core/composition/src/api/composition.warehouse';
import { FilterIntegration } from '../../../../core/structure/filter/src/api-integration/filter.integration';
import { StructureIdGenerator } from '../../../../feature-view/grid-view/src/structure-id.generator';

@Component({
	selector: 'gui-grid',
	templateUrl: './gui.grid.component.html',
	styleUrls: [
		'./gui.grid.ngx.scss'
	],
	providers: [
		{
			provide: StructureDefinition,
			useValue: guiGridStructureDefinition
		},
		{
			provide: structureParentComponent,
			useExisting: GuiGridComponent
		}
	],
	encapsulation: ViewEncapsulation.None
})
export class GuiGridComponent extends GuiGridGateway implements OnInit, OnDestroy {

	private static readonly GUI_GRID_ID = 'gui-grid-id';

	@ViewChild('structure', { static: true })
	structureRef: any;

	@Input()
	gridId: string; // can be set once

	public api: GuiGridApi;

	private localGridId: string;

	private readonly gridThemeCommandInvoker: GuiGridThemeCommandInvoker;

	private readonly classModifier: ClassModifier;

	private readonly attributeModifier: AttributeModifier;

	constructor(@Inject(PLATFORM_ID) private readonly platformId: any,
				private readonly elementRef: ElementRef,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly gridRegister: GuiGridRegister,
				private readonly structureIdGenerator: StructureIdGenerator,
				// delegate
				private readonly formationCommandDispatcher: FormationPublisher,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly compositionCommandInvoker: CompositionPublisher,
				private readonly compositionWarehouse: CompositionWarehouse,
				private readonly filterIntegration: FilterIntegration,
				private readonly sourceCommandDispatcher: SourcePublisher,
				private readonly searchCommandInvoker: SearchPublisher,
				private readonly schemaCommandInvoker: SchemaPublisher,
				private readonly structureCommandDispatcher: StructurePublisher,
				private readonly summariesCommandInvoker: SummariesPublisher,
				private readonly sortingCommandInvoker: SortingPublisher,
				private readonly pagingCommandInvoker: PagingPublisher
	) {
		super();

		this.gridThemeCommandInvoker = new GuiGridThemeCommandInvoker(
			this.schemaCommandInvoker,
			this.gridThemeConverter,
			this.gridRowColoringConverter
		);

		this.classModifier = new ClassModifier(this.elementRef.nativeElement);
		this.attributeModifier = new AttributeModifier(this.elementRef.nativeElement);
	}

	ngOnInit() {

		this.classModifier.getHost().add('gui-grid');

		this.initApi();

		let gridId = this.gridId;

		if (gridId === undefined) {
			// gridId = this.gridIdGenerator.generateId();
			gridId = 'gui-grid-' + this.structureIdGenerator.generateId();
		}

		this.localGridId = gridId;

		this.exposeGridId();

		// register
		this.gridRegister.register(gridId, this, this.structureRef.getStructureId());

		if (isPlatformBrowser(this.platformId)) {
			window['getGuiGrid'] = (gridId: string) => {

				const gridConsoleConfig = this.gridRegister.getValues(gridId);

				if (gridConsoleConfig) {
					return this.api;
				} else {
					return undefined;
				}
			};
		}
	}

	ngOnDestroy() {
		this.gridRegister.unregister(this.localGridId);
	}

	getElementRef(): ElementRef {
		return this.elementRef;
	}

	detectChanges(): void {
		this.changeDetectorRef.detectChanges();
	}

	private exposeGridId(): void {
		this.attributeModifier.getHost().setAttribute(GuiGridComponent.GUI_GRID_ID, this.localGridId);
	}

	private initApi(): void {
		this.api = new GuiGridApiProvider(
			this.structureRef.structureId,
			this.structureRef.compositionId,
			this.structureRef.schemaReadModelRootId,
			this.formationCommandDispatcher,
			this.formationWarehouse,
			this.compositionCommandInvoker,
			this.compositionWarehouse,
			this.filterIntegration,
			this.sourceCommandDispatcher,
			this.searchCommandInvoker,
			this.gridThemeCommandInvoker,
			this.structureCommandDispatcher,
			this.summariesCommandInvoker,
			this.sortingCommandInvoker,
			this.pagingCommandInvoker
		).provide();
	}

}
