import { CommandHandler, CommandType } from '@generic-ui/hermes';

import { SelectAllUniqueFilterCommand } from './select-all-unique-filter.command';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';
import { FieldId } from '../../../../../field/src/domain/field/field.id';


export class SelectAllUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, SelectAllUniqueFilterCommand> {

	forCommand(): CommandType<SelectAllUniqueFilterCommand> {
		return SelectAllUniqueFilterCommand;
	}

	handle(structure: StructureAggregate, command: SelectAllUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId();

		structure.selectAllUniqueFilter(fieldId);
	}

}
