import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { PrevPageCommand } from './prev-page.command';
import { PrevPageEvent } from './prev-page.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class PrevPageCommandHandler implements CommandHandler<StructureAggregate, PrevPageCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<PrevPageCommand> {
		return PrevPageCommand;
	}

	handle(structure: StructureAggregate, command: PrevPageCommand): void {

		structure.prevPage();
	}

	publish(aggregate: StructureAggregate, command: PrevPageCommand): void {
		this.domainEventPublisher.publish(new PrevPageEvent(command.getAggregateId()));
	}

}
