import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, Input, ViewEncapsulation } from '@angular/core';

import { CellTemplateWithContext } from '../../../../../core/composition/src/core-read/definition/cell-template-with-context';
import { StructureColumnConfigService } from '../menu/structure.column-config.service';
import { SortOrder } from '../../../../../core/composition/src/domain/column/sort/sort-order';
import { SortingPublisher } from '../../../../../core/structure/sorting/src/api/sorting.publisher';
import { CompositionId } from '../../../../../core/composition/src/api/global/composition.id';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FormationPublisher } from '../../../../../core/structure/formation/src/api/formation.publisher';
import { Group } from '../../../../../core/composition/src/api/group/group';
import { PureComponent } from '../../../../../feature/common/src/cdk/component/lib/src/pure-component';


@Component({
	selector: 'div[gui-structure-header-groups][groups][checkboxSelection]',
	templateUrl: './structure-header-groups.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	providers: [
		StructureColumnConfigService
	]
})
export class StructureHeaderGroupsComponent extends PureComponent {

	@Input()
	groups: Array<Group>;

	@Input()
	showGroups: boolean;

	@Input()
	checkboxSelection: boolean = false;

	globalSearching = false;

	constructor(private readonly elementRef: ElementRef,
				private readonly injector: Injector,
				private readonly changeDetectorRef: ChangeDetectorRef,
				private readonly compositionId: CompositionId,
				private readonly structureId: StructureId,
				private readonly formationCommandDispatcher: FormationPublisher,
				private readonly sortingCommandDispatcher: SortingPublisher) {
		super(elementRef);
	}

	toggleSort(column: CellTemplateWithContext): void {

		if (column.isSortEnabled()) {
			this.sortingCommandDispatcher.toggleSort(column.getFieldId(), this.compositionId, this.structureId);
		}
	}

	isSortAsc(column: CellTemplateWithContext): boolean {
		return column.getSortStatus() === SortOrder.ASC;
	}

	isSortDesc(column: CellTemplateWithContext): boolean {
		return column.getSortStatus() === SortOrder.DESC;
	}

	isGlobalSortEnabled(): boolean {
		return this.globalSearching;
	}

	protected getSelectorName(): string {
		return 'gui-structure-header-groups';
	}

}
