import { hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';
import { StructureReadModelRootId } from '../../../structure-core/src/api/read/structure.read-model-root-id';
import { SearchPhraseSetDomainEvent } from '../core/phrase/set-phrase/search-phrase-set.domain-event';
import { SearchEventRepository } from '../api/search.event-repository';


export class SearchDomainEventRepository extends SearchEventRepository {

	constructor() {
		super();
	}

	onSearchPhrase(structureId: StructureReadModelRootId): HermesObservable<string> {
		return this.onEvent(structureId, SearchPhraseSetDomainEvent)
				   .pipe(
					   hermesFilter((event: SearchPhraseSetDomainEvent) => event.isInitial() === false),
					   hermesMap((event: SearchPhraseSetDomainEvent) => event.getPhrase()),
					   hermesFilter((phrase: string) => phrase !== null)
				   );
	}
}
