import { PagingManager } from '../domain/paging.manager';
import { PagingModel } from '../api/model/paging.model';


export class PagingConverter {

	convert(aggregate: PagingManager): PagingModel {
		return new PagingModel(
			aggregate.isEnabled(),
			aggregate.getPage(),
			aggregate.getPageSize(),
			aggregate.getPageSizes(),
			aggregate.isPagerTop(),
			aggregate.isPagerBottom(),
			aggregate.isNextPageDisabled(),
			aggregate.isPrevPageDisabled(),
			aggregate.calculateStart(),
			aggregate.calculateEnd(),
			aggregate.getSourceSize()
		);
	}

}
