import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Injector, OnInit, ViewEncapsulation } from '@angular/core';

import { FabricDialogService } from '@generic-ui/fabric';

import { SmartComponent } from '../../../../../feature/common/src/cdk/component/lib/src/smart-component';

import { StructureInfoModalComponent } from './info-modal/structure-info-modal.component';

import { SourceWarehouse } from '../../../../../core/structure/source/src/api/source.warehouse';
import { StructureDialogColumnManagerService } from '../../column-manager/dialog/structure.dialog-column-manager.service';
import { CompositionId } from '../../../../../core/composition/src/api/global/composition.id';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { SchemaReadModelRootId } from '../../../../../core/schema/src/api/global/schema.read-model-root-id';
import { StructureInfoPanelArchive } from '../../../../../core/structure/structure-core/src/api/panel/info/structure.info-panel.archive';
import { StructureInfoPanelConfig } from '../../../../../core/structure/structure-core/src/api/panel/info/structure.info-panel.config';
import { StructureDialogSchemaManagerService } from '../../../../../feature/schema/src/manager/dialog/structure.dialog-schema-manager.service';
import { ItemEntity } from '../../../../../core/structure/source/src/domain/item/item.entity';
import { TranslationFacade } from '../../../../../core/l10n/src/api/translation.facade';
import { Translation } from '../../../../../core/l10n/src/api/translation';
import { useReactiveContext } from '../../../../../feature/common/src/cdk/component/lib';


@Component({
	selector: 'div[gui-structure-info-panel]',
	templateUrl: './structure.info-panel.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureInfoPanelComponent extends SmartComponent implements OnInit {

	totalItemsSize: number;

	preparedItemsSize: number;

	rowHeight: number;

	infoModal = StructureInfoModalComponent;

	infoPanelConfig: StructureInfoPanelConfig;

	themeManagerTooltipText: string;

	columnManagerTooltipText: string;

	infoTooltipText: string;

	context = useReactiveContext();

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly injector: Injector,
				private readonly sourceWarehouse: SourceWarehouse,
				private readonly dialog: FabricDialogService,
				private readonly compositionId: CompositionId,
				private readonly structureId: StructureId,
				private readonly schemaReadModelRootId: SchemaReadModelRootId,
				private readonly menuColumnManagerService: StructureDialogColumnManagerService,
				private readonly translationService: TranslationFacade,
				private readonly schemaManagerService: StructureDialogSchemaManagerService,
				private readonly structureInfoPanelArchive: StructureInfoPanelArchive) {
		super(changeDetectorRef, elementRef);
	}

	ngOnInit() {

		useReactiveContext(this.structureInfoPanelArchive.on(), this.changeDetectorRef)
			.subscribeAndRender((infoPanel: StructureInfoPanelConfig) => {
				this.infoPanelConfig = infoPanel;
			});

		this.context
			.connect(this.sourceWarehouse.onOriginSize(this.structureId))
			.subscribeAndRender((size: number) => {
				this.totalItemsSize = size;
			});

		this.context
			.connect(this.sourceWarehouse.onPreparedItems(this.structureId))
			.subscribeAndRender((preparedItems: Array<ItemEntity>) => {
				this.preparedItemsSize = preparedItems.length;
			});

		useReactiveContext(this.translationService.onTranslation(), this.changeDetectorRef)
			.subscribeAndRender((translation: Translation) => {
				this.themeManagerTooltipText = translation.infoPanelThemeMangerTooltipText;
				this.columnManagerTooltipText = translation.infoPanelColumnManagerTooltipText;
				this.infoTooltipText = translation.infoPanelInfoTooltipText;
			});
	}

	openInfo(): void {
		this.dialog.open({ component: this.infoModal });
	}

	openColumnManager(): void {
		this.menuColumnManagerService.open(this.compositionId, this.schemaReadModelRootId, this.injector);
	}

	openSchemaManager(): void {
		this.schemaManagerService.open(this.schemaReadModelRootId, this.injector);
	}

	protected getSelectorName(): string {
		return 'gui-structure-info-panel';
	}

}
