import { Injectable, TemplateRef } from '@angular/core';
import { EditTemplateFactory } from './edit-template.factory';
import { DataType } from '../../../../../core/structure/field/src/domain/field/data/data-type';

@Injectable()
export class EditTemplateRepository {

	constructor(private editTemplateFactory: EditTemplateFactory) {
	}

	static readonly services = [EditTemplateFactory];

	findTemplate(dataType: DataType): TemplateRef<any> {
		const typeToTemplate = this.editTemplateFactory.getTemplates();

		return typeToTemplate.get(dataType);
	}

}
