import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { ListViewModeArchive } from './list-view.mode.archive';
import { ListViewId } from '../../core/list-view.id';
import { ListViewModeSetEvent } from '../../core/mode/list-view-mode-set.event';


export class ListViewModeSetEventHandler implements DomainEventHandler<ListViewId, ListViewModeSetEvent> {

	constructor(private readonly listViewModeArchive: ListViewModeArchive) {
	}

	static readonly services = [ListViewModeArchive];

	forEvent(): DomainEventType<ListViewModeSetEvent> {
		return ListViewModeSetEvent;
	}

	handle(event: ListViewModeSetEvent): void {

		if (event.ofMessageType('ListViewModeSetEvent')) {
			this.listViewModeArchive.next(event.getAggregateId(), event.getTheme());
		}

	}

}
