import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { ListViewSelectorArchive } from './list-view.selector.archive';

import { ListViewId } from '../../../core/list-view.id';
import { ListViewSelectorToggledEvent } from '../../../core/mode/selector/list-view-selector-toggled.event';


export class ListViewSelectorToggledEventHandler implements DomainEventHandler<ListViewId, ListViewSelectorToggledEvent> {

	constructor(private readonly listViewSelectorArchive: ListViewSelectorArchive) {
	}

	static readonly services = [ListViewSelectorArchive];

	forEvent(): DomainEventType<ListViewSelectorToggledEvent> {
		return ListViewSelectorToggledEvent;
	}

	handle(event: ListViewSelectorToggledEvent): void {

		if (event.ofMessageType('ListViewSelectorToggledEvent')) {
			this.listViewSelectorArchive.next(event.getAggregateId(), event.isEnabled());
		}
	}

}
