import { PagingDispatcher } from './paging.dispatcher';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { PagingConfig } from '../api/config/paging-config';
import { PagingPublisher } from '../api/paging.publisher';


export class PagingDomainPublisher extends PagingPublisher {

	constructor(private readonly pagingDispatcher: PagingDispatcher) {
		super();
	}

	static readonly services = [PagingDispatcher];

	enable(structureId: StructureId): void {
		this.pagingDispatcher.setPaging({ enabled: true }, structureId);
	}

	disable(structureId: StructureId): void {
		this.pagingDispatcher.setPaging({ enabled: false }, structureId);
	}

	setPaging(paging: PagingConfig, structureId: StructureId): void {
		this.pagingDispatcher.setPaging(paging, structureId);
	}

	changePageSize(pageSize: number, structureId: StructureId): void {
		this.pagingDispatcher.changePageSize(pageSize, structureId);
	}

	nextPage(structureId: StructureId): void {
		this.pagingDispatcher.nextPage(structureId);
	}

	prevPage(structureId: StructureId): void {
		this.pagingDispatcher.prevPage(structureId);
	}

	goToPage(pageNumber: number, currentPage: number, structureId: StructureId) {

		if (currentPage < pageNumber) {
			while (currentPage !== pageNumber) {
				this.nextPage(structureId);
				currentPage += 1;
			}
		} else {
			while (currentPage !== pageNumber) {
				this.prevPage(structureId);
				currentPage -= 1;
			}
		}
	}

	changePagerTop(enabled: boolean, structureId: StructureId): void {
		this.pagingDispatcher.setPaging({ pagerTop: enabled }, structureId);
	}

	changePagerBottom(enabled: boolean, structureId: StructureId): void {
		this.pagingDispatcher.setPaging({ pagerBottom: enabled }, structureId);
	}
}
