import { AggregateFactory } from '@generic-ui/hermes';

import { SchemaAggregate } from '../../domain/schema.aggregate';
import { SchemaId } from '../schema.id';


export class SchemaAggregateFactory extends AggregateFactory<SchemaId, SchemaAggregate> {

	constructor() {
		super();
	}

	create(aggregateId: SchemaId): SchemaAggregate {
		return new SchemaAggregate(aggregateId);
	}
}
