import { AggregateDefinition, AggregateId, AggregateRoot, Command, Container, DomainEvent, DomainProviders } from '@generic-ui/hermes';

import { ToggleSortCommandHandler } from './toggle/toggle-sort.command-handler';
import { SetSortingCommandHandler } from './set-config/set-sorting.command-handler';
import { SetSortOrderCommandHandler } from './order/set-sort-order.command-handler';
import { structureKey } from '../../../structure-core/src/api/global/structre.key';
import { SortingDispatcher } from './sorting.dispatcher';


export class SortingDomainModule implements DomainProviders {

	defineAggregate(): AggregateDefinition<any, any, any> | null {
		return null;
	}

	registerKey(): string {
		return structureKey;
	}

	registerProviders(container: Container): void {
		container.provide(SortingDispatcher);
	}

	registerCommandHandlers<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(): Array<any> {
		return [
			ToggleSortCommandHandler,
			SetSortingCommandHandler,
			SetSortOrderCommandHandler
		];
	}

	registerDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [];
	}

	registerMultiDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(): Array<any> {
		return [];
	}

}
