import { TemplateRef } from '@angular/core';
import { CellTemplateWithContext } from '../../../../core/composition/src/core-read/definition/cell-template-with-context';

export class CompositionTemplateModel extends CellTemplateWithContext {

	constructor(public viewTemplate: TemplateRef<any> | null = null,
				public editTemplate: TemplateRef<any> | null = null,
				context: CellTemplateWithContext) {
		super(
			context.getHeader(),
			context.getDataType(),
			context.context,
			context.width,
			context.getFieldId(),
			context.getColumnDefinitionId(),
			context.getSortStatus(),
			context.isEnabled(),
			context.getCssClasses(),
			context.getStyles(),
			context.isSortEnabled(),
			context.getAlign()
		);
	}
}
