import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { VerticalFormationDomainModule } from '../core/vertical-formation.domain-module';
import { VerticalFormationConverter } from '../core/vertical-formation.converter';
import { VerticalFormationRepository } from '../core-read/vertical-formation.repository';
import { VerticalFormationWarehouse } from './vertical-formation.warehouse';
import { VerticalFormationScrollBarPositionArchive } from '../core-read/scroll-bar/vertical-formation.scroll-bar-position.archive';
import { VerticalFormationDomainWarehouse } from '../core-read/vertical-formation.domain-warehouse';
import { VerticalFormationPublisher } from './vertical-formation.publisher';
import { VerticalFormationDomainPublisher } from '../core/vertical-formation.domain-publisher';


export class VerticalFormationApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(VerticalFormationConverter);
		container.provide(VerticalFormationRepository);
		container.provide(VerticalFormationWarehouse, VerticalFormationDomainWarehouse);
		container.provide(VerticalFormationPublisher, VerticalFormationDomainPublisher);
		container.provide(VerticalFormationScrollBarPositionArchive);
	}
}

export function initializeVerticalFormation(): void {
	new DomainInitializer(new VerticalFormationApiModule(), new VerticalFormationDomainModule()).init();
}
