import { AggregateFactory } from '@generic-ui/hermes';

import { CompositionId } from '../api/global/composition.id';
import { CompositionAggregate } from '../domain/composition.aggregate';
import { ColumnEntityFactory } from '../domain/column/column-entity.factory';
import { CompositionGroupFactory } from './group/composition.group.factory';


export class CompositionAggregateFactory extends AggregateFactory<CompositionId, CompositionAggregate> {

	constructor(private readonly columnFactory: ColumnEntityFactory,
				private readonly groupFactory: CompositionGroupFactory) {
		super();
	}

	static readonly services = [
		ColumnEntityFactory,
		CompositionGroupFactory
	] as const;

	create(id: CompositionId): CompositionAggregate {
		return new CompositionAggregate(id, this.columnFactory, this.groupFactory);
	}
}
