import { FieldId } from '../../../../field/src/domain/field/field.id';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { StructureCommand } from '../../../../structure-core/src/core/structure.command';


export class ToggleFilterCommand extends StructureCommand {

	constructor(structureId: StructureId,
				private readonly fieldId: FieldId,
				private readonly externalFilterId: string,
				private readonly filterValue: string) {
		super(structureId, 'ToggleFilterCommand');
	}

	getFieldId(): FieldId {
		return this.fieldId;
	}

	getExternalFilterId(): string {
		return this.externalFilterId;
	}

	getFilterValue(): string {
		return this.filterValue;
	}

}
