import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSchemaVerticalGridCommand } from './set-schema-vertical-grid.command';
import { SchemaAggregate } from '../../../domain/schema.aggregate';


export class SetSchemaVerticalGridCommandHandler implements CommandHandler<SchemaAggregate, SetSchemaVerticalGridCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetSchemaVerticalGridCommand> {
		return SetSchemaVerticalGridCommand;
	}

	handle(schemaAggregate: SchemaAggregate, command: SetSchemaVerticalGridCommand): void {

		const enabled = command.isEnabled();

		schemaAggregate.setVerticalGrid(enabled);
	}

	publish(schemaAggregate: SchemaAggregate, command: SetSchemaVerticalGridCommand): void {
		this.domainEventPublisher.publishFromAggregate(schemaAggregate);
	}

}
