import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetConfigSearchingCommand } from './set-config-searching.command';
import { ConfigSearchingSetEvent } from './config-searching-set.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SetConfigSearchingCommandHandler implements CommandHandler<StructureAggregate, SetConfigSearchingCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetConfigSearchingCommand> {
		return SetConfigSearchingCommand;
	}

	handle(aggregate: StructureAggregate, command: SetConfigSearchingCommand): void {

		const searchingConfig = command.getConfig();

		aggregate.setSearchingConfig(searchingConfig);
	}

	publish(aggregate: StructureAggregate, command: SetConfigSearchingCommand): void {

		const searchingConfig = command.getConfig();

		this.domainEventPublisher.publish(new ConfigSearchingSetEvent(command.getAggregateId(), searchingConfig.enabled));
	}

}
