import { FieldModel } from '../../api/model/field.model';
import { FieldEntity } from '../../domain/field/field.entity';


export class FieldConverter {

	convert(fields: Array<FieldEntity>): Array<FieldModel> {
		return fields.map((field: FieldEntity) => {
			return this.convertOne(field);
		});
	}

	private convertOne(field: FieldEntity): FieldModel {

		return new FieldModel(field.getId(), field.getDataType(), field.getName());
	}
}
