import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSourceLoadingEvent } from './set-source-loading.event';
import { SourceSetLoadingCommand } from './source-set-loading.command';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class SourceSetLoadingCommandHandler implements CommandHandler<StructureAggregate, SourceSetLoadingCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SourceSetLoadingCommand> {
		return SourceSetLoadingCommand;
	}

	handle(aggregate: StructureAggregate, command: SourceSetLoadingCommand): void {

		const enabled = command.isEnabled(),
			source = aggregate.getSource();

		source.setLoading(enabled);
	}

	publish(aggregate: StructureAggregate, command: SourceSetLoadingCommand): void {
		this.domainEventPublisher.publish(new SetSourceLoadingEvent(command.getAggregateId()));
	}

}
