import { Directive, ElementRef } from '@angular/core';
import { GuiComponent } from './gui-component';

@Directive()
export abstract class DynamicallyCreatedComponent extends GuiComponent {

	private readonly subClassConstructor: Function;

	protected constructor(elRef: ElementRef) {
		super(elRef);
		this.subClassConstructor = this.constructor;
		if (this.hasInputs()) {
			this.throwError('it cannot have properties annotated with @Inputs().');
		}
	}

	private hasInputs(): boolean {
		return !this.isEmpty((this.constructor as any).ɵcmp.inputs);
	}

	private throwError(reason: string): void {
		throw new Error(`Component "${this.subClassConstructor.name}" is a DynamicallyCreatedComponent, ${reason}.`);
	}

	private isEmpty(obj: Object) {
		for (const key in obj) {
			if (obj.hasOwnProperty(key))
				return false;
		}
		return true;
	}
}
