import { CoreContainer, DomainEvent, DomainEventBus, hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';

import { StructureId } from '../../../../structure-core/src/api/global/structure.id';
import { EditedItemModel } from './edited-item.model';
import { StructureSourceItemEditedEvent } from '../../core/origin/edit/structure.source-item-edited.event';


export class SourceEventService {

	private readonly domainEventBus: DomainEventBus = CoreContainer.resolve(DomainEventBus);

	onSourceEdited(structureId: StructureId): HermesObservable<EditedItemModel> {

		return this.domainEventBus
				   .ofEvents([
						   StructureSourceItemEditedEvent as any
					   ]
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toString()),
					   hermesMap((event: StructureSourceItemEditedEvent) => {

						   const afterItem = event.getAfterItem().getSourceItem(),
							   beforeItem = event.getBeforeItem().getSourceItem();

						   return new EditedItemModel(afterItem, beforeItem);
					   })
				   );
	}
}
