import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSchemaHorizontalGridCommand } from './set-schema-horizontal-grid.command';
import { SchemaAggregate } from '../../../domain/schema.aggregate';


export class SetSchemaHorizontalGridCommandHandler implements CommandHandler<SchemaAggregate, SetSchemaHorizontalGridCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetSchemaHorizontalGridCommand> {
		return SetSchemaHorizontalGridCommand;
	}

	handle(schemaAggregate: SchemaAggregate, command: SetSchemaHorizontalGridCommand): void {

		const enabled = command.isEnabled();

		schemaAggregate.setHorizontalGrid(enabled);
	}

	publish(schemaAggregate: SchemaAggregate, command: SetSchemaHorizontalGridCommand): void {
		this.domainEventPublisher.publishFromAggregate(schemaAggregate);
	}
}
