import { Directive, Input, OnChanges } from '@angular/core';
import { Gate } from '../../../../../../feature/common/src/cdk/component/lib/src/gate';
import { GuiRowStyle } from '../../../../../../gui/grid/src/core/api/gui.grid.public-api';
import { SchemaRowStyle } from '../../../../../../core/schema/src/api/styling/schema.row-style';
import { NgChanges } from '../../../../../../feature/common/src/cdk/component/lib/src/ng-changes';
import { SchemaReadModelRootId } from '../../../../../../core/schema/src/api/global/schema.read-model-root-id';
import { SchemaPublisher } from '../../../../../../core/schema/src/api/schema.publisher';

@Directive({
	selector: 'gui-structure[rowStyle]'
})
export class StructureRowStyleGate extends Gate implements OnChanges {

	@Input()
	rowStyle: GuiRowStyle;

	constructor(private readonly schemaReadModelRootId: SchemaReadModelRootId,
				private readonly schemaPublisher: SchemaPublisher) {
		super();
	}

	ngOnChanges(changes: NgChanges<StructureRowStyleGate>) {

		if (this.isDefined('rowStyle', changes)) {
			this.schemaPublisher.setRowStyle(this.rowStyle as SchemaRowStyle, this.schemaReadModelRootId);
		}
	}

}
