import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';

import { SmartComponent } from '../../../../feature/common/src/cdk/component/lib/src/smart-component';

import { StructureHeaderColumnsComponent } from './column/structure-header-columns.component';
import { StructureId } from '../../../../core/structure/structure-core/src/api/global/structure.id';
import { FilterWarehouse } from '../../../../core/structure/filter/src/api/filter.warehouse';
import { CompositionWarehouse } from '../../../../core/composition/src/api/composition.warehouse';
import { StructureHeaderFiltersComponent } from './filters/structure-header-filters.component';
import { StructureWarehouse } from '../../../../core/structure/structure-core/src/api/structure.warehouse';
import { VerticalFormationWarehouse } from '../../../../core/structure/vertical-formation/src/api/vertical-formation.warehouse';
import { RowSelectionType } from '../../../../core/structure/formation/src/api/row-selected/row-selection';
import { Group } from '../../../../core/composition/src/api/group/group';
import { CompositionId } from '../../../../core/composition/src/api/global/composition.id';
import { GroupCollection } from '../../../../core/composition/src/api/group/group.collection';
import { StyleModifier } from '../../../../feature/common/src/cdk/component/lib/src/dom/style/style-modifier';
import { CompositionTemplateWarehouse } from '../../../../feature/composition/src/column/composition.template-warehouse';
import { CompositionTemplateModel } from '../../../../feature/composition/src/column/composition.template.model';
import { FormationWarehouse } from '../../../../core/structure/formation/src/api/formation.warehouse';


@Component({
	selector: 'div[gui-structure-header]',
	templateUrl: './structure-header.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureHeaderComponent extends SmartComponent implements OnInit, AfterViewInit {

	@ViewChild(StructureHeaderColumnsComponent, { read: ElementRef, static: true })
	readonly containerRef?: ElementRef;

	@ViewChild(StructureHeaderFiltersComponent, { read: ElementRef, static: true })
	readonly filtersRef?: ElementRef;

	headerColumns: Array<CompositionTemplateModel> = [];

	filterRowEnabled: boolean = false;

	showSelection: boolean;

	groups: Array<Group>;

	showGroups: boolean = false;

	private readonly styleModifier: StyleModifier;

	constructor(private readonly changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly filterWarehouse: FilterWarehouse,
				private readonly structureWarehouse: StructureWarehouse,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly structureVerticalFormationWarehouse: VerticalFormationWarehouse,
				private readonly compositionTemplateWarehouse: CompositionTemplateWarehouse,
				private readonly compositionWarehouse: CompositionWarehouse) {
		super(changeDetectorRef, elementRef);
		this.styleModifier = new StyleModifier(elementRef.nativeElement);
	}

	ngOnInit() {

		this.subscribe(
			this.formationWarehouse.onType(this.structureId),
			(type: RowSelectionType) => {
				this.showSelection = type === RowSelectionType.CHECKBOX || type === RowSelectionType.RADIO;
			}
		);

		this.subscribe(
			this.compositionTemplateWarehouse.onHeaderCols(this.compositionId),
			(columns: Array<CompositionTemplateModel>) => {
				this.headerColumns = columns;
			}
		);

		this.subscribeWithoutRender(
			this.compositionWarehouse.onContainerWidth(this.compositionId),
			(width: number) => {
				this.styleModifier.getElement(this.containerRef.nativeElement).setWidth(width);
			}
		);

		this.subscribe(
			this.filterWarehouse.onFilteringEnabled(this.structureId),
			(enabled: boolean) => {
				this.filterRowEnabled = enabled;
			}
		);

		this.subscribe(
			this.compositionWarehouse.onGroups(this.compositionId),
			(collection: GroupCollection) => {
				this.showGroups = collection.isVisible();
				this.groups = collection.getGroups();
			}
		);
	}

	ngAfterViewInit() {
		super.ngAfterViewInit();

		this.subscribeWithoutRender(
			this.structureVerticalFormationWarehouse.onRowHeight(this.structureId),
			(rowHeight: number) => {

				const headerHeight = +(rowHeight) + 2;

				if (this.filtersRef) {
					this.styleModifier.getElement(this.filtersRef.nativeElement).setHeight(headerHeight);
				}
			}
		);
	}

	protected getSelectorName(): string {
		return 'gui-structure-header';
	}

}
