import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetRowHeightBasedOnThemeCommand } from './set-row-height-based-on-theme.command';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';
import { RowHeightSetBasedOnThemeEvent } from './row-height-set-based-on-theme.event';


export class SetRowHeightBasedOnThemeCommandHandler implements CommandHandler<StructureAggregate, SetRowHeightBasedOnThemeCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<SetRowHeightBasedOnThemeCommand> {
		return SetRowHeightBasedOnThemeCommand;
	}

	handle(structure: StructureAggregate, command: SetRowHeightBasedOnThemeCommand): void {

		const theme = command.getTheme();

		structure.setTheme(theme);
	}

	publish(aggregate: StructureAggregate, command: SetRowHeightBasedOnThemeCommand): void {
		this.domainEventPublisher.publish(new RowHeightSetBasedOnThemeEvent(command.getAggregateId()));
	}

}
