import { SorterId } from '../api/id/sorter.id';
import { ItemEntity } from '../../../source/src/domain/item/item.entity';
import { FieldEntity } from '../../../field/src/domain/field/field.entity';

export class Sorter {

	private readonly sorterId: SorterId;

	private columnId: string;

	private field: FieldEntity;

	private rank: number = 1;

	private direction: boolean;

	constructor(id: SorterId,
				field: FieldEntity,
				direction: boolean = true) {
		this.sorterId = id;
		this.field = field;
		this.direction = direction;
	}

	getId(): SorterId {
		return this.sorterId;
	}

	getRank(): number {
		return this.rank;
	}

	getField(): FieldEntity {
		return this.field;
	}

	hasDirection(): boolean {
		return this.direction;
	}

	changeDirection(): void {
		this.direction = !this.direction;
	}

	setDirection(direction: boolean): void {
		this.direction = direction;
	}

	sort(entities: Array<ItemEntity>): Array<ItemEntity> {

		if (entities.length === 0) {
			return entities;
		}

		return entities.sort((entityOne: ItemEntity, entityTwo: ItemEntity) => {
			return this.field.sort(entityOne, entityTwo, this.direction);
		});
	}

}
