import { ChangeDetectionStrategy, Component, ElementRef, ViewEncapsulation } from '@angular/core';
import { FormationWarehouse } from '../../../../../core/structure/formation/src/api/formation.warehouse';
import { DynamicallyCreatedComponent, useReactiveContext } from '../../../../common/src/cdk/component/lib';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { FormationCustomSelection } from '../../../../../core/structure/formation/src/api/custom/formation.custom-selection';
import { FormationPublisher } from '../../../../../core/structure/formation/src/api/formation.publisher';
import { FormationCustomSelectId } from '../../../../../core/structure/formation/src/api/custom/formation.custom-select.id';

@Component({
	selector: 'div[gui-select-custom-modal]',
	template: `

		<ul>
			<li *ngFor="let sel of selection.getSelections()"
				(click)="selectCustom(sel.getCustomSelectId())"
				[attr.id]="sel.key">
				{{sel.text}}
			</li>
		</ul>

	`,
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None,
	styles: [`

		.gui-select-custom-modal {
			background: #fff;
			border: 1px solid #d6d6d6;
			border-radius: 0 0 4px 4px;
		}

		.gui-select-custom-modal ul {
			list-style: none;
			margin: 0;
			padding: 0;
		}

		.gui-select-custom-modal ul li {
			user-select: none;
			background: #fff;
			border-color: #d6d6d6;
			box-sizing: border-box;
			color: #333;
			cursor: pointer;
			display: block;
			font: 14px Arial;
			padding: 8px 12px 8px 12px;
		}

	`]
})
export class SelectCustomModalComponent extends DynamicallyCreatedComponent {

	selection: FormationCustomSelection;

	rc = useReactiveContext();

	constructor(elRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly formationPublisher: FormationPublisher,
				private readonly formationWarehouse: FormationWarehouse) {
		super(elRef);
	}

	ngOnInit(): void {
		this.rc
			.connect(this.formationWarehouse.onCustomSelections(this.structureId))
			.subscribeAndRender((selection) => {
				this.selection = selection;
			});
	}

	selectCustom(id: FormationCustomSelectId): void {
		this.formationPublisher.selectCustom(id, this.structureId);
	}

	protected getSelectorName(): string {
		return 'gui-select-custom-modal';
	}
}
