import { Directive } from '@angular/core';

import { PagingPublisher } from '../../../../../core/structure/paging/src/api/paging.publisher';
import { PagingEventRepository } from '../../../../../core/structure/paging/src/api/paging.event-repository';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { PagingGate } from '../../../../../feature/structure/paging/src/gate/paging.gate';
import { PagingConfig } from '../../../../../core/structure/paging/src/api/config/paging-config';
import { PagingDisplayModeArchive } from '../../../../../feature/structure/paging/src/mode/paging-display-mode.archive';
import { NgChanges } from '../../../../../feature/common/src/cdk/component/lib/src/ng-changes';
import { CompositionId } from '../../../../../core/composition/src/api/global/composition.id';


@Directive({
	selector: 'gui-structure[paging]'
})
export class StructurePagingGate extends PagingGate {

	constructor(structureId: StructureId,
				compositionId: CompositionId,
				pagingPublisher: PagingPublisher,
				pagingEventRepository: PagingEventRepository,
				private readonly pagingDisplayModeArchive: PagingDisplayModeArchive) {
		super(structureId, compositionId, pagingPublisher, pagingEventRepository);
	}

	override ngOnChanges(changes: NgChanges<StructurePagingGate>) {

		if (this.isDefined('paging', changes)) {

			let pagingConfig: PagingConfig;

			if (typeof this.paging === 'boolean') {
				pagingConfig = {
					enabled: this.paging
				};
			} else {
				pagingConfig = this.paging;

				if (this.paging.displayMode !== undefined) {
					this.pagingDisplayModeArchive.next(this.paging.displayMode);
				}
			}

			this.pagingCommandInvoker.setPaging(pagingConfig, this.structureId);
		}
	}

}
