import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { StructureSetHeightCommand } from './structure-set-height.command';
import { StructureHeightSetEvent } from './structure-height-set.event';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';


export class StructureSetHeightCommandHandler implements CommandHandler<StructureAggregate, StructureSetHeightCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	forCommand(): CommandType<StructureSetHeightCommand> {
		return StructureSetHeightCommand;
	}

	handle(aggregate: StructureAggregate, command: StructureSetHeightCommand): void {

		const height = command.getHeight();

		aggregate.setHeight(height);
	}

	publish(aggregate: StructureAggregate, command: StructureSetHeightCommand): void {
		this.domainEventPublisher.publish(new StructureHeightSetEvent(command.getAggregateId()));
	}

}
