import { CommandHandler, CommandType } from '@generic-ui/hermes';

import { UnselectUniqueFilterCommand } from './unselect-unique-filter.command';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';
import { FieldId } from '../../../../../field/src/domain/field/field.id';


export class UnselectUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, UnselectUniqueFilterCommand> {

	forCommand(): CommandType<UnselectUniqueFilterCommand> {
		return UnselectUniqueFilterCommand;
	}

	handle(structure: StructureAggregate, command: UnselectUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId(),
			uniqueValueId = command.getUniqueValueId();

		structure.unselectUniqueFilter(fieldId, uniqueValueId);
	}

}
