import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { FormationEventRepository } from './formation.event-repository';
import { FormationPublisher } from './formation.publisher';
import { FormationWarehouse } from './formation.warehouse';
import { RowSelectedArchive } from '../core-read/row-selected/row-selected.archive';
import { FormationDomainModule } from '../core/formation.domain-module';
import { FormationDomainWarehouse } from '../core-read/formation.domain-warehouse';
import { FormationDomainPublisher } from '../core/formation.domain-publisher';


export class FormationApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(RowSelectedArchive);
		container.provide(FormationEventRepository);
		container.provide(FormationPublisher, FormationDomainPublisher);
		container.provide(FormationWarehouse, FormationDomainWarehouse);
	}
}

export function initializeFormation(): void {
	new DomainInitializer(
		new FormationApiModule(),
		new FormationDomainModule()
	)
		.init();
}
