import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { StructureId } from '../../../../../core/structure/structure-core/src/api/global/structure.id';
import { SummariesEventRepository } from '../../../../../core/structure/summaries/src/api/summaries.event-repository';
import { StructureSummariesChangedEvent } from '../../../../../core/structure/summaries/src/core/structure.summaries-changed.event';
import { SmartComponent } from '../../../../common/src/cdk/component/lib/src/smart-component';
import { SourceWarehouse } from '../../../../../core/structure/source/src/api/source.warehouse';
import { TranslationFacade } from '../../../../../core/l10n/src/api/translation.facade';
import { Translation } from '../../../../../core/l10n/src/api/translation';
import { SummariesTranslations } from '../structure.summaries-translations';
import { RowSelectionType } from '../../../../../core/structure/formation/src/api/row-selected/row-selection';
import { CompositionId } from '../../../../../core/composition/src/api/global/composition.id';
import { CompositionTemplateWarehouse } from '../../../../composition/src/column/composition.template-warehouse';
import { CompositionTemplateModel } from '../../../../composition/src/column/composition.template.model';
import { FormationWarehouse } from '../../../../../core/structure/formation/src/api/formation.warehouse';


@Component({
	selector: 'div[gui-structure-summaries-panel][enabled]',
	templateUrl: './structure.summaries-panel.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush,
	encapsulation: ViewEncapsulation.None
})
export class StructureSummariesPanelComponent extends SmartComponent implements OnInit {

	@Input()
	enabled!: boolean;

	sourceEmpty: boolean = false;

	headerColumns: Array<CompositionTemplateModel>;

	summaries: Map<string, any>;

	summariesTranslations: SummariesTranslations;

	checkboxSelection: boolean;

	constructor(changeDetectorRef: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly structureId: StructureId,
				private readonly compositionId: CompositionId,
				private readonly structureSummariesEventRepository: SummariesEventRepository,
				private readonly translationService: TranslationFacade,
				private readonly sourceWarehouse: SourceWarehouse,
				private readonly formationWarehouse: FormationWarehouse,
				private readonly compositionTemplateWarehouse: CompositionTemplateWarehouse) {
		super(changeDetectorRef, elementRef);

		this.addClassToHost('gui-flex');

		this.subscribe(
			this.structureSummariesEventRepository.onSummariesChanged(this.structureId.toReadModelRootId()),
			(event: StructureSummariesChangedEvent) => {
				this.summaries = event.getSummaries();
			}
		);
	}

	ngOnInit() {

		this.subscribe(
			this.formationWarehouse.onType(this.structureId),
			(type: RowSelectionType) => {
				this.checkboxSelection = type === RowSelectionType.CHECKBOX;
			}
		);

		this.subscribe(
			this.sourceWarehouse.onItemsSize(this.structureId),
			(size: number) => {
				this.sourceEmpty = size === 0;
			}
		);

		this.subscribe(
			this.compositionTemplateWarehouse.onHeaderCols(this.compositionId),
			(columns: Array<CompositionTemplateModel>) => {
				this.headerColumns = columns;
			}
		);

		this.subscribe(
			this.translationService.onTranslation(),
			(translation: Translation) => {
				this.summariesTranslations = new SummariesTranslations(
					translation.summariesDistinctValuesTooltip,
					translation.summariesAverageTooltip,
					translation.summariesMinTooltip,
					translation.summariesMaxTooltip,
					translation.summariesMedTooltip,
					translation.summariesCountTooltip);
			}
		);
	}

	isSummariesTypePresent(summaries: number): boolean {
		return summaries !== undefined && summaries !== null;
	}

	protected getSelectorName(): string {
		return 'gui-structure-summaries-panel';
	}
}
