import { ApiProviders, Container, DomainInitializer } from '@generic-ui/hermes';

import { SearchDomainModule } from '../core/search.domain-module';
import { SearchPublisher } from './search.publisher';
import { SearchWarehouse } from './search.warehouse';
import { SearchEventRepository } from './search.event-repository';
import { SearchPhraseArchive } from '../core/phrase/search-phrase.archive';
import { SearchHighlightArchive } from '../core/highlight/search-highlight.archive';
import { SearchPlaceholderArchive } from '../core/placeholder/search-placeholder.archive';
import { SearchingEnabledArchive } from '../core/enabled/searching-enabled.archive';
import { SearchDomainWarehouse } from '../core-read/search.domain-warehouse';
import { SearchDomainEventRepository } from '../core-read/search.domain-event-repository';
import { SearchDomainPublisher } from '../core/search.domain-publisher';


export class SearchApiModule implements ApiProviders {

	registerProviders(container: Container): void {
		container.provide(SearchPublisher, SearchDomainPublisher);
		container.provide(SearchWarehouse, SearchDomainWarehouse);
		container.provide(SearchEventRepository, SearchDomainEventRepository);

		container.provide(SearchPhraseArchive);
		container.provide(SearchHighlightArchive);
		container.provide(SearchPlaceholderArchive);
		container.provide(SearchingEnabledArchive);
	}
}

export function initializeSearch(): void {
	new DomainInitializer(new SearchApiModule(), new SearchDomainModule()).init();
}
