import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SetSearchPhraseCommand } from './set-search-phrase.command';
import { SearchPhraseSetDomainEvent } from './search-phrase-set.domain-event';
import { SourceDomainEventPublisher } from '../../../../../source/src/core/source.domain-event.publisher';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';


export class StructureSetSearchPhraseCommandHandler implements CommandHandler<StructureAggregate, SetSearchPhraseCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	constructor(private readonly structureSourceDomainEventPublisher: SourceDomainEventPublisher) {
	}

	static readonly services = [SourceDomainEventPublisher];

	forCommand(): CommandType<SetSearchPhraseCommand> {
		return SetSearchPhraseCommand;
	}

	handle(aggregate: StructureAggregate, command: SetSearchPhraseCommand): void {

		const phrase = command.getPhrase();

		aggregate.addSearchPhrase(phrase);
	}

	publish(aggregate: StructureAggregate, command: SetSearchPhraseCommand): void {

		const phrase = command.getPhrase(),
			initial = command.isInitial();

		this.domainEventPublisher.publish(new SearchPhraseSetDomainEvent(command.getAggregateId(), phrase, initial));
		this.structureSourceDomainEventPublisher.publish(aggregate.getEvents());
	}

}
