import { CommandHandler, CommandType } from '@generic-ui/hermes';

import { UnselectAllUniqueFilterCommand } from './unselect-all-unique-filter.command';
import { StructureAggregate } from '../../../../../structure-core/src/domain/structure.aggregate';
import { FieldId } from '../../../../../field/src/domain/field/field.id';


export class UnselectAllUniqueFilterCommandHandler implements CommandHandler<StructureAggregate, UnselectAllUniqueFilterCommand> {

	forCommand(): CommandType<UnselectAllUniqueFilterCommand> {
		return UnselectAllUniqueFilterCommand;
	}

	handle(structure: StructureAggregate, command: UnselectAllUniqueFilterCommand): void {

		const fieldId: FieldId = command.getFieldId();

		structure.unselectAllUniqueFilter(fieldId);
	}

}
