import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { VerticalFormationScrollBarPositionArchive } from './vertical-formation.scroll-bar-position.archive';
import { ScrollBarPositionSetEvent } from '../../core/scroll-bar/scroll-bar-position-set.event';
import { StructureId } from '../../../../structure-core/src/api/global/structure.id';


export class ScrollBarPositionSetEventHandler implements DomainEventHandler<StructureId, ScrollBarPositionSetEvent> {

	constructor(private readonly positionRepository: VerticalFormationScrollBarPositionArchive) {
	}

	static readonly services = [VerticalFormationScrollBarPositionArchive] as const;

	forEvent(): DomainEventType<ScrollBarPositionSetEvent> {
		return ScrollBarPositionSetEvent;
	}

	handle(event: ScrollBarPositionSetEvent): void {

		if (event.ofMessageType('ScrollBarPositionSetEvent')) {
			this.positionRepository.next(event.getAggregateId(), event.getPosition());
		}

	}

}
