import { CoreContainer, DomainEvent, DomainEventBus, hermesFilter, hermesMap, HermesObservable, hermesSwitchMap } from '@generic-ui/hermes';
import { StructureReadModelRootId } from '../../../structure-core/src/api/read/structure.read-model-root-id';
import { StructureId } from '../../../structure-core/src/api/global/structure.id';
import { PagingWarehouse } from '../api/paging.warehouse';
import { PrevPageEvent } from '../core/prev-page/prev-page.event';
import { NextPageEvent } from '../core/next-page/next-page.event';
import { PageChangedEvent } from '../core/page/page-changed.event';
import { PagingModel } from '../api/model/paging.model';
import { PagesizeChangedEvent } from '../core/change-pagesize/pagesize-changed.event';
import { PagingEventRepository } from '../api/paging.event-repository';


export class PagingDomainEventRepository extends PagingEventRepository {

	private readonly eventBusToRemove: DomainEventBus = CoreContainer.resolve(DomainEventBus);

	constructor(private readonly pagingWarehouse: PagingWarehouse) {
		super();
	}

	static readonly services = [PagingWarehouse];

	onPageChange(structureId: StructureReadModelRootId): HermesObservable<number> {

		return this.eventBusToRemove
				   .ofEvents([
						   PrevPageEvent as any,
						   NextPageEvent as any,
						   PageChangedEvent as any
					   ]
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toAggregateId().toString()),
					   hermesSwitchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingWarehouse
									  .oncePaging(structureId.toAggregateId())
									  .pipe(
										  hermesMap((paging: PagingModel) => paging.getPage())
									  );
					   })
				   );
	}

	onPageSizeChange(structureId: StructureReadModelRootId): HermesObservable<number> {

		return this.eventBusToRemove
				   .ofEvents([
						   PagesizeChangedEvent as any
					   ]
				   )
				   .pipe(
					   hermesFilter((event: DomainEvent<StructureId>) => event.getAggregateId().toString() === structureId.toAggregateId().toString()),
					   hermesSwitchMap((event: DomainEvent<StructureId>) => {

						   return this.pagingWarehouse
									  .oncePaging(structureId.toAggregateId())
									  .pipe(
										  hermesMap((paging: PagingModel) => paging.getPageSize())
									  );
					   })
				   );
	}

}
