import { CommandHandler, CommandType, CoreContainer, DomainEventPublisher } from '@generic-ui/hermes';

import { SourceDomainEventPublisher } from '../../../../source/src/core/source.domain-event.publisher';
import { StructureSetSummariesEnabledCommand } from './structure.set-summaries-enabled.command';
import { StructureSummariesEnabledSetEvent } from './structure.summaries-enabled-set.event';
import { StructureAggregate } from '../../../../structure-core/src/domain/structure.aggregate';


export class StructureSetSummariesEnabledCommandHandler implements CommandHandler<StructureAggregate, StructureSetSummariesEnabledCommand> {

	private readonly domainEventPublisher: DomainEventPublisher = CoreContainer.resolve(DomainEventPublisher);

	constructor(private readonly structureSourceDomainEventPublisher: SourceDomainEventPublisher) {
	}

	static readonly services = [SourceDomainEventPublisher];

	forCommand(): CommandType<StructureSetSummariesEnabledCommand> {
		return StructureSetSummariesEnabledCommand;
	}

	handle(structure: StructureAggregate, command: StructureSetSummariesEnabledCommand): void {

		const enabled = command.isEnabled();

		structure.setSummariesEnabled(enabled);
	}

	publish(aggregate: StructureAggregate, command: StructureSetSummariesEnabledCommand): void {

		const enabled = command.isEnabled(),
			aggregateEvents = aggregate.getEvents();

		this.domainEventPublisher.publish(new StructureSummariesEnabledSetEvent(command.getAggregateId(), enabled));
		this.structureSourceDomainEventPublisher.publish(aggregateEvents);
	}

}
